<template>
  <div class="pageBox addressAddMdfBox">
    <p class="title">
      联系方式
    </p>
    <div class="addressAddMdfContact ">
      <div class="after ">
        <p class="lebel ">
          联系人
        </p>
        <input type="text" placeholder="你的姓名" v-model="obj.name">
      </div>
      <div class="after">
        <p class="lebel">
          联系电话
        </p>
        <input type="text" placeholder="你的手机号码" v-model="obj.phone" maxlength="11">
      </div>
    </div>
    <p class="title">
      收货/服务地址
    </p>
    <div class="addressAddMdfContact ">
      <div class="after">
        <p class="lebel">
          地址
        </p>
        <!--<input type="text" placeholder="选择区域" v-model="obj.address" readonly @click="mapHide">-->
        <div @click="mapHide" class="mapClass"><p>{{obj.address}}</p></div>
      </div>
      <div class="after">
        <p class="lebel">
          门牌号
        </p>
        <input type="text" placeholder="详细地址" v-model="obj.homeNum">
      </div>
    </div>
    <div class="btnBox btnBoxDelt" @click="addressFun">
      <span v-show="typese==1">确认添加</span>
      <span v-show="typese==2">保存</span>
    </div>
    <div class="btnBox btnBoxDelete" @click="deleteFun" v-if="typese==2">
    <span>删除</span>
    </div>
    <addressMap v-show="mapFlag" ref="mapFun" @subReturnFuns="mapClidFun"></addressMap>
  </div>
</template>

<script>
  import {MP} from '../../utils/map.js'
  import { Toast } from 'mint-ui';
  import addressMap from '@/views/addressAddOrModify/AddressMap'
  export default {
    components: {
      addressMap
    },
    data() {
      return {
        idse:'',//地址id
        typese:'1',//类型 1-新增 2-编辑
        addressStr:'',
        obj:{
          name:'',
          phone:'',
          longitude:'',
          latitude:'',
          address:'选择区域',
          homeNum:'',
        },
        showMapComponent: this.value,
        keyword: '',
        mapFlag:false
      }
    },
    mounted () {
      this.typese=this.$route.query.type;
      if( this.typese==2){
        this.idse=this.$route.query.id;
        this.modifyInit();
      }
      console.log(this.typese);

    },
    methods: {
      modifyInit(){
        this.$http.post('/address/list')
          .then(res => {
            if (res.status === 200) {
              if(res.data.code===200){
                console.log(res.data.data);
                let cityList=res.data.data
//                this.addressData=res.data.data
                if(Number(this.typese)===2){
                  console.log('----------------')
                  console.log(this.idse)
                  for(let i=0;i<cityList.length;i++){
                    if(Number(this.idse)===Number(cityList[i].id)){
                      this.obj.name=cityList[i].name;
                      this.obj.phone=cityList[i].phone;
                      this.obj.longitude=cityList[i].longitude;
                      this.obj.latitude=cityList[i].latitude;
                      this.obj.address=cityList[i].address;
                      this.obj.homeNum=cityList[i].homeNum;
                      this.obj.userId=cityList[i].userId;
                    }
                  }
                }
              }
            }
          })
      },
      /**
       * 地图弹窗
       */
      mapHide(){
        this.$refs.mapFun.seachShow();
        this.mapFlag=true

      },
      /***
       * 地图点击事件。
       */
      mapClidFun(data){
        this.mapFlag=false;
        console.log(data)
        this.obj.longitude=data.lng;
        this.obj.latitude=data.lat;
        this.obj.address=data.address;
      },
      /**
       * 提交地址
       */
      addressFun(){
        let objFlag=isEmpty(this.obj);
        console.log(objFlag)
        if(objFlag){
          if(this.typese==1){
            this.subFun('/address/add',this.obj)
          }else {
            let midData=this.obj;
            midData.id=this.idse;
            this.subFun('/address/edit',midData)
          }
        }else {
          Toast({
            message:'请填写完整',
            duration: 1000
          });
        }


      },
      /**
       *提交函数
       */
      subFun(url,obj){
        this.$http.post(url, this.$qs.stringify(obj))
          .then(res => {
            console.log('res.data', res)
            if (res.status === 200) {
              if (res.data.code === 200) {
                Toast({
                  message:'操作成功',
                  duration: 1000
                });
                this.$router.push({ path: 'address' });
              } else {
                Toast({
                  message:res.data.msg,
                  duration: 1000
                });
              }
            }
          })
      },
      /**
       * 删除地址
       */
      deleteFun(){
        let deleteObj={
          addressId:this.idse
        };
        this.$http.post('/address/del', this.$qs.stringify(deleteObj))
          .then(res => {
            console.log('res.data', res);
            if (res.status === 200) {
              if (res.data.code === 200) {
                Toast({
                  message:'删除成功',
                  duration: 1000
                });
                this.$router.push({ path: 'address' });
              } else {
                Toast({
                  message:res.data.msg,
                  duration: 1000
                });
              }
            }
          })
      }
    },
    computed: {},
  }

  /**
   * 判断对象是否是全部填完
   * @param obj
   * @returns {boolean}
   */
  function isEmpty(obj) {
    for (let name in obj){
      if(obj[name]===""||obj[name]===null||obj[name]===undefined){
        return false;
      }
    }
    return true;
  }
</script>

<style lang="scss">
.addressAddMdfBox{
  background-color: #f7f7f7;
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow-y: scroll;
  .title{
    font-size: 0.24rem;
    color: #3b3b3b;
    line-height: 0.6rem;
    padding-left: 0.3rem;
  }
  .addressAddMdfContact{
    padding-left: 0.3rem;
    background-color: #ffffff;
    .after{
      line-height: 1rem;
      .lebel{
        float: left;
        font-size: 0.28rem;
        color: #3b3b3b;
        width: 1.2rem;
      }
      input{
        float: left;
        font-size: 0.28rem;
        color: #3b3b3b;
        display: block;
        padding: 0.34rem 0;
      }
    }
    .after:first-child{
      border-bottom: 1px solid #f0f0f0;
    }
  }
.btnBox{
  width: 6.1rem;
  height: 0.7rem;
  line-height: 0.7rem;
  text-align: center;
  font-size: 0.3rem;
  color: #ffffff;
  border-radius: 0.5rem;
  margin:1rem auto;
  background-color: #de3031;
}
  .btnBoxDelete{
    margin-top: 0.3rem;
    background-color: #ccc;
  }
  .btnBoxDelt{
    margin-bottom: 0.3rem;
  }
  .mapBox{
    position: fixed;
    left: 0;
    right: 0;
    bottom: 0;
    top:0;
    width: 100%;
    height: 100%;
    z-index: 50;
  }
  .baidumap{
    width: 100%;
    height: 100%;
    .bm-view{
      width: 100%;
      height: 100%;
    }
  }
  .serachinput{
    width: 300px;
    box-sizing: border-box;
    padding: 9px;
    border: 1px solid #dddee1;
    line-height: 20px;
    font-size: 16px;
    height: 38px;
    color: #333;
    position: relative;
    border-radius: 4px;
    -webkit-box-shadow: #666 0px 0px 10px;
    -moz-box-shadow: #666 0px 0px 10px;
    box-shadow: #666 0px 0px 10px;
  }
  .mapClass{
    float: left;
    font-size: 0.28rem;
    color: #3b3b3b;
    width:80%;
    line-height:100%;
    display:flex;
    align-items:center;/*垂直居中*/
    justify-content: center;/*水平居中*/
    height: 1rem;
    p{
      margin: auto;
      display: -webkit-box;
      -webkit-box-orient: vertical;
      -webkit-line-clamp: 2;
      overflow: hidden;
    }
  }
}
</style>



