<template>
  <div class="pageBox myIntergralBox">
    <mt-loadmore :bottom-method="loadBottom" :bottom-all-loaded="allLoaded" ref="loadmore"  :auto-fill="false">
      <ul>
        <li v-for="item in myIntegralArr">
          <div class="myIntergralLeft">
            <p class="p1" v-if="item.integralType===-1">
              购物抵消
            </p>
            <p class="p1" v-if="item.integralType===1">
              购物获赠
            </p>
            <p class="p2">
             {{item.craTime}}
            </p>
          </div>
          <p class="myIntergralRight p3"  v-if="item.integralType===-1">
            -{{item.integralNumber}}
          </p>
          <p class="myIntergralRight p4"  v-if="item.integralType===1">
            +{{item.integralNumber}}
          </p>
        </li>
      </ul>
      <div slot="bottom" class="mint-loadmore-bottom">
        <img src="../../assets/img/loding.gif" alt="">
      </div>
    </mt-loadmore>

  </div>
</template>

<script>
  export default {
    data() {
      return {
        allLoaded:false,//上拉加载标记
        myIntegralData:'',
        myIntegralArr:[],
      }
    },
    mounted () {
      let obj={
        current:'1',
        size:'10',
      };
      this.myIntegralInit(obj)
    },
    methods: {
      /**
       * 页面初始化
       */
      myIntegralInit(obj){
        this.$http.post('/userIntegral/getInfo',this.$qs.stringify(obj))
          .then(res => {
            if (res.status === 200) {
              if(res.data.code===200){
                console.log(res.data)
                this.myIntegralData=res.data.data
                if(res.data.data!=null){
                  this.myIntegralArr=this.myIntegralArr.concat(res.data.data.records)
                  if(this.myIntegralData.pages<=1){
                    this.allLoaded=true
                  }else {
                    this.allLoaded=false
                  }
                }else {
                  this.allLoaded=true
                }
              }
            }
          })
      },
      /**
       * 上拉
       */
      loadBottom() {
        let pages=Number(this.myIntegralData.current)+1;
        let obj={
          current:pages,
          size:'10',
        }
        this.myIntegralInit(obj)
        this.$refs.loadmore.onBottomLoaded();
      },
    },
    computed: {}
  }
</script>

<style lang="scss">
  .myIntergralBox{
    background-color: #ffffff;
    ul{
      li{
        padding: 0.2rem 0.3rem;
        border-bottom: 1px solid #f0f0f0;
        display: flex;
        justify-content:space-between;
        .p1{
          font-size: 0.28rem;
          color: #3b3b3b;
        }
        .p2{
          font-size: 0.22rem;
          color: #a0a0a0;

        }
        .myIntergralRight{
          line-height: 0.6rem;
          font-size: 0.26rem;
        }
        .p3{
          color: #676767;
        }
        .p4{
          color: #f54d4d;
        }
      }
    }
  }
</style>



