<template>
  <div class="pageBox orderBox">
    <div class="orderHeader">
      <div class="indexHeaderSerch">
        <input type="search" placeholder="请输入订单关键字">
        <i class="icon iconfont icon-sousuo"></i>
      </div>
    </div>
    <div class="orderCont">
      <mt-navbar v-model="selected">
        <mt-tab-item id="1">全部</mt-tab-item>
        <mt-tab-item id="2">待付款</mt-tab-item>
        <mt-tab-item id="3">待发货</mt-tab-item>
        <mt-tab-item id="4">待收货</mt-tab-item>
        <mt-tab-item id="5">售后</mt-tab-item>
      </mt-navbar>

      <!-- tab-container -->
      <mt-tab-container v-model="selected">
        <mt-tab-container-item id="1">
          <ul class="orderContBox">
            <li>
              <div class="after orderCartListTit">
                <a class="after orderName">
                  <i class="icon iconfont icon-iconfontdianpu"></i>
                  HEFWNG专卖店
                  <i class="icon iconfont icon-bread"></i>
                </a>
                <p class="orderCoupon" >
                  等待卖家发货
                </p>
              </div>
              <div class="orderList">
                <div class="after orderListBox">
                  <div class="orderListImgBox">
                    <img src="@/assets/img/index_tu4.png" alt="" class="img-my">
                  </div>
                  <div class="orderListFontBox">
                    <p class="orderListExplain">
                      和风手绘古朴釉下彩陶艺小酱碗 点心糕点碟盘
                    </p>
                    <p class="orderListModel">
                      型号：中号*10
                    </p>
                  </div>
                  <div class="orderLisPriceBox">
                    <p class="price">
                      ￥84.5
                    </p>
                    <p class="nameber">
                      x1
                    </p>
                  </div>
                </div>
                <div class="after CalculationBox">
                    <p class="CalculationPrice">
                      共 1 件 合计 <span>¥84.5</span>
                    </p>
                  <div class="after CalculationBtnBox">
                    <!--<p class="grayColor">-->
                      <!--催一催-->
                    <!--</p>-->
                    <!--<p class="redColor">-->
                    <!--取消订单-->
                    <!--</p>-->
                    <p class="grayColor">
                      查看物流
                    </p>
                    <p class="redColor">
                    确认收货
                    </p>
                    <!---->
                    <!--<p class="grayColor">-->
                      <!--删除订单-->
                    <!--</p>-->
                    <!--<p class="redColor">-->
                      <!--评价-->
                    <!--</p>-->
                    <!--<p class="redColor">-->
                      <!--再次购买-->
                    <!--</p>-->
                    <!--<p class="redColor">-->
                      <!--付款-->
                    <!--</p>-->
                  </div>
                </div>

              </div>
            </li>

          </ul>

        </mt-tab-container-item>
        <mt-tab-container-item id="2">

        </mt-tab-container-item>
        <mt-tab-container-item id="3">

        </mt-tab-container-item>
        <mt-tab-container-item id="4">

        </mt-tab-container-item>
        <mt-tab-container-item id="5">

        </mt-tab-container-item>
      </mt-tab-container>
    </div>
    <bottom></bottom>
  </div>
</template>
<script>

  import bottom from '@/components/bottom'
    export default {
      name: 'order',
      components: {
        bottom
      },
        data() {
            return {
              selected:"1"
            }
        },
        methods: {},
        computed: {}
    }
</script>
<style lang="scss">
  .orderBox{
    background-color: #f7f7f7;
    padding: 0.9rem 0 0.96rem 0;
    .orderHeader{
      position: fixed;
      left: 0;
      right: 0;
      top: 0;
      z-index: 5;
      background-color: #de3031;
      padding: 0 0.2rem;
      .indexHeaderSerch{
        height: 0.6rem;
        line-height: 0.9rem;
        margin: 0.15rem 0;
        position: relative;
        input{
          border-radius: 0.5rem;
          width: 100%;
          display: block;
          padding: 0.16rem 0.3rem;
          font-size: 0.24rem;
          color: #bfbfbf;
        }
        i{
          position: absolute;
          right: 0.3rem;
          top: 0;
          line-height: 0.6rem;
          font-size: 0.3rem;
          color: #bfbfbf;
          background-color: #fff;
        }
      }
    }
    .mint-navbar{
      margin-bottom: 0.2rem;
      padding: 0 0.2rem;
    }
    .mint-navbar .mint-tab-item {
      padding: 0.24rem 0;
    }
    .mint-navbar .mint-tab-item.is-selected {
      border-bottom: 0.05rem solid #de3031;
      color: #de3031;
      margin-bottom:0.06rem;
    }
    .mint-tab-item-label{
      font-size: 0.28rem;
    }
    .orderCartListTit{
      line-height: 0.84rem;
      border-bottom: 1px solid #cccccc;
      padding: 0 0 0 0.2rem;
      background-color: #ffffff;
      .orderName{
        float: left;
        position: relative;
        font-size: 0.24rem;
        padding-left: 0.2rem;
        display: block;
        width: 4.72rem;
        overflow: hidden;
        text-overflow:ellipsis;
        white-space: nowrap;
        i{
          font-size: 0.24rem;
        }

      }
      .orderCoupon{
        float: right;
        font-size: 0.26rem;
        padding: 0 0.2rem;
      }

    }
    .orderList{
      margin-bottom: 0.2rem;
      background-color: #ffffff;
      .orderListBox{
        padding: 0.3rem  0.2rem 0.1rem 0.2rem;
        border-bottom: 1px solid #ebebeb;
        .orderListImgBox{
          width: 1.3rem;
          height: 1.3rem;
          display: -webkit-flex;
          display: flex;
          float: left;
           img{
             margin: auto;
           }
        }
        .orderListFontBox{
          float: left;
          height: 0.7rem;
          width: 3.86rem;
          margin-left: 0.3rem;
          .orderListExplain{
            font-size: 0.28rem;
            color: #3b3b3b;
            max-width: 100%;
            overflow:hidden;
            text-overflow:ellipsis;
            display:-webkit-box;
            -webkit-box-orient:vertical;
            -webkit-line-clamp:2;
          }
          .orderListModel{
            font-size: 0.24rem;
            color: #939393;
          }
        }
        .orderLisPriceBox{
          float: right;
          max-width: 1.37rem;
          .price{
            font-size: 0.28rem;
            color: #333333;
            text-align: right;
          }
          .nameber{
            font-size: 0.24rem;
            color: #939393;
            text-align: right;
          }
        }
      }
      .CalculationBox{
        padding: 0 0.2rem 0.26rem 0.2rem;
        .CalculationPrice{
          font-size: 0.24rem;
          color: #939393;
          text-align: right;
          line-height: 0.7rem;
          span{
            font-size: 0.26rem;
            color: #f53232;
          }
        }
        .CalculationBtnBox{
          float: right;
          p{
            width: 1.7rem;
            height: 0.5rem;
            line-height: 0.5rem;
            text-align: center;
            border-radius: 0.5rem;
            font-size: 0.26rem;
            margin-left:0.3rem ;
            float: left;
          }
          .grayColor{
            color: #333333;
            border:1px solid #d8d8d8
          }
          .redColor{
            color: #de3031;
            border:1px solid #de3031
          }
        }
      }


    }
  }
</style>



