<script>
  import Vue from 'vue'
  export default {
    name: 'indexCityCountry',
    data () {
      return {
        newsPopFlag:false,//消息弹出框标记
        cityClassFlag:false,//分类标记
        activeMouldId:'',//分类标记id
        indexData:{
          news:[
            {
              title:'',
              content:''
            }
          ]
        },//banner和分类列表
        indexCityCouGoods:{},//banner和分类列表
        modularId:''
      }
    },
    mounted () {
      this.modularId=this.$route.query.id
      this.indexInit()
    },
    methods: {
      /**
       * 页面初始化函数
       */
      indexInit(){
        let timeObj={
          modularId:this.modularId
        };
        this.$http.post('/activity/getNiceChoose',this.$qs.stringify(timeObj))
          .then(res => {
            if (res.status === 200) {
              console.log('res.data', res.data);
              if(res.data.code===200){
                this.indexData=res.data.data;
                let idObj={
                  modularId:this.indexData.modular[0].id
                };
                this.modularFun(idObj)
                Vue.set(this.indexData.modular[0],'cityClassFlag',true);
                this.activeMouldId=this.indexData.modular[0].id
              }

            }
          })
      },
      /**
       * 分类点击效果
       */
      indexCityCon(item, index){
        this.$nextTick(function() {
          this.indexData.modular.forEach(function(item) {
            Vue.set(item,'cityClassFlag',false)
          });
          Vue.set(item,'cityClassFlag',true)
        });
        let idObj={
          modularId:item.id
        };
        this.modularFun(idObj)
        this.activeMouldId=item.id
      },
      /**
       * 控制新闻弹窗
       */
      newsShowFun(){
        this.newsPopFlag=!this.newsPopFlag
      },
      /**
       * 返回上一个页面
       */
      fanhuiFun(){
        this.$router.go(-1)
      },
      /**
       * 查询商品列表和商家列表
       * @param obj id
       */
      modularFun(obj){
        this.$http.post('/modularPortal/getShopAndGoodsByModular',this.$qs.stringify(obj))
          .then(res => {
            if (res.status === 200) {
              if(res.data.code===200){
                console.log('res.data', res.data);
                this.indexCityCouGoods=res.data.data;
              }
            }
          })
      }
    },

  }

</script>

