<template>
  <div class="pageBox bankCardBox">
    <div class="indexConHeader">
      <a class="indexbackOff" @click="fanhuiFun()">
        <i class="icon iconfont icon-guanbi1-copy"></i>
      </a>
      银行卡
      <router-link to="bankCardAdd" class="indexNews">
        <i class="icon iconfont icon-jia2"></i>
      </router-link>
    </div>
    <div class="bankCardBack" v-for="item in bankCardData.records">
      <div class="logobox">
        <img :src="item.icon" alt="" class="img-my">
      </div>
      <div class="fontBox">
        <p class="bankName">
          {{item.bankName}}
        </p>
        <p class="cardType">
          存储卡
        </p>
        <p class="cardTel">
          {{item.bankNumber}}
        </p>
      </div>
    </div>
    <!--<div class="bankCardBack">-->
      <!--<div class="logobox">-->
        <!--<img src="@/assets/img/indexTu4.png" alt="" class="img-my">-->
      <!--</div>-->
      <!--<div class="fontBox">-->
        <!--<p class="bankName">-->
          <!--建设银行-->
        <!--</p>-->
        <!--<p class="cardType">-->
          <!--存储卡-->
        <!--</p>-->
        <!--<p class="cardTel">-->
          <!--**** 2288 **** 2288-->
        <!--</p>-->
      <!--</div>-->
    <!--</div>-->
  </div>
</template>

<script>
    export default {
        data() {
            return {
              bankCardData:{

              }
            }
        },
      mounted () {
        this.bankCardInit()
      },
        methods: {
          /**
           * 页面初始化
           */
          bankCardInit(){
            this.$http.post('/bank/list')
              .then(res => {
                if (res.status === 200) {
                  if(res.data.code===200){
                    console.log(res.data);
                    this.bankCardData=res.data.data;
//                    this.myEvalArr=res.data.data.records
                  }
                }
              })
          },
          /**
           * 返回上一个页面
           */
          fanhuiFun(){
            this.$router.go(-1)
          },


        },
        computed: {}
    }
</script>

<style lang="scss">
  .bankCardBox{
    background-color: #ffffff;
    padding: 1.26rem 0.27rem 0.3rem 0.27rem;
    .indexConHeader {
      position: fixed;
      left: 0;
      right: 0;
      top: 0;
      z-index: 5;
      background-color: #de3031;
      height: 0.9rem;
      color: #fff;
      font-size: 0.34rem;
      text-align: center;
      line-height: 0.9rem;
      i{
        font-size: 0.4rem;
      }
      .indexbackOff {
        position: absolute;
        left: 0;
        top: 0;
        bottom: 0;
        line-height: 0.9rem;
        padding: 0 0.2rem;
        color: #fff;
      }
      .indexNews {
        position: absolute;
        right: 0;
        top: 0;
        bottom: 0;
        line-height: 0.9rem;
        padding: 0 0.2rem;
        color: #fff;
        font-size: 0.24rem;
      }
    }
    .bankCardBack{
      background-color: #28adf1;
      color: #ffffff;
      padding: 0.2rem 0.3rem 0.3rem 0.44rem;
      display: flex;
      justify-content:flex-start;
      border-radius: 10px;
      margin-bottom: 0.3rem;
      .logobox{
        margin-right: 0.3rem;
        img{
          width: 0.9rem;
          height: 0.9rem;
          border-radius: 50%;
        }
      }
      .fontBox{
        padding-top: 0.1rem;
        .bankName{
          font-size: 0.28rem;
        }
        .cardType{
          font-size: 0.22rem;
        }
        .cardTel{
          font-size: 0.38rem;;
          margin-top: 0.1rem;
        }
      }
    }
  }
</style>



