<template>
  <div class="pageBox addressBox">
    <ul class="addressUl">
      <li v-for="item in addressData">
      <div class="addressLeft">
        <div class="after">
          <p class="name">
            {{item.name}}
          </p>
          <p class="tel">
            {{item.phone}}
          </p>
        </div>
        <p class="addressFont">
          收货地址：{{item.address}}
        </p>
      </div>
      <div class="addressRight">
        <router-link  :to="{path:'addressAddOrModify',query: {id: item.id,type:2}}">
          <i class="icon iconfont icon-bianji"></i>
        </router-link>
      </div>
    </li>
    </ul>
    <div class="addressBottom">
      <router-link  :to="{path:'addressAddOrModify',query: {id: 0,type:1}}">
      <div class="after addressBottomBox">
          <i class="icon iconfont icon-xinzeng"></i>
          <span>新增地址</span>
      </div>
      </router-link>
    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        addressData:''
      }
    },
    mounted () {
      this.addressInit()
    },
    methods: {
      /**
       * 页面初始化
       */
      addressInit(){
        this.$http.post('/address/list')
          .then(res => {
            if (res.status === 200) {
              if(res.data.code===200){
                console.log(res.data);
                this.addressData=res.data.data

              }
            }
          })
      },
    },
    computed: {}
  }
</script>

<style lang="scss">
  .addressBox{
    background-color: #f7f7f7;
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    overflow-y: scroll;
    .addressUl li:after{
      display: block;
      content: "";
      clear: both;
    }
    .addressUl li{
      background-color: #ffffff;
      padding-left: 0.3rem;
      margin-bottom: 0.2rem;
      display: -webkit-flex;
      display: flex;

    }
    .addressLeft{
      float: left;
      line-height: 0.5rem;
      width: 6.2rem;
      .name{
        font-size: 0.28rem;
        color: #3b3b3b;
        float: left;
      }
      .tel{
        font-size: 0.28rem;
        float: right;
        color: #949494;
        padding-right: 1rem;
      }
      .addressFont{
        font-size: 0.24rem;
        color: #9f9f9f;
        clear: both;
      }
    }
    .addressRight{
      float: right;
      width: 1rem;
      line-height: 1rem;
      /*height: 100%;*/
      text-align: center;
      margin: auto;
      i{
        display: block;
        font-size: 0.42rem;
        color: #df3637;
      }
    }
    .addressBottom{
      position: fixed;
      left: 0;
      bottom:0;
      right: 0;
      background-color: #ffffff;
      line-height: 1rem;
      text-align: center;
      a{
        color: #de3031;
        font-size: 0.28rem;
        width: 100%;
        height: 100%;
        display: block;
      }
      .addressBottomBox{
        width: 1.8rem;
        margin: auto;

      }
      span{
        display: block;
        float: left;
      }
      i{
        font-size: 0.48rem;
        display: block;
        float: left;
        margin-right: 0.2rem;
      }
    }
  }
</style>



