<template>
  <div class="cityBox" v-show="cityFlag">
    <!--header start-->
    <div class="indexHeader" ref="header">
      <a class="indexbackOff" @click="hideFun()">
        <i class="icon iconfont icon-guanbi1-copy"></i>
      </a>
      <div class="indexHeaderSerch">
        <form action="javascript:return true;">
          <input type="search" placeholder="请输入关键字" @keypress="searchFun">
          <i class="icon iconfont icon-sousuo"></i>
        </form>
      </div>
    </div>
    <p class="cityP" ref="cityTit">
      请选择城市
    </p>
    <ul ref="cityListBox">
      <li v-for="item in modify" @click="subReturnFun(item)">
        {{item.countyName}}
      </li>

    </ul>
  </div>
</template>

<script>
  import Vue from 'vue'
  import { IndexList, IndexSection } from 'mint-ui';
  import * as city from '../utils/city.js'
  Vue.component(IndexList.name, IndexList);
  Vue.component(IndexSection.name, IndexSection);
    export default {
        data() {
            return {
              cityArr:[]
            }
        },
      props: ['cityFlag','modify'],
      mounted () {
        this.cityInit()
      },
        methods: {
          cityInit(){


            let heightMx=document.documentElement.clientHeight;
            let header= this.$refs.header.offsetHeight;
            let cityTit= this.$refs.cityTit.offsetHeight;
            heightMx=heightMx-header-cityTit;
            this.$refs.cityListBox.style.height =heightMx+'px';
          },
          // 关闭弹窗
          hideFun () {
            this.$emit('update:cityFlag')
          },
          /**
           * 点击事件
           * @param name
           */
          subReturnFun(name){
            console.log(name)
            this.$emit('subReturnFuns',{"name":name});
            this.hideFun ()
          },
          /**
           * 返回上一个页面
           */
          fanhuiFun(){
            this.$router.go(-1)
          },
          /**
           * 搜索框
           */
          searchFun(){
            var keycode = event.keyCode;
            if(keycode=='13') {
              alert(123)
              this.moreShopArr=[];
              event.preventDefault();
            }

          },
        },
        computed: {}
    }
</script>

<style lang="scss">
  .cityBox{
    position: fixed;
    left: 0;
    right: 0;
    top:0;
    bottom: 0;
    z-index: 10000;
    background: #f7f7f7;
    padding-top: 0.9rem;
    .indexHeader{
      position: fixed;
      left: 0;
      right: 0;
      top: 0;
      z-index: 5;
      background-color: #de3031;
      overflow-y: scroll;
      height: 0.9rem;
      .indexbackOff{
        line-height: 0.9rem;
        font-size: 0.34rem;
        color: #ffffff;
        float: left;
        width: 0.8rem;
        text-align: center;
      }
      .indexHeaderSerch{
        float: left;
        width:6.3rem;
        height: 0.6rem;
        line-height: 0.9rem;
        margin: 0.15rem 0;
        position: relative;
        input{
          border-radius: 0.5rem;
          width: 100%;
          display: block;
          padding: 0.16rem 0.3rem;
          font-size: 0.24rem;
          color: #bfbfbf;
        }
        i{
          position: absolute;
          right: 0.3rem;
          top: 0;
          line-height: 0.6rem;
          font-size: 0.3rem;
          color: #bfbfbf;
          background-color: #fff;
        }
      }
      .indexHeaderNews{
        float: right;
        color: #ffffff;
        padding: 0 0.2rem;
        i{
          font-size: 0.4rem;
          display: block;
          margin: 0.23rem 0;
        }
      }
    }
    .cityP{
      padding-left: 0.3rem;
      font-size: 0.3rem;
      height: 0.9rem;
      line-height: 0.9rem;
    }
    ul{
      overflow-y: scroll;
      li{
        padding: 0.2rem 0.3rem;
        border-bottom: 1px solid #cccccc;
        font-size: 0.24rem;
        background-color: #ffffff;
      }
      li:last-child{
        border-bottom: none;
      }
    }
  }

</style>



