<template>
  <div class="pageBox forgotPasswordBox">
    <mt-header title="注册" v-if="forgotData.forgotFlag===true">
      <a class="indexbackOff" @click="fanhuiFun()" slot="left">
        <mt-button icon="back"></mt-button>
      </a>
        <mt-button  slot="right"> <router-link  :to="{path:'/'}" >登录  </router-link></mt-button>
    </mt-header>
    <mt-header title="忘记密码" v-else="forgotData.forgotFlag===false">
      <a class="indexbackOff" @click="fanhuiFun()" slot="left">
        <mt-button icon="back"></mt-button>
      </a>
    </mt-header>
    <div class="loginFormBox">
      <div class="loginFormDivBox after">
        <input type="text" v-model="login.phone" placeholder="手机号/邮箱">
        <p @touchend="mobileFun" class="timeHide"  v-show="localData.yamFlag===true">
          验证
        </p>
        <p  class="timeShow"  v-show="localData.yamFlag===false">
          {{localData.count}}
        </p>
      </div>
      <input type="text" v-model="login.code" placeholder="验证码">
      <input type="text" v-model="login.password" placeholder="6-16位登录密码">
      <p class="loginFormBtn" @click="forgotClickFun">
        登录
      </p>
    </div>
    <p class="loginFont">
      点击登录，即表示同意 <a >《用户协议》</a>
    </p>
  </div>
</template>
<script>
  import { Toast } from 'mint-ui';
  import * as util from '../../utils/regular.js'
  import md5 from 'js-md5';
  export default {
    name: 'forgotPassword',
    data: function () {
      return {
        login: {
          phone: '',
          code: '',
          password: ''
        },
        forgotData:{//存储一些对象
          forgotFlag:false,//标记 如果是注册页面就位true 忘记密码就是false
          forgotTitStr:"", //储存title
        },
        localData:{
          count:'', //60s倒计时
          yamFlag:true, //标记一下状态
          timer:null,
        },
      }
    },
    mounted (){
      this.forgotPawsFun()
    },
    methods: {
      submitForm () {

      },
      /**
       * this.$route.query.name ==1 就是注册  ==2 就是忘记密码
       */
      forgotPawsFun(){
        console.log(this.$route.query.name);
        if(Number(this.$route.query.name)==1){
          this.forgotData.forgotFlag=true
        }else {
          this.forgotData.forgotFlag=false
        }
      },
      /**
       * 获取验证码
       */
      mobileFun () {
        let mobile=util.mobiles(this.login.phone)
        if(mobile===true){
          if(Number(this.$route.query.name)==1){
          // 注册短信发送
            let objIphone={
              phone:this.login.phone,
              type:'0'
            };
            this.funAjaxSubFun(objIphone,'/sms/sendByRegister')
          }else {
            //找回密码发送
            let objIphone={
              phone:this.login.phone,
              type:'0'
            };
            this.funAjaxSubFun(objIphone,'/sms/sendByFind')
          }
        }else {
          Toast({
            message:mobile,
            position: 'top',
            duration: 1000
          });
        }
      },
      /**
       * 返回上一个页面
       */
      fanhuiFun(){
        this.$router.go(-1)
      },
      /**
       * 获取验证码
       */
      funAjaxSubFun(mobile,url){
        this.$http.post(url, this.$qs.stringify(mobile))
          .then(res => {
            console.log('res.data', res)
            if (res.status === 200) {
              if (res.data.code === 200) {
                const TIME_COUNT = 60;
                if (!this.timer) {
                  this.localData.count = TIME_COUNT;
                  this.localData.yamFlag = false;
                  this.localData.timer = setInterval(() => {
                    if (this.localData.count > 0 && this.localData.count <= TIME_COUNT) {
                      this.localData.count--;
                    } else {
                      this.localData.yamFlag = true;
                      clearInterval(this.localData.timer);
                      this.localData.timer = null;
                    }
                  }, 1000)
                }//if
              } else {
                Toast({
                  message:"操作失败",
                  position: 'top',
                  duration: 1000
                });
              }
            }
          })
      },
      /**
       * 表单提交
       */
      forgotClickFun(){
        if(Number(this.$route.query.name)==1){
          // 注册短信发送
          this.forSubFunAjax('/user/register')
        }else {
          //找回密码发送
          this.forSubFunAjax('/user/editPasswordByCode')
        }
      },
      forSubFunAjax(url){
//        this.login.password=md5(this.login.password)
        let password=md5(this.login.password);
        let obj={
          phone: this.login.phone,
          password: password,
          code: this.login.code,
        }
        this.$http.post(url, this.$qs.stringify(obj))
          .then(res => {
            console.log('res.data', res)
            if (res.status === 200) {
              if (res.data.code === 200) {
                this.$router.push({ path: '/' });
              } else {
                Toast({
                  message:"操作失败",
                  position: 'top',
                  duration: 1000
                });
              }
            }
          })
      }
    },

  }
</script>

<style  lang="scss">
  .forgotPasswordBox{
    background-color: #ffffff;
    .mint-header{
      background-color: transparent;
      color: #3b3b3b;
      font-size: 0.34rem;
      height:0.9rem;
      border-bottom: 1px solid #e8e8e8;
    }
    .loginMode{
      padding: 0.68rem 1.4rem;
      display: -webkit-flex;
      display: flex;
      .loginModeItem{
        margin:0 auto;
        text-align: center;

        img{
          width: 0.9rem;
          height: 0.9rem;
        }
        p{
          font-size: 0.24rem;
          color: #939393;
          margin-top: 0.24rem;
        }
      }
    }
    .loginFormBox{
      padding: 0.6rem 0.45rem 0.6rem 0.45rem;
      input{
        padding: 0.19rem 0.4rem;
        border-radius: 0.5rem;
        background-color: #f6f6f6;
        color: #949494;
        font-size: 0.28rem;
        display: block;
        width: 100%;
        margin-bottom: 0.4rem;
      }
      .loginFormDivBox{
        margin-bottom: 0.4rem;
        input{
          width: 4.6rem;
          float: left;
          margin-bottom: 0;
        }
        p{
          height: 0.7rem;
          line-height: 0.7rem;
          border-radius: 0.5rem;
          font-size: 0.28rem;
          width: 1.76rem;
          float: right;
          text-align: center;
        }
        .timeShow{
          border:1px solid #f7f7f7;
          color: #939393;
          background-color: #f7f7f7;
        }
        .timeHide{
          border:1px solid #de3031;
          color: #de3031;
        }
      }
      .loginFormFontBox{
        font-size: 0.24rem;
        margin-bottom: 0.45rem;
        .left{
          float: left;
          color: #de3031;
        }
        .right{
          float: right;
          color: #939393;
        }
      }
      .loginFormBtn{
        font-size: 0.3rem;
        background-color: #de3031;
        border-radius: 0.5rem;
        line-height: 0.7rem;
        margin-bottom: 0.28rem;
        text-align: center;
        color: #fff;
      }
    }
    .loginFont{
      font-size: 0.24rem;
      color: #939393;
      text-align: center;
      a{
        color: #de3031;
      }
    }
  }
</style>



