<template>
  <div class="pageBox bankCardAddBox">
    <p class="title">
      请绑定持卡本人的银行卡
    </p>
    <input type="text" placeholder="开户人姓名" v-model="backCardData.name">
    <input type="text"  placeholder="开户银行" v-model="backCardData.bankName">
    <input type="number"  placeholder="银行卡号"  v-model="backCardData.bankNumber">
    <p class="subBtns" @click="subFun">
      确定
    </p>
  </div>
</template>
<script>
  import { Toast } from 'mint-ui';
  export default {
    data() {
      return {
        backCardData:{
          type:"0",
          bankNumber:"",
          bankName:"",
          name:"",
        }
      }
    },
    methods: {
      subFun(){
        this.$http.post("/bank/add", this.$qs.stringify(this.backCardData))
          .then(res => {
            console.log('res.data', res)
            if (res.status === 200) {
              if (res.data.code === 200) {
                Toast({
                  message:'添加成功',
                  duration: 1000
                });
                this.$router.push({ path: 'bankCard' });
              } else {
                Toast({
                  message:res.data.msg,
                  duration: 1000
                });
              }
            }
          })
      }
    },
    computed: {}
  }
</script>

<style lang="scss">
  .bankCardAddBox{
    background-color: #ffffff;
    .title{
      line-height: 0.8rem;
      font-size: 0.28rem;
      color: #939393;
      padding:0 0.4rem;
    }
    input{
      display: block;
      width: 6.6rem;
      background-color: #f6f6f6;
      font-size: 0.28rem;
      margin:0  auto 0.4rem auto;
      padding:0.19rem 0.4rem;
      border-radius: 0.5rem;
    }
    .subBtns{
      width: 6.6rem;
      background-color: #de3031;
      font-size: 0.3rem;
      margin:0 auto 0.4rem auto;
      border-radius: 0.5rem;
      line-height: 0.7rem;
      color:#ffffff;
      text-align: center;
    }
  }
</style>



