
<template>
  <div class="pageBox indexOutletBox">
    <!--header start-->
    <div class="indexHeader">
      <a class="indexbackOff" @click="fanhuiFun()">
        <i class="icon iconfont icon-guanbi1-copy"></i>
      </a>
      <div class="indexHeaderSerch">
        <router-link :to="{path:'search',query: {modularId: modularId}}">
          <input type="search" placeholder="请输入关键字" disabled>
          <i class="icon iconfont icon-sousuo"></i>
        </router-link>
      </div>
      <router-link to="myMessageCenter" class="indexHeaderNews">
        <i class="icon iconfont icon-icon-"></i>
      </router-link>
    </div>
    <!--header end-->
    <!--banner start-->
    <div class="indexBanner">
      <mt-swipe :auto="4000">
        <mt-swipe-item v-for=" item in indexData.banner">
          <!--<mt-swipe-item >-->
          <router-link :to="{path:'commodityDetails',query: {id: item.id}}">
            <img :src="item.image " alt="" class="img-my">
          </router-link>
        </mt-swipe-item>
      </mt-swipe>
    </div>
    <!--banner end-->
    <!--九宫格 start-->
    <div class="indexNineBox">
      <ul class="indexNine">
        <li class="indexNineItem" v-for="(item,$index) in indexData.modular" @click="indexCityCon(item,$index)">
          <div>
            <img :src="item.logo" alt="" class="img-my">
            <p>
            {{item.name}}
            </p>
          </div>
        </li>
      </ul>
    </div>
    <!--九宫格 end-->

    <div class="indexmestoActBox">
      <div class="indexFontTit">
        <div class="indexTitBox">
          <img src="@/assets/img/indexTitLe1.png" alt="">
          <span>果农直供</span>
          <img src="@/assets/img/indexTitRi1.png" alt="">
        </div>
        <router-link :to="{path:'moreSpecial',query: {id: activeMouldId}}">
          更多<i class="icon iconfont icon-bread"></i>
        </router-link>
      </div>
      <ul class="after">
        <li  v-for="item in indexOutLetShopList.shopGoods">
          <router-link :to="{path:'commodityDetails',query: {id: item.id}}">
            <div class="imgBox">
              <img :src="item.mainImage" alt="" class="img-my">
            </div>
            <div class="fontBox">
              <p class="font">
                {{item.goodsName}}
              </p>
              <div class="after">
                <p class="left">
                  ¥{{item.salesPrice}} <span>起</span>
                </p>

              </div>
            </div>
          </router-link>
        </li>
      </ul>
    </div>
   <div class="indexOutletListBox">
     <div class="indexFontTit">
       <div class="indexTitBox">
         <img src="@/assets/img/indexTitLe1.png" alt="">
         <span>家乡馆</span>
         <img src="@/assets/img/indexTitRi1.png" alt="">
       </div>
       <router-link :to="{path:'moreShop',query: {id: activeMouldId}}">
         更多<i class="icon iconfont icon-bread"></i>
       </router-link>
     </div>
     <ul>
       <li class="after" v-for="item in indexOutLetShopList.shop">
         <router-link :to="{path:'shopDetails',query: {id: item.id}}">
           <div class="leftImgBox">
             <img :src="item.headImg" alt="" class="img-my">
           </div>
           <div class="rightFontBox">
             <p class="rightFontTit">
               {{item.shopName}}
             </p>
             <p class="rightFontP">
               {{item.announcement}}
             </p>
           </div>
         </router-link>

       </li>
     </ul>
   </div>
  </div>
</template>
<script>
  import Vue from 'vue'
  export default {
    name: 'indexOutlet',
    data () {
      return {
        indexData:{ //banner和类
          news:[ //新闻
            {
              content:'',
              title:''
            }
          ],
        },
//        分类
        outClassFlag:false,
        modularId:'',
        activeMouldId:'',//分类标记id
        indexOutLetShopList:{}//商家和商品列表
      }
    },
    mounted () {
      this.modularId=this.$route.query.id
      this.indexInit()
    },
    methods: {
      /**
       * 页面初始化函数
       */
      indexInit(){
        let idObj={
          modularId:this.modularId
        }
        this.$http.post('/activity/getMoreInfo',this.$qs.stringify(idObj))
          .then(res => {
            if (res.status === 200) {
              console.log('res.data', res.data);
              if(res.data.code===200){
                this.indexData=res.data.data;
                let idObj={
                  modularId:this.indexData.modular[0].id
                };
                this.modularFun(idObj)
                this.activeMouldId=this.indexData.modular[0].id
              }
            }
          })
      },
      /**
       * 分类点击效果
       */
      indexCityCon(item, index){
//        this.$nextTick(function() {
//          this.outClass.forEach(function(item) {
//            Vue.set(item,'outClassFlag',false)
//          });
//          Vue.set(item,'outClassFlag',true)
//        });

        let idObj={
          modularId:item.id
        };
        this.modularFun(idObj)
        this.activeMouldId=item.id
      },
      /**
       * 返回上一个页面
       */
      fanhuiFun(){
        this.$router.go(-1)
      },
      /**
       * 获取模块
       * @param obj
       */
      modularFun(obj){
        this.$http.post('/modularPortal/getShopAndGoodsByModular',this.$qs.stringify(obj))
          .then(res => {
            if (res.status === 200) {
              console.log('res.data', res.data);
              if(res.data.code===200){
                this.indexOutLetShopList=res.data.data;
              }
            }
          })
      }
    },

  }

</script>

<style lang="scss">
  .indexOutletBox{
    background-color: #f7f7f7;
    padding-top: 0.9rem;
    /*padding-bottom: 0.96rem;*/
    /*header start*/
    .indexHeader{
      position: fixed;
      left: 0;
      right: 0;
      top: 0;
      z-index: 5;
      background-color: #de3031;
      height: 0.9rem;
      i{
        font-size: 0.4rem;
      }
      .indexbackOff{
        line-height: 0.9rem;
        font-size: 0.34rem;
        color: #ffffff;
        float: left;
        width: 0.8rem;
        text-align: center;
      }
      .indexHeaderSerch{
        float: left;
        width:5.5rem;
        height: 0.6rem;
        line-height: 0.9rem;
        margin: 0.15rem 0;
        position: relative;
        input{
          border-radius: 0.5rem;
          width: 100%;
          display: block;
          padding: 0.16rem 0.3rem;
          font-size: 0.24rem;
          color: #bfbfbf;
          background-color: #fff !important;
        }
        i{
          position: absolute;
          right: 0.3rem;
          top: 0;
          line-height: 0.6rem;
          font-size: 0.3rem;
          color: #bfbfbf;
          background-color: #fff;
        }
      }
      .indexHeaderNews{
        float: right;
        color: #ffffff;
        padding: 0 0.2rem;
        i{
          font-size: 0.4rem;
          display: block;
          margin: 0.23rem 0;
        }
      }
    }
    /*header end*/
    .indexBanner{
      height: 3rem;
      .mint-swipe-indicator{
        background-color: #c0d2f7;
        opacity: 1;
      }
      .mint-swipe-indicator.is-active {
        background: #ff6f20;
        width: 10px;
        height: 10px;
      }
      img{
        width: 100%;
      }
      .indexBox .indexBanner .mint-swipe-indicator.is-active{
        margin-top: -1px;
      }
      .mint-swipe-indicators{
        bottom: -0.2rem;
      }
    }
    .indexNineBox{
      padding: 0.3rem 0.2rem;
      background-color: #ffffff;
      .indexNine{
        display: -webkit-flex;
        display: flex;
        .indexNineItem{
          margin: auto;
          text-align: center;
          img{
            display: block;
            max-width: 0.8rem;
            max-height: 0.8rem;
            margin: auto;
          }
          p{
            font-size: 0.26rem;
            color: #3b3b3b;
            display: block;
            margin: 0.1rem auto;
          }
        }
      }
      .indexNine:first-child{
        margin-bottom: 0.1rem;
      }
    }
    .indexNotice{
      padding:0 0.2rem;
      margin-top: 0.2rem;
      background-color: #ffffff;
      img{
        max-width: 1.2rem;
        max-height: 0.3rem;
        display: block;
        float: left;
        margin: 0.2rem 0.3rem 0.2rem 0.25rem;
      }
      .indexNoticeBtn{
        color: #fa5537;
        font-size: 0.24rem;
        display: block;
        float: left;
        border:1px solid #fa5537;
        border-radius: 4px;
        width: 0.7rem;
        height: 0.35rem;
        text-align: center;
        line-height: 0.35rem;
        margin: 0.175rem 0.15rem 0.175rem 0;
      }
      .indexNoticeCont{
        width: 4.5rem;
        display: block;
        float: left;
        color: #3b3b3b;
        font-size: 0.24rem;
        line-height: 0.7rem;
        overflow: hidden;
        text-overflow:ellipsis;
        white-space: nowrap;
      }
    }
    .indexActivPush{
      margin-top: 0.2rem;
      .indexActivPushCont{
        padding: 0.24rem 0.2rem;
        background-color: #ffffff;
        float: left;
        width: 50%;
        .indexActivPushFontBox{
          float: left;
          padding: 0.2rem 0;
          .actTit{
            font-size: 0.3rem;
            color: #ff2c31;
            padding-bottom: 0.1rem;
            overflow: hidden;
            text-overflow:ellipsis;
            white-space: nowrap;
            max-width: 2.12rem;
          }
          .actSm{
            font-size: 0.24rem;
            color: #939393;
            overflow: hidden;
            text-overflow:ellipsis;
            white-space: nowrap;
            max-width: 2.12rem;
          }
        }
        .indexActivPushImgBox{
          float: right;
          width: 1.1rem;
          height: 1.1rem;
          border-radius: 50%;
          background-color: #f9fafb;
          display: -webkit-flex;
          display: flex;
          overflow: hidden;
          img{
            margin: auto;
          }
        }
      }
      .indexActivPushCont:first-child{
        border-right: 1px solid #f7f7f7;
      }
    }
    /*横向滑动  start*/
    .indexmestoActBox{
      background-color: #ffffff;
      padding:0 0 0.1rem 0;
      margin-top: 0.2rem;
      .indexFontTit{
        color: #ff661a;
      }
      ul{
        padding: 0 0.05rem;
        li{
          float: left;
          margin:0 0.05rem 0.2rem 0.05rem;
          width: 3.6rem;
          border-radius: 5px;
          border: 1px solid #f0f0f0;
          overflow: hidden;
          a{
            display: block;
            width: 100%;
            height: 100%;
            border-radius: 5px;
            .imgBox{
              width: 3.6rem;
              height: 3.3rem;
              display: -webkit-flex;
              display: flex;
              background-color: #ffffff;
              img{
                margin: auto;
              }
            }
            .font{
              color: #3b3b3b;
              font-size: 0.24rem;
              overflow: hidden;
              text-overflow:ellipsis;
              white-space: nowrap;
            }
            .fontBox{
              background-color: #ffffff;
              padding: 0.1rem;
            }
            .after{
              padding: 0.1rem 0 ;
            }
            .left{
              float: left;
              font-size: 0.28rem;
              color: #ff3f3f;
              span{
                font-size: 0.2rem;
                color: #bebebe;
              }
            }
          }
        }
      }
    }

    /*横向滑动  end*/
    /*头部公用样式*/
    .indexFontTit{
      width: 100%;
      line-height: 0.8rem;
      padding: 0 0.2rem;
      text-align: center;
      font-size: 0.28rem;
      position: relative;
      img{
        vertical-align: middle;
      }
      a{
        padding: 0 0.2rem;
        position: absolute;
        right: 0;
        top: 0;
        font-size: 0.24rem;
        color: #939393;
        i{
          font-size: 0.24rem;
        }
      }
    }
    /*头部公用样式 end*/
  .indexOutletListBox{
    background-color: #ffffff;
    padding:0 0 0.1rem 0;
    margin-top: 0.2rem;
    margin-bottom: 0.2rem;
    .indexFontTit{
      color: #ff661a;
    }
    ul{

      li{
        padding: 0.3rem 0.3rem;
        border: 1px solid #f0f0f0;
        .leftImgBox{
          float: left;
          width: 2rem;
          height: 1.6rem;
          border-radius: 6px;
          overflow: hidden;
          display: -webkit-flex;
          display: flex;
          img{
            margin: auto;
          }
        }
        .rightFontBox{
          width: 4.6rem;
          margin-left: 0.2rem;
          float: left;
          .rightFontTit{
            font-size: 0.3rem;
            color: #333333;
            margin-bottom: 0.1rem;
          }
          .rightFontP{
            font-size: 0.26rem;
            color: #939393;
            overflow: hidden;
            text-overflow: ellipsis;
            display: -webkit-box;
            -webkit-box-orient: vertical;
            -webkit-line-clamp: 3;
          }
        }
      }
      li:last-child{
        border-bottom: none;
      }
    }
  }
  }
</style>



