<template>
  <div class="pageBox myMemberBox">
    <div class="after mymemberUser">
      <div class="imgbox">
        <img src="@/assets/img/indexTu3.png" alt="" class="img-my">
      </div>
      <div class="fonTBox">
        <p class="name">
          陈淑华
        </p>
        <p class="memberGrade">
          您是<span>铜牌</span>会员
        </p>
      </div>
    </div>
    <div class="memberGradeList">
      <div class="item" v-for="(item,index) in simulationData" @touchend="checkedFun(item.id,item.money)">
        <div class="memberIcon">
          <img   alt="" class="img-my" :src="item.src">
        </div>
        <div class="memberFont">
          <p class="memberP1">
            成为<span :class="item.id==1 ? 'copper': item.id==2 ? 'silver' : 'gold'">{{item.name}}</span>会员
          </p>
          <p class="money">
            充值{{item.money}}元
          </p>
        </div>
        <div class="memberDiscount">
          <p  :class="item.ckeckedFlag==true? 'is-checked':'discount' ">
            全场享{{item.discount}} <span>折</span>
          </p>
          <p class="checkBox">
            <i class="icon iconfont icon-danxuan-copy" v-if="item.ckeckedFlag==false"></i>
            <i class="icon iconfont icon-radio" v-else="item.ckeckedFlag==true"></i>
          </p>
        </div>
      </div>
    </div>
    <div class="mymemberBtns">
      <p>
        立即充值：{{storageData.money}}元
      </p>
    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        storageData:{
          money:"0.00",
        },
        simulationData:[
          {
            id:"1",
            src:"../../static/img/memeber_tu3.png",
            name:"铜牌",
            money:"1000",
            discount:" 9.5",
            ckeckedFlag:false,
          },
          {
            id:"2",
            src:"../../static/img/memeber_tu2.png",
            name:"银牌",
            money:"2000",
            discount:" 9.5",
            ckeckedFlag:false,
          },
          {
            id:"3",
            src:"../../static/img/memeber_tu1.png",
            name:"金牌",
            money:"3000",
            discount:" 9.5",
            ckeckedFlag:false,
          },
        ]
      }
    },
    methods: {
      checkedFun(id,money){
        console.log(id)
        console.log(money)
        this.storageData.money=money;
        for(let i=0;i<this.simulationData.length;i++){

          if(this.simulationData[i].id==id){
            this.simulationData[i].ckeckedFlag=true
          }else {
            this.simulationData[i].ckeckedFlag=false
          }
        }
      }
    },
    computed: {}
  }
</script>

<style lang="scss">
  .myMemberBox{
    background-color: #f7f7f7;
    .mymemberUser{
      background-color: #ffffff;
      padding: 0.3rem 0.4rem;
      margin-bottom: 0.3rem;
      .imgbox{
        float: left;
        margin-right: 0.3rem;
        width: 0.9rem;
        height: 0.9rem;
        img{
          width: 0.9rem;
          height: 0.9rem;
          border-radius: 50%;
        }
      }
      .fonTBox{
        float: left;
        .name{
          font-size: 0.34rem;
          color: #3b3b3b;
        }
        .memberGrade{
          font-size: 0.24rem;
          color: #939393;
          span{
            color: #ff2727;
          }
        }
      }
    }
    .memberGradeList{
      padding-left: 0.4rem;
      background-color: #ffffff;
      .item{
        border-bottom: 1px solid #e9e9e9;
        padding: 0.3rem 0.3rem 0.3rem 0;
        .memberIcon{
          float: left;
          display: -webkit-flex;
          display: flex;
          margin-right: 0.3rem;
          height: 0.72rem;
          img{
            width: 0.5rem;
            height: 0.5rem;
            margin: auto;
          }
        }
        .memberFont{
          float: left;
          .memberP1{
            font-size: 0.3rem;
            color: #3b3b3b;
            .copper{
              color: #f1a12f;
            }
            .silver{
              color: #b9adad;
            }
            .gold   {
              color: #ffc11b;
            }
          }
          .money{
            font-size: 0.24rem;
            color: #ff2727;
          }
        }
        .memberDiscount{
          float: right;
          line-height: 0.72rem;
          .discount{
            font-size: 0.3rem;
            color: #3b3b3b;
            float: left;
            span{
              font-size: 0.24rem;
            }
          }
          .is-checked{
            color: #ff2727;
            font-size: 0.3rem;
            float: left;
            span{
              font-size: 0.24rem;
            }
          }
          .checkBox{
            float: left;
            margin-left: 0.3rem;

            i{
              font-size: 0.42rem;
              color: #de3031;
              display: block;
            }
          }
        }
      }
      .item:after{
        display: block;
        clear: both;
        content: '';
      }
    }
    .mymemberBtns{
      margin: 0.6rem 0;
      p{
        line-height: 0.7rem;
        width: 6.9rem;
        border-radius: 0.5rem;
        background-color: #de3031;
        color: #ffffff;
        font-size: 0.3rem;
        margin: auto;
        text-align: center;
      }

    }
  }
</style>



