      <li>
        <p class="title">
          您购买的商品已经发货
        </p>
        <a class="newsBox">
          <div class="imgBox">
            <img src="@/assets/img/index_tu4.png" alt="" class="img-my">
          </div>
          <p class="fontBox">
            和风手绘古朴釉下彩陶艺小酱碗点心糕点碟盘
          </p>
        </a>
