    methods: {},
    computed: {}
  }
</script>

<style lang="scss">
  .myMsgSeeBox{
    background-color: #f7f7f7;
    ul{
      padding: 0.3rem;
      li{
        background-color: #ffffff;
        .title{
          font-size: 0.24rem;
          color: #939393;
          line-height: 0.6rem;
          padding:0 0.2rem;
