<template>
  <div class="pageBox realNameBox">
    <p class="title">
      信息填写
    </p>
    <div class="realNameFill">
      <div class="after realNameInputBox">
        <p>真实姓名</p>
        <input type="text" placeholder="请填写真实姓名" v-model="realNameData.realName" :disabled="realNameFlag===true">
      </div>
      <div class="after realNameInputBox">
        <p>身份证号</p>
        <input type="text" maxlength="18" placeholder="请填写身份证号" v-model="realNameData.idCard" :disabled="realNameFlag===true">
      </div>
    </div>
    <p class="title">
      请上传相关证件，用于验证身份
    </p>
    <div class="realNamePicture">
      <div class="after">
        <p class="font">
          手持身份证照
        </p>
        <div class="pictureBox">
          <img :src="realNameData.idCardImg" alt="" class="img-my">
          <input type="file" @change="upload('idCardImg')" accept="image/*" capture="camera" :disabled="realNameFlag===true" />
        </div>
      </div>
      <div class="after">
        <p class="font">
          身份证正面
        </p>
        <div class="pictureBox">
          <img :src="realNameData.idCardImgZ" alt="" class="img-my">
          <input type="file" @change="upload('idCardImgZ')" accept="image/*" capture="camera" :disabled="realNameFlag===true"/>
        </div>
      </div>
      <div class="after">
        <p class="font">
          身份证反面
        </p>
        <div class="pictureBox">
          <img :src="realNameData.idCardImgF" alt="" class="img-my">
          <input type="file" @change="upload('idCardImgF')" accept="image/*" capture="camera" :disabled="realNameFlag===true"/>
        </div>
      </div>
    </div>
    <div class="realNameBottom" @touchend="realNameSubFun">
        提交认证
    </div>
  </div>
</template>

<script>
  import { MessageBox,Toast } from 'mint-ui';
  import * as util from '../../utils/regular.js'
  export default {
    data() {
      return {
        realNameFlag:'',//标记能不能编辑信息 true -正在审核/已通过
        realNameNum:'',//标记能不能编辑信息 true -正在审核/已通过
        realNameData:{
          realName:'',
          idCard:'',
          idCardImgZ:'',
          idCardImgF:'',
          idCardImg:'',
        }
      }
    },
    mounted () {
      this. realNmaeinit()
    },
    methods: {
      /**
       *页面初始化函数
       */
      realNmaeinit(){
        /**
         * 认证状态
         */
        this.$http.post('/userAuthentication/getStatusByUserId')
          .then(res => {
            if (res.status === 200) {
              this.realNameNum=res.data.code
              if(res.data.code===-1 || res.data.code===-2){
                this.realNameFlag=false
              }else {
                this.realNameFlag=true
              }
              console.log( res.data.code)
              console.log( this.realNameFlag)
            }
          })
        /**
         * 查询认证信息
         */
        this.$http.post('/userAuthentication/getInfoByUserId')
          .then(res => {
            if (res.status === 200) {
              if(res.data.code===200){
                this.realNameData=res.data.data;
                console.log(this.realNameData)
              }
            }
          })
      },
      /**
       * 图片上传函数
       */
      upload(name){
        console.log(name)
        console.log(event)
        let file = event.target.files[0];
        let param = new FormData(); //创建form对象
        param.append('file',file,file.name);//通过append向form对象添加数据
//            param.append('chunk','0');//添加form表单中其他数据
        console.log(param.get('file')); //FormData私有类对象，访问不到，可以通过get判断值是否传进去
        this.$http.post('/upload/imgToOSS',param)
          .then(res=>{
            if (res.status === 200) {
              if(res.data.code===200){
//                this.mySetUpData.headImg=res.data.msg
                for(let item in this.realNameData){
                  if(item==name){
                    this.realNameData[item]=res.data.msg
                  }
                }
              }
            }
          })
      },
      realNameSubFun(){
        console.log(this.realNameNum)
        if(this.realNameNum===0){
          Toast({
            message:'审核通过就不能修改了额',
            duration: 1000
          });
          return false
        }
        if(this.realNameNum===1){
          Toast({
            message:'正在审核中',
            duration: 1000
          });
          return false
        }
        let realNameArr=[];
        for(let item in this.realNameData){
          if(this.realNameData[item]){
            realNameArr.push(true)
          }
        }
        if(realNameArr.length<5){
          Toast({
            message:'请输入完整',
            duration: 1000
          });
        }else {
          let idCard=util.scCard(this.realNameData.idCard);
          console.log(idCard)
          if(idCard==true){
            this.$http.post('/userAuthentication/add', this.$qs.stringify(this.realNameData))
              .then(res => {
                if (res.status === 200) {
                  Toast({
                    message:res.data.msg,
                    duration: 1000
                  });
                  if(res.data.code===200){
                    this.$router.push({ path: '/mySetUp' });
                  }
                }
              })
          }else {
            Toast({
              message:'身份证号码格式错误',
              duration: 1000
            });
          }

        }



      }
    },
    computed: {}
  }
</script>

<style lang="scss">
  .realNameBox{
    background-color: #f7f7f7;
    padding-bottom: 1rem;
    .title{
      padding-left: 0.3rem;
      font-size: 0.28rem;
      color: #9b9b9b;
      line-height: 0.7rem;
    }
    .realNameFill{
      background-color: #ffffff;
      padding-left: 0.3rem;
      .realNameInputBox{
        line-height: 0.9rem;
        border-bottom: 1px solid #ececec;
        padding-right: 0.3rem;
        p{
          float: left;
          font-size: 0.28rem;
          color: #3b3b3b;
        }
        input{
          display: block;
          float: right;
          padding: 0.3rem 0;
          text-align: right;
        }
      }
      .realNameInputBox:last-child{
        border-bottom: none;
      }
    }
    .realNamePicture{
      background-color: #ffffff;
      padding-left: 0.3rem;
      .after{
        padding: 0.3rem 0.3rem 0.3rem 0;
        border-bottom: 1px solid #ececec;
        .font{
          font-size: 0.28rem;
          color: #3b3b3b;
          float: left;
        }
        .pictureBox{
          background: url("../../assets/img/realName_tu1.png")no-repeat center #f0f0f0;
          background-size: 2.4rem 1.6rem;
          float: right;
          width: 2.5rem;
          height: 1.6rem;
          position: relative;
          overflow: hidden;
          /*background-color: ;*/
          border-radius: 6px;
          display: -webkit-flex;
          display: flex;
          img{
            margin: auto;
          }
          input{
            position: absolute;
            left: 0;
            right: 0;
            top: 0;
            bottom: 0;
            display: block;
            opacity: 0;
          }
        }
      }
    }
    .realNameBottom{
      position: fixed;
      left: 0;
      right: 0;
      bottom: 0;
      line-height: 1rem;
      text-align: center;
      font-size: 0.32rem;
      color: #ffffff;
      background-color: #de3031;
    }
    input[disabled=disabled]{
      background-color: transparent;
    }
  }
</style>



