<template>
  <div class="pageBox releaseVehicleBox" >
    <div class="indexConHeader">
      <a class="indexbackOff" @click="fanhuiFun()">
        <i class="icon iconfont icon-guanbi1-copy"></i>
      </a>
      <span v-if="this.typeFlag==1">发布</span>
      <span v-if="this.typeFlag==2">编辑</span>
      <router-link to="usedReleaseList" class="indexNews">
        <i class="icon iconfont icon-cebian-wodedingdan"></i>
      </router-link>
    </div>
    <div class="releaseCont">
      <textarea name="" v-model="relVeData.content" @input="descInput"  class="releaseContText" placeholder="请您多说说需求说明，便于其他人更了解哦！" maxlength="500"></textarea>
      <p class="releaseContNumber">
        {{contentNumber}}/500
      </p>
      <ul class="after releaseImgBox">
        <li v-for="item in imgArr">
          <img :src="item" alt="" class="img-my">
          <i class="icon iconfont icon-guanbi colen" @click="deleteImgFun(item)"></i>
        </li>
        <div class="releaseFile" v-show="imgArr.length<=3">
          <i class="icon iconfont icon-xiangji zxj"></i>
          <input type="file" @change="upload" accept="image/*" capture="camera"/>
        </div>
      </ul>

    </div>
    <div class="releaseFormBox">
      <div class="releaseForm">
        <p>
          联系人
        </p>
        <input type="text" placeholder="你的姓名" v-model="relVeData.name" maxlength="6">
      </div>
      <div class="releaseForm">
        <p>
          联系电话
        </p>
        <input type="tel" placeholder="你的手机号码" v-model="relVeData.phone" maxlength="11">
      </div>
    </div>
    <div class="releaseBtn" @click="releaVehSubFun">
      <p>
        <span v-if="this.typeFlag==1">发布</span>
        <span v-if="this.typeFlag==2">保存</span>
      </p>
    </div>
  </div>

</template>

<script>
  import { Toast } from 'mint-ui';
  import Vue from 'vue';
  import picker from '@/components/picker'
  import * as util from '../../utils/utils.js'
  export default {
    components: {
      picker
    },
    data() {
      return {
        typeFlag:'',//判断是否是编辑状态还是新增状态1 -新增 2-编辑
        myReiD:'',//从编辑来的id
        contentNumber:'0',
        imgArr:[],
        relVeData:{ //整个提交对象
//          classId:'',//id
          content:'',//文本类容
          images:'',//图片路径多个图片路径用逗号隔开
          name:'',//联系人
          phone:'',//联系人电话
          type:3
        },
      }
    },
    mounted () {
      this.typeFlag=this.$route.query.midType;
      this.myReiD=this.$route.query.myReId;
      this.modifyQuery()


    },
    methods: {
      /**
       *如果是编辑状态就查询信息
       *
       */
      modifyQuery(){
        if(this.typeFlag===2){
          console.log('asdasdsadddddddddddddddd',this.typeFlag)
        }

        let objId={
          id:this.myReiD,
        };
        this.$http.post('/activity/toMarketInfo',this.$qs.stringify(objId))
          .then(res => {
            if (res.status === 200) {
              if(res.data.code===200){
                console.log(res.data);
                this.relVeData.content=res.data.data.content;
                this.relVeData.name=res.data.data.name;
                this.relVeData.phone=res.data.data.phone;
                this.imgArr=res.data.data.images.split(",");
              }
            }
          })
      },

      /**
       * 返回上一个页面
       */
      fanhuiFun(){
        this.$router.go(-1)
      },
      /**
       * 文本框输入事件
       */
      descInput(){
        var txtVal = this.relVeData.content.length;
        this.contentNumber = txtVal;
      },
      /**
       * 图片预览函数
       * @param e
       */

      upload(e){
        let file = e.target.files[0];
        let param = new FormData(); //创建form对象
        param.append('file',file,file.name);//通过append向form对象添加数据
//            param.append('chunk','0');//添加form表单中其他数据
        console.log(param.get('file')); //FormData私有类对象，访问不到，可以通过get判断值是否传进去
        this.$http.post('/upload/imgToOSS',param)
          .then(res=>{
            if (res.status === 200) {
              console.log(res.data)
              if(res.data.code===200){
//                this.mySetUpData.headImg=res.data.msg
                console.log(res.data.data)
                this.imgArr.push(res.data.data)
              }
            }
          })
      },
      /**
       * 删除图片
       * @param img 图片路径
       */
      deleteImgFun(img){
//        let arrImg=this.imgArr;
        console.log(img);
        var loop = this.imgArr.length;
        console.log(loop)
        for(let i=0;i<loop;i++){
          if(img === this.imgArr[i]){
            console.log(i);
            this.imgArr.splice(i,1);
            if(i>=1){
              i = i-1;
              console.log(i);
              loop=loop-1
            }
          }
        }
      },
      /**
       * 发布信息
       */
      releaVehSubFun(){
        if(this.$route.query.id){
          this.relVeData.classId=this.$route.query.id;
        }
        let bureArr=[]
        if(this.imgArr.length>0){
          this.relVeData.images=this.imgArr.join(',')
        }
        for(let item in this.relVeData){
          if(!this.relVeData[item]){
            bureArr.push(false)
          }
        }
        if(bureArr.length>0){
          Toast({
            message:'请填写完整',
            duration: 1000
          });
        }else {
          console.log(Number(this.typeFlag))
          if(Number(this.typeFlag)===1){
            let url='/activity/add';
            this.releasSubAjaxFun(url)
          }else {
            this.relVeData.id=this.myReiD
            let url='/activity/updateActivity';
            this.releasSubAjaxFun(url)
          }

        }


      },
      /**
       * 提交函数
       * @param url
       */
      releasSubAjaxFun(url){
        this.$http.post(url,this.$qs.stringify(this.relVeData))
          .then(res => {
            if (res.status === 200) {
              if(res.data.code===200){
                Toast({
                  message:'发布成功',
                  duration: 1000
                });
                this.$router.push({ path: 'usedReleaseList' });
              }else {
                Toast({
                  message:'操作失败',
                  duration: 1000
                });
              }
            }
          })
      }
    },
    computed: {}
  }
</script>
<style lang="scss">

  .releaseVehicleBox {
    background-color: #f7f7f7;
    padding-top: 0.9rem;
    margin-bottom: 0.96rem;
    position: fixed;
    left: 0;
    right: 0;
    bottom: 0;
    top: 0;
    .indexConHeader {
      position: fixed;
      left: 0;
      right: 0;
      top: 0;
      z-index: 5;
      background-color: #de3031;
      height: 0.9rem;
      color: #fff;
      font-size: 0.34rem;
      text-align: center;
      line-height: 0.9rem;
      i{
        font-size: 0.4rem;
      }
      .indexbackOff {
        position: absolute;
        left: 0;
        top: 0;
        bottom: 0;
        line-height: 0.9rem;
        padding: 0 0.2rem;
        color: #fff;
      }
      .indexNews {
        position: absolute;
        right: 0;
        top: 0;
        bottom: 0;
        line-height: 0.9rem;
        padding: 0 0.2rem;
        color: #fff;
      }
    }
    .releaseCont{
      width: 100%;
      padding:0.3rem 0.2rem;
      background-color: #ffffff;
      margin-bottom: 0.2rem;
      .releaseContText{
        width: 100%;
        height: 2.3rem;
        border: none;
      }
      .releaseContNumber{
        color: #939393;
        font-size: 0.24rem;
        text-align: right;
        margin-bottom: 0.2rem;
      }
      .releaseImgBox{
        li{
          position: relative;
          width: 1.34rem;
          height: 1.34rem;
          /*overflow: hidden;*/
          /*display: -webkit-flex;*/
          /*display: flex;*/
          border-radius: 6px;
          margin-right: 0.2rem;
          float: left;
          img{
            /*margin: auto;*/
            width: 1.34rem;
            height: 1.34rem;
            border-radius: 6px;
          }
          .colen{
            padding: 0.1rem;
            font-size: 0.34rem;
            position: absolute;
            right: -0.24rem;
            top: -0.24rem;
            color: #fe3e3e;

          }
        }
        .releaseFile{
          position: relative;
          width: 1.34rem;
          height: 1.34rem;
          overflow: hidden;
          /*display: -webkit-flex;*/
          /*display: flex;*/
          text-align: center;
          line-height: 1.34rem;
          border-radius: 6px;
          border:1px solid #e5e5e5;
          float: left;
          .zxj{
            font-size: 0.5rem;
            display: block;
          }
          input{
            position: absolute;
            left: 0;
            right: 0;
            top: 0;
            bottom: 0;
            opacity: 0;
          }
        }
      }
    }
    .releaseFormBox{
      padding: 0 0 0 0.3rem;
      background-color: #ffffff;
      .releaseForm{
        width: 100%;
        height: 1rem;
        line-height: 1rem;
        border-bottom: 1px solid #f0f0f0;
        p{
          float: left;
          font-size: 0.28rem;
          color: #3b3b3b;

        }
        input{
          float: right;
          display: block;
          font-size: 0.28rem;
          padding: 0.334rem;
          text-align: right;
        }
        i{
          /*float: right;*/
          /*display: block;*/
          font-size: 0.28rem;
          /*padding-right: 0.2rem;*/
          color: #939393;
        }
        span{
          display: block;
          float: right;
          font-size: 0.28rem;
          padding-right: 0.2rem;
          color: #333333;
        }
      }
    }
    .releaseBtn{
      position: fixed;
      left: 0;
      right: 0;
      bottom: 0;
      padding: 0.1rem 0 0.15rem 0;
      background-color: #ffffff;
      p{
        width: 1.7rem;
        height: 0.7rem;
        text-align: center;
        background-color: #de3031;
        color: #ffffff;
        font-size: 0.28rem;
        line-height: 0.7rem;
        border-radius: 0.5rem;
        margin: auto;
      }
    }
    .releaseBTySelectBox{
      position: fixed;
      left: 0;
      right: 0;
      bottom: 0;
      top: 0;
      z-index: 50;
      background-color: rgba(0,0,0,.5);
      border-top: 1px solid #cccccc;
      .releaseBTySelectCont{
        position: absolute;
        left: 0;
        right: 0;
        bottom: 0;
      }
      .releaseBTySelectTit{
        font-size: 0.24rem;
        line-height: 0.6rem;
        background-color: #f7f7f7;
        /*padding: 0 0.2rem;*/
        padding: 0.1rem;
        p{
          width: 1rem;
          border-radius: 4px;
          text-align: center;
        }
        .Close{
          float: left;
          border: 1px solid #ccc;
          background-color: #ffffff;
        }
        .sure{
          float: right;
          background-color: #de3031;
          color: #ffffff;
        }
      }
      .releaseBTySelect{
        background-color: #dddddd;
      }

    }
    .picker-slot{
      font-size: 0.24rem;
    }
    .mint-datetime-action{
      width: 1rem;
      border-radius: 4px;
      text-align: center;
      display: block;
      border: 1px solid #ccc;
      background-color: #ffffff;
      font-size: 0.24rem;
      line-height: 0.6rem;
      color: #333;
    }
    .mint-datetime-confirm{
      background-color: #de3031;
      color: #ffffff;
    }
    .picker-toolbar{
      background-color: #f7f7f7;
      padding: 0.1rem;
    }
  }
</style>



