<template>
  <div class="pageBox aboutUsBox">
    <div class="aboutUsLogoBox">
      <img src="@/assets/img/logo.png" alt="" class="img-my">
    </div>
    <div class="aboutUsCont">
      <div class="after iconRight">
        <p class="p1">
          版本更新
        </p>
      </div>
      <div class="after iconRight">
        <p class="p1">
          微信公众号
        </p>
        <p  class="p2">
          艾坡的
        </p>
      </div>
      <div class="after iconRight">
        <p class="p1">
          评价
        </p>
      </div>
      <div class="after iconRight">
        <p class="p1">
          用户协议
        </p>
      </div>
    </div>
    <p class="aboutUsFottor">
      巨商u客商城   2018
    </p>
  </div>
</template>

<script>
  export default {
    data() {
      return {}
    },
    methods: {},
    computed: {}
  }
</script>

<style lang="scss">
  .aboutUsBox{
    padding-top: 0.4rem;
    background-color: #f7f7f7;
    position: fixed;
    left: 0;
    right: 0;
    bottom: 0;
    top:0;
    .aboutUsLogoBox{
      padding: 0.8rem 0;
      width: 100%;
      display: -webkit-flex;
      display: flex;
      img{
        width: 2.07rem;
        height: 2.07rem;
        border-radius: 6px;
        margin: auto;
      }
    }
    .aboutUsCont{
      width: 6.9rem;
      margin: auto;
      background-color: #ffffff;
    }
    .iconRight{
      width: 100%;
      position: relative;
      border-bottom: 1px solid #f0f0f0;
      padding:0 0.7rem 0 0.2rem;
      .p2{
        font-size: 0.28rem;
        color: #949494;
        line-height: 0.9rem;
        float: right;
      }
      .p1{
        font-size: 0.28rem;
        color: #393838;
        line-height: 0.9rem;
        float: left;
      }

    }
    .iconRight:before{
      font-family: "iconfont" !important;
      font-size: 0.32rem;
      font-style: normal;
      -webkit-font-smoothing: antialiased;
      -moz-osx-font-smoothing: grayscale;
      content: "\E63C";
      position: absolute;
      right: 0.28rem;
      top: 50%;
      margin: -0.16rem 0;
      display: block;
      color: #949494;
    }
    .aboutUsFottor{
      position: fixed;
      left: 0;
      right: 0;
      bottom: 0.5rem;
      font-size: 0.2rem;
      color: #bcbcbc;
      text-align: center;
    }
  }
</style>



