<template>
  <div class="pageBox cashierBox">
    <div class="cashierPrice">
      支付金额：<span>￥159.00</span>
    </div>
    <ul class="cashierList">
      <li>
        <p class="left">
          <i class="icon iconfont icon-danxuan"></i>
        </p>
        <p class="left ">
          <img src="@/assets/img/cashier_tu1.png" alt="" class="">
          余额
        </p>
      </li>
      <li>
        <p class="left">
          <i class="icon iconfont icon-danxuan"></i>
        </p>
        <p class="left ">
          <img src="@/assets/img/cashier_tu2.png" alt="" class="">
          货到付款（需先支付运费）
        </p>
      </li>
      <li>
        <p class="left">
          <i class="icon iconfont icon-danxuan"></i>
        </p>
        <p class="left ">
          <img src="@/assets/img/cashier_tu3.png" alt="" class="">
          银行卡快捷支付
        </p>
        <p class="right">
          <i class="icon iconfont icon-bread"></i>
        </p>
      </li>
      <li>
        <p class="left">
          <i class="icon iconfont icon-danxuan"></i>
        </p>
        <p class="left ">
          <img src="@/assets/img/cashier_tu4.png" alt="" class="">
          支付宝支付
        </p>
      </li>
      <li>
        <p class="left">
          <i class="icon iconfont icon-danxuan"></i>
        </p>
        <p class="left ">
          <img src="@/assets/img/cashier_tu5.png" alt="" class="">
          微信支付
        </p>
      </li>
    </ul>
    <div class="comDetCouponClone">
      去付款
    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        value:"",
      }
    },
    methods: {},
    computed: {}
  }
</script>

<style lang="scss">
  .cashierBox{
    background-color: #f7f7f7;
    padding-bottom: 0.9rem;
    .cashierPrice{
      line-height: 0.9rem;
      padding: 0 0.3rem;
      font-size: 0.3rem;
      color: #3b3b3b;
      margin-bottom: 0.2rem;
      background-color: #ffffff;
      span{
        color: #ff2d2d;
      }
    }
    .cashierList{
      padding: 0 0 0 0.3rem;
      background-color: #ffffff;
      li{
        border-bottom: 1px solid #f0f0f0;
        line-height: 1.1rem;
        padding-right: 0.3rem;
        .left{
          float: left;
          font-size: 0.3rem;
          color: #3b3b3b;
          i{
            font-size: 0.4rem;

          }
          img{
            width: 0.5rem;
            height: 0.5rem;
            vertical-align: middle;
            margin: 0 0.3rem;
          }
        }
        .right{
          float: right;
          i{
            display: block;
          }
        }
      }
      li:after{
        content: "";
        display: block;
        clear: both;
      }
    }
    .comDetCouponClone{
      position: absolute;
      bottom: 0;
      right: 0;
      left: 0;
      line-height: 0.9rem;
      text-align: center;
      color: #ffffff;
      background-color: #de3031;
      font-size: 0.32rem;
    }
  }
</style>



