<template>
  <div class="pageBox feedbackBox">
    <div class="txtarea">
      <textarea v-model="content" placeholder="请留下您的宝贵意见，我们会认真阅读并更好的优 化产品。 ">

      </textarea>
    </div>
    <p class="subBtns" @click="feedBackFun">
      提交
    </p>
  </div>

</template>

<script>
  import { Toast } from 'mint-ui';
    export default {
        data() {
            return {
              content:''
            }
        },
        methods: {
          feedBackFun(){
            let obj={
              content:this.content
            }
            this.$http.post("/feedback/add", this.$qs.stringify(obj))
              .then(res => {
                console.log('res.data', res)
                if (res.status === 200) {
                  if (res.data.code === 200) {
                    Toast({
                      message:'提交成功',
                      duration: 1000
                    });
                    this.$router.push({ path: 'my' });
                  } else {
                    Toast({
                      message:res.data.msg,
                      duration: 1000
                    });
                  }
                }
              })
          }
        },
        computed: {}
    }
</script>

<style lang="scss">
.feedbackBox{
  background-color: #f7f7f7;
  position: fixed;
  left: 0;
  right: 0;
  bottom: 0;
  top:0;
  .txtarea{
    width: 6.9rem;
    height: 4.5rem;
    margin:0.2rem auto 0 auto;
    textarea{
      resize: none;
      display: block;
      width: 100%;
      height: 100%;
      font-size: 0.28rem;
      padding: 0.2rem 0.4rem;
    }
  }
  .subBtns{
    width: 6.9rem;
    background-color: #de3031;
    font-size: 0.3rem;
    margin: 0.4rem auto 0.4rem auto;
    border-radius: 0.5rem;
    line-height: 0.7rem;
    color:#ffffff;
    text-align: center;
  }
}
</style>



