            display:-webkit-box;
            -webkit-box-orient:vertical;
            -webkit-line-clamp:2;
          }
          .after{
            /*margin-top: 0.2rem;*/
            .price{
              font-size: 0.28rem;
              color: #ff4444;
              float: left;
            }
            .shoppCalculation{
              float: right;
              width: 1.9rem;
              p{
                float: left;
                font-size: 0.3rem;
                line-height: 0.5rem;
                img{
                  width: 0.5rem;
                  height: 0.5rem;
                }
              }
              .shoppCalculationIconNumber{
                width: 0.8rem;
                text-align: center;
              }
            }
          }
          .shoppCheck{
            line-height: 1.32rem;
