            bottom: 0;

          }
        }



        .shoppCartListCont:last-child{
          border-bottom: none;
        }
        .move-enter-active, .move-leave-active {
          transition: all 0.5s linear;
          transform: translate3d(0, 0, 0);
        }
         .move-enter,  .move-leave {
          transform: translate3d(30%, 0, 0);
        }
      }
    }
    .shoppSettlement{
      position: fixed;
      left: 0;
