<script>
  import Vue from 'vue';
  import { Toast } from 'mint-ui';
    export default {
        data() {
            return {
              allLoaded:false,//上拉加载标记
              comDetCouFlag:false,//优惠卷弹窗
              scFlag:false,

              userId:'',
              iskillFlag:'',
              list:10,
              selected:"2",
              commPriceArr:[],
              htmlStr:'<p>店内环境</p><img src="http://img0.imgtn.bdimg.com/it/u=3129600111,2504721571&fm=27&gp=0.jpg" alt="" class="img-my"><p>商家简介</p><p > 商家简介商家简介商家简介商家简介</p><img src="http://img0.imgtn.bdimg.com/it/u=3129600111,2504721571&fm=27&gp=0.jpg" alt="" class="img-my">',
              storageData:{//用来存储一些标记
                lineORcolFlag:true, //为true===竖 false===行
                togetherPopupFlag:false, //控制综合弹窗标记
                screenPopupFlag:false, //控制筛选弹窗标记

              },
              commodityData:{},//商品信息
              commBanner:[],//banner
              commPrice:{},//规格信息
              commTitArr:{},//条件
              pingJiaArr:[],//评价列表

            }
        },
      mounted () {
        this.iskillFlag=this.$route.query.iskill;
        this.userId=localStorage.getItem('userId')
        this.commDetInit();
        this.comodPanDuanFun()
      },
        methods: {
          /**
           * 页面初始化
           */
          commDetInit(){
            let objData={
              id:this.$route.query.id
            };
            this.$http.post('/shopGoods/getShopGoodsById',this.$qs.stringify(objData))
              .then(res => {
                if (res.status === 200) {
                  console.log('res.data', res.data);
                  if(res.data.code===200){
                    this.commodityData=res.data.data.shopGoods;
                    if(res.data.data.shopGoods.shopGoodsSize.length>=1){
                      this.commPrice=res.data.data.shopGoods.shopGoodsSize[0];
                      this.commPriceArr=res.data.data.shopGoods.shopGoodsSize;
                      Vue.set(this.commPriceArr[0],'sizeFlag',true);
                    }
                    if(res.data.data.goodsLabel){
                      this.commTitArr=res.data.data.goodsLabel.label.split(",");
                    }
                    this.commBanner=res.data.data.shopGoods.goodsImg.split(",");
//                    let yhjObj={
//                      userId:this.userId,
//                      shopId:this.commodityData.shopId,
//                      money:'',
//                    }
                    let pjObj={
                      goodsId:this.commodityData.id
                    };
                    this.pingJiaFun(pjObj)
                  }
                }
              })
          },
          /**
           * 上拉加载
           */
          loadBottom() {
            this.list=Number( this.list)+5
            if(this.list>=100){
              this.allLoaded = true;
            }
            this.$refs.loadmore.onBottomLoaded();
          },
          /**
           * 关闭优惠卷弹窗
           */
          youhuiSowFun(){
            this.comDetCouFlag=!this.comDetCouFlag
          },
          /**
           * 阻止事件冒泡
           */
          receiveYouhui(){
            event.stopPropagation();
          },
          /**
           * 选择规格
           * @param item
           */
          sizeClickFun(item){
//            console.log(item)
            this.$nextTick(function() {
              this.commPriceArr.forEach(function(data) {
                Vue.set(data,'sizeFlag',false)
              });
              Vue.set(item,'sizeFlag',true)
            });
            this.commPrice=item
          },
          /**
           * 获取优惠卷列表
           * @param obj
           */
          youhuiJuanFun(obj){
            this.$http.post('/coupon/listByShopId',this.$qs.stringify(obj))
              .then(res => {
                if (res.status === 200) {
                  console.log('res.data', res.data);
                  if(res.data.code===200){

                  }
                }
              })
          },
          /**
           * 评价数据
           * @param obj
           */
          pingJiaFun(obj){
            this.$http.post('/goodsEvaluate/get',this.$qs.stringify(obj))
              .then(res => {
                if (res.status === 200) {
                  console.log('res.data', res.data);
                  if(res.data.code===200){
                      this.pingJiaArr=res.data.data
                  }
                }
              })
          },
          /**
           * 添加收藏
           */
          shouCangFun(){
            let scObj={
              goodsId:this.$route.query.id,
              userId:this.userId
            };
            this.$http.post('/collectGoods/add',this.$qs.stringify(scObj))
              .then(res => {
                if (res.status === 200) {
                  console.log('res.data', res.data);
                  if(res.data.code===200){
                    if(res.data.data===0){
                      this.scFlag=false
                    }else {
                      this.scFlag=true
                    }
                    Toast({
                      message:res.data.msg,
                      duration: 1000
                    });
                  }
                }
              })
          },
          /**
           * 查询该商品是否是收藏状态
           */
          comodPanDuanFun(){
            let scPdobj={
              goodsId:this.$route.query.id,
              userId:this.userId
            };
            this.$http.post('/collectGoods/isCollected',this.$qs.stringify(scPdobj))
              .then(res => {
                if (res.status === 200) {
                  console.log('res.data', res.data);
                  if(res.data.code===200){
                    if(res.data.data===0){
                      this.scFlag=false
                    }else {
                      this.scFlag=true
                    }
                  }
                }
              })
          },
          /**
           * 查询该商品是否是收藏状态
           */
          addCartFun(){
            let scPdobj={
              goodsId:this.$route.query.id,
              userId:this.userId
            };
            this.$http.post('/collectGoods/isCollected',this.$qs.stringify(scPdobj))
              .then(res => {
                if (res.status === 200) {
                  console.log('res.data', res.data);
                  if(res.data.code===200){
                    if(res.data.data===0){
                      this.scFlag=false
                    }else {
                      this.scFlag=true
                    }
                  }
                }
              })
          },
        },
        computed: {}
    }
</script>

