    .comDetBtmItem:first-child{
      width: 34%;
    }
    .addCart{
      background-color: #FDC00f;
      font-size: 0.28rem;
      color: #ffffff;
      line-height: 1rem;
      text-align: center;
    }
    .payLike{
      background-color: #de3031;
      font-size: 0.28rem;
      color: #ffffff;
      line-height: 1rem;
      text-align: center;
    }

  }
  /*.优惠卷弹窗*/
  .comDetCouponBox{
    position: fixed;
    left: 0;
    right: 0;
    bottom: 0;
    top: 0;
    z-index: 15;
    background-color: rgba(225,225,225,1);
    .comDetCoupon{
      background-color: #ffffff;
      border-top: 1px solid #f0f0f0;
      position: absolute;
      left: 0;
      right: 0;
      bottom: 0;
      padding: 0.3rem 0 0.3rem 0.3rem;
      .comDetCouponTit{
        font-size: 0.32rem;
        color: #3b3b3b;
        text-align: center;
      }
      ul{
        min-height: 5rem;
        max-height: 6rem;
        overflow-y: auto;
        padding-bottom: 0.9rem;
        li{
          padding: 0.3rem 0.3rem  0.3rem 0;
          border-bottom: 1px solid #f0f0f0;
          .left{
            float: left;
            max-width: 5.6rem;
            .comDetCouponPrice{
              font-size: 0.32rem;
              color: #fa7070;
            }
            .comDetCouponCim{
              font-size: 0.28rem;
              color: #676767;
            }
            .time{
              font-size: 0.20rem;
              color: #a0a0a0;

            }
          }
          .right{
            float: right;
            height: 1.08rem;
            display: -webkit-flex;
            display: flex;
            .comDetCouponNo{
              width: 1.2rem;
              height: 0.46rem;
              border-radius: 4px;
              text-align: center;
              line-height: 0.46rem;
              margin: auto;
              color: #de3031;
              font-size: 0.28rem;
              border: 1px solid #de3031;
            }
          }
        }
        li:after{
          display: block;
          content: "";
          clear: both;
        }

      }
      .comDetCouponClone{
        position: absolute;
        bottom: 0;
        right: 0;
        left: 0;
        line-height: 0.9rem;
        text-align: center;
        color: #ffffff;
        background-color: #de3031;
        font-size: 0.32rem;
      }
    }
  }
  .mint-loadmore-bottom{
    background-color: #ffffff;

    /*p{*/
      /*font-size: 0.24rem;*/
    /*}*/
  }
}
</style>



