<template>
  <div class="releaseBTySelectBox" v-show="pickerFlag">
    <div class="releaseBTySelectCont">
      <div class="after releaseBTySelectTit">
        <p class="Close"  @click="hideFun">
          取消
        </p>
        <p class="sure"  @click="subReturnFun">
          确认
        </p>
      </div>
      <div class="releaseBTySelect">
        <mt-picker :slots="modify.slots" :value-key=modify.names @change="onValuesChange1" ></mt-picker>
      </div>
    </div>
  </div>
</template>

<script>
    export default {
        data() {
            return {
              pickerDataNmae:"",//name
              pickerData:"",//data
              pickerDataVal:"",//name
            }
        },
      props: ['pickerFlag', 'modify'],
        methods: {
          onValuesChange1(picker, values) {
            console.log(values)
            this.pickerData=values
//            this.pickerDataVal=values[0].value
//            if (values[0] > values[1]) {
//              picker.setSlotValue(1, values[0]);
//            }
          },
          // 关闭弹窗
          hideFun () {
            this.$emit('update:pickerFlag')
          },
          /**
           * 提交事件
           */
          subReturnFun(){
            this.$emit('subReturnFuns',{"row":this.pickerData,'ziDuanMin':this.modify.title});
            this.hideFun ()
          },
        },

        computed: {}
    }
</script>

<style lang="scss">

  .releaseBTySelectBox{
    position: fixed;
    left: 0;
    right: 0;
    bottom: 0;
    top: 0;
    z-index: 50;
    background-color: rgba(0,0,0,.5);
    border-top: 1px solid #cccccc;
    .releaseBTySelectCont{
      position: absolute;
      left: 0;
      right: 0;
      bottom: 0;
    }
    .releaseBTySelectTit{
      font-size: 0.24rem;
      line-height: 0.6rem;
      background-color: #f7f7f7;
      /*padding: 0 0.2rem;*/
      padding: 0.1rem;
      p{
        width: 1rem;
        border-radius: 4px;
        text-align: center;
      }
      .Close{
        float: left;
        border: 1px solid #ccc;
        background-color: #ffffff;
      }
      .sure{
        float: right;
        background-color: #de3031;
        color: #ffffff;
      }
    }
    .releaseBTySelect{
      background-color: #dddddd;
    }

  }

</style>



