<template>
<div class="pageBox MybalanceBox">
  <div class="indexConHeader">
    <a class="indexbackOff" @click="fanhuiFun()">
      <i class="icon iconfont icon-guanbi1-copy"></i>
    </a>
    余额
    <router-link to="myBill" class="indexNews">
     账单
    </router-link>
  </div>
  <div class="MybalanceCont">
    <div class="imgBox">
      <img src="@/assets/img/myBalance.png" alt="" class="img-my">
    </div>
    <p class="name">
      我的余额
    </p>
    <p class="price">
      ¥{{myBalanceStr}}
    </p>
  </div>
  <router-link to="putForward" >
    <p class="subBtns">
      提现
    </p>
  </router-link>

</div>
</template>

<script>
    export default {
        data() {
            return {
              myBalanceStr:'',
            }
        },
      mounted () {
        this.myBalanceInit()
      },
        methods: {

          myBalanceInit(){
            this.$http.post('/userRemainder/getCount')
              .then(res => {
                if (res.status === 200) {
                  if(res.data.code===200){
                    this.myBalanceStr=res.data.data
                    console.log(res.data)
                  }

                }
              })

          },
          /**
           * 返回上一个页面
           */
          fanhuiFun(){
            this.$router.go(-1)
          },
        },
        computed: {}
    }
</script>

<style lang="scss">
.MybalanceBox{
  background-color: #f7f7f7;
  padding: 2.3rem 0.3rem 0 0.3rem;
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom:0;
  .indexConHeader {
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    z-index: 5;
    background-color: #de3031;
    height: 0.9rem;
    color: #fff;
    font-size: 0.34rem;
    text-align: center;
    line-height: 0.9rem;
    i{
      font-size: 0.4rem;
    }
    .indexbackOff {
      position: absolute;
      left: 0;
      top: 0;
      bottom: 0;
      line-height: 0.9rem;
      padding: 0 0.2rem;
      color: #fff;
    }
    .indexNews {
      position: absolute;
      right: 0;
      top: 0;
      bottom: 0;
      line-height: 0.9rem;
      padding: 0 0.2rem;
      color: #fff;
      font-size: 0.24rem;
    }
  }
  .MybalanceCont{
    margin-bottom: 2.3rem;
    text-align: center;
    .imgBox{
      width: 2.1rem;
      height: 2.1rem;
      margin: auto;
    }
    .name{
      font-size: 0.28rem;
      color: #333333;
      margin: 0.1rem 0;
    }
    .price{
      font-size: 0.48rem;
      color: #3b3b3b;

    }
  }
  .subBtns{
    width: 100%;
    background-color: #de3031;
    font-size: 0.3rem;
    margin: 0 auto 0 auto;
    border-radius: 0.6rem;
    line-height: 0.7rem;
    color:#ffffff;
    text-align: center;
  }
}
</style>



