<template>
  <div class="pageBox myBox">
    <div class="myheader">
        <div class="after myheaderTit">
          <router-link  :to="{path:'mySetUp'}" class="left">设置</router-link>
            <router-link  :to="{path:'myMessageCenter'}" class="right">
              <i class="icon iconfont icon-icon-"></i>
            </router-link>
        </div>
      <div class="after myheaderCont">
        <div class="myHeaderIntegral">
          <router-link  :to="{path:'myIntegral'}" >
          <img src="@/assets/img/myIntegral.png" alt="" class="img-my">
          <p>
              积分{{myData.sum_jiFen}}
          </p>
          </router-link>
        </div>
        <div class="myheaderPortrait">
          <router-link  :to="{path:'mySetUp'}" >
            <div class="myheaderImgBox">
              <img :src="myData.user.headImg" alt="" class="img-my">
            </div>
            <p>
              {{myData.user.nickName}}
            </p>
          </router-link>
        </div>
        <div class="myheaderVip">
          <router-link  :to="{path:'myMember'}" >
            <img src="@/assets/img/myPortrait.png" alt="" class="img-my">
            <p v-if="myData.user.level==0">
              普牌会员
            </p>
            <p v-else-if="myData.user.level==1">
              铜牌会员
            </p>
            <p v-else-if="myData.user.level==2">
              银牌会员
            </p>
            <p v-else-if="myData.user.level==3">
              金牌会员
            </p>
          </router-link>

        </div>
      </div>
    </div>
    <div class="myExhibitionBox">
      <router-link  :to="{path:'Mybalance'}" class="myExhibitionItem">
        <p class="myExhibitionItemP1">
          <span>{{myData.sum_yuE}}</span>元
        </p>
        <p class="myExhibitionItemP2">
          余额
        </p>
      </router-link>
      <router-link  :to="{path:'coupons'}" class="myExhibitionItem">
        <p class="myExhibitionItemP1">
          <span>{{myData.sum_youHuiJuan}}</span>张
        </p>
        <p class="myExhibitionItemP2">
          优惠劵
        </p>
      </router-link>
      <router-link  :to="{path:'myCollection'}"  class="myExhibitionItem">
        <p class="myExhibitionItemP1">
          <span>{{myData.sum_shouCang}}</span>个
        </p>
        <p class="myExhibitionItemP2">
          收藏
        </p>
      </router-link>
    </div>
    <div class=" myTitleBox">
      <router-link  :to="{path:'address'}" class="after" >
        <img src="@/assets/img/myIcon_tu1.png" alt="" class="img-my">
        <p>
          我的地址
