      </router-link>
      <router-link  :to="{path:'bankCard'}" class="after" >
        <img src="@/assets/img/myIcon_tu3.png" alt="" class="img-my">
        <p>
          银行卡管理
        </p>
        <i class="icon iconfont icon-bread"></i>
      </router-link>
    </div>
    <div class=" myTitleBox">
      <router-link  :to="{path:''}" class="after" >
        <img src="@/assets/img/myIcon_tu4.png" alt="" class="img-my">
        <p>
          到店支付
        </p>
        <i class="icon iconfont icon-bread"></i>
      </router-link>
    </div>
    <div class=" myTitleBox">
      <router-link  :to="{path:'myInvitation'}" class="after" >
        <img src="@/assets/img/myIcon_tu5.png" alt="" class="img-my">
        <p>
          邀请好友
        </p>
        <i class="icon iconfont icon-bread"></i>
      </router-link>
      <router-link  :to="{path:'feedback'}" class="after" >
        <img src="@/assets/img/myIcon_tu6.png" alt="" class="img-my">
        <p>
          寄件反馈
        </p>
        <i class="icon iconfont icon-bread"></i>
      </router-link>
      <router-link  :to="{path:'aboutUs'}" class="after" >
        <img src="@/assets/img/myIcon_tu7.png" alt="" class="img-my">
        <p>
          关于我们
        </p>
        <i class="icon iconfont icon-bread"></i>
      </router-link>

    </div>
