<script>
  import bottom from '@/components/bottom'
  import * as util from '../../utils/utils.js'
    export default {
      name: 'my',
      components: {
        bottom
      },
      data() {
            return {
              myData:{
                user:{

                },
              }
            }
        },
      mounted () {
       this. myinit()
      },
      methods: {
          myinit(){
            this.$http.post('/user/getCount')
              .then(res => {
                if (res.status === 200) {
                  console.log('res.data', res.data)
                  if(res.data.code===200){
//                    console.log('res.data', res.data.data)
                    this.myData= res.data.data

                    console.log(this.myData)
                  }

                }
              })

          },
        },
      computed: {}
    }

</script>

<style lang="scss">

  .myBox{
    background-color: #f7f7f7;
    padding: 0 0 0.96rem 0;
    .myheader{
      background: url("../../assets/img/myBack.png") no-repeat center center;
      padding: 0 0.2rem;
      .myheaderTit{
        background-color: transparent;
        color: #ffffff;
        line-height: 0.9rem;
       .left{
         float: left;
         font-size: 0.28rem;
         color: #ffffff;
       }
        .right{
          float: right;
          i{
            font-size: 0.4rem;
            display: block;
            color: #ffffff;
          }

