      .myheaderCont{
        /*padding: 0 0.2rem;*/

        .myHeaderIntegral{
          width: 2.1rem;
          float: left;
          /*padding-left: 0.4rem;*/
          padding-top: 0.6rem;
          text-align: center;
          p{
            font-size: 0.24rem;
            color: #fff;
            margin-top: 0.1rem;
          }
          img{
            width: 0.47rem;
            height: 0.41rem;
           margin: auto;
          }
        }
        .myheaderPortrait{
          width: 2.5rem;
          text-align: center;
          float: left;
          p{
            font-size: 0.34rem;
            color: #fff;
            margin: 0.3rem 0 0.7rem 0;
          }
          .myheaderImgBox{
            width: 1.4rem;
            height: 1.4rem;
            border-radius: 50%;
            overflow: hidden;
            background-color: #ea7e6f;
            padding: 0.08rem;
            display: -webkit-flex;
            display: flex;
            margin: auto;
            img{
              border-radius: 50%;
              margin: auto;
            }
          }
        }
        .myheaderVip{
          float: right;
          width: 2.1rem;
          text-align: center;

          padding-top: 0.6rem;
          p{
            font-size: 0.24rem;
            color: #fff;
            margin-top: 0.1rem;
          }
          img{
            width: 0.47rem;
            height: 0.46rem;
           margin: auto;
          }
        }
      }
    }
    .myExhibitionBox{
      display: -webkit-flex;
      display: flex;
      padding: 0.3rem 0;
      background-color: #ffffff;
      margin-bottom: 0.2rem;
      .myExhibitionItem{
        margin: auto;
        text-align: center;
        .myExhibitionItemP1{
          font-size: 0.18rem;
          color: #949494;
          span{
            font-size: 0.24rem;
            padding-right: 0.03rem;
          }
        }
        .myExhibitionItemP2{
          color: #000000;
          font-size: 0.26rem;
        }
      }
    }
    .myTitleBox{
      line-height: 0.9rem;
      padding-left: 0.4rem;
      background-color: #ffffff;
      margin-bottom: 0.2rem;
      a{
        display: block;
        width: 100%;
        height: 100%;
        border-bottom: 1px solid #ededed;
        img{
          float: left;
          width: 0.4rem;
          height: 0.4rem;
          margin: 0.25rem 0.2rem 0.25rem 0.02rem ;
        }
        p{
          float: left;
          font-size: 0.28rem;
          color: #272727;
        }
        i{
          display: block;
          float: right;
          margin-right: 0.3rem;
        }
      }
      a:last-child{
        border-bottom: none;
      }
    }

  }
</style>



