<template>
  <div class="pageBox shApplyBox">
    <div class="after shApplyComdiy">
      <div class="imgBox">
        <img src="@/assets/img/indexTu3.png" alt="" class="img-my">
      </div>
      <div class="fontBox">
        <p class="p1">
          和风手绘古朴釉下彩陶艺小酱碗点心糕点 碟盘
        </p>
        <p class="p2">
          型号：中号*10
        </p>
      </div>
    </div>
    <div class="shApplySelectListBox">
      <div class="after shApplySelectList">
        <a>
          <div class="itemImg">
            <img src="@/assets/img/shouhou_tu1.png" alt="" class="img-my">
          </div>
          <div class="itemFontBox">
            <p class="p1">
              仅退款
            </p>
            <p class="p2">
              未收到货，或卖家协商同意前提下
            </p>
          </div>
          <div class="itemFontIcon">
            <i class="icon iconfont icon-bread"></i>
          </div>
        </a>
      </div>
      <div class="after shApplySelectList">
        <a>
          <div class="itemImg">
            <img src="@/assets/img/shouhou_tu2.png" alt="" class="img-my">
          </div>
          <div class="itemFontBox">
            <p class="p1">
              退货退款
            </p>
            <p class="p2">
              已收到货，需要退换已收到的货物
            </p>
          </div>
          <div class="itemFontIcon">
            <i class="icon iconfont icon-bread"></i>
          </div>
        </a>
      </div>
      <div class="after shApplySelectList">
        <a>
          <div class="itemImg">
            <img src="@/assets/img/shouhou_tu3.png" alt="" class="img-my">
          </div>
          <div class="itemFontBox">
            <p class="p1">
              换货
            </p>
            <p class="p2">
              已收到货，需要更换已收到的货物
            </p>
          </div>
          <div class="itemFontIcon">
            <i class="icon iconfont icon-bread"></i>
          </div>
        </a>
      </div>

    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {}
    },
    methods: {},
    computed: {}
  }
</script>

<style lang="scss">
.shApplyBox{
  background-color: #f7f7f7;
  .shApplyComdiy{
    padding: 0.2rem 0.3rem 0.2rem 0.2rem;
    background-color: #ffffff;
    margin-bottom: 0.2rem;
    .imgBox{
      float: left;
      width: 1.3rem;
      height: 1.3rem;
      border-radius: 6px;
      margin-right: 0.3rem;
      img{
        width: 1.3rem;
        height: 1.3rem;
        border-radius: 6px;
      }
    }
    .fontBox{
      float: left;
      width: 5.4rem;
      .p1{
        color: #3b3b3b;
        font-size: 0.28rem;
        overflow:hidden;
        text-overflow:ellipsis;
        display:-webkit-box;
        -webkit-box-orient:vertical;
        -webkit-line-clamp:2;
        max-width: 100%;
      }
      .p2{
        color: #939393;
        font-size: 0.24rem;

      }
    }
  }
  .shApplySelectListBox{
    background-color: #ffffff;
    .shApplySelectList{
      border-bottom: 1px solid #e7e7e7;
      a{
        display: block;
        width: 100%;
        height: 100%;
      }
      .itemImg{
        float: left;
        height: 1.2rem;
        display: -webkit-flex;
        display: flex;
        width: 1rem;
        img{
          margin: auto;
          width: 0.4rem;
          height: 0.4rem;
        }
      }
      .itemFontBox{
        float: left;
        padding: 0.2rem 0;
        .p1{
          font-size: 0.28rem;
          color: #3b3b3b;
          /*line-height: 0.6rem;*/
        }
        .p2{
          font-size: 0.24rem;
          color: #939393;
          /*line-height: 0.6rem;*/
        }
      }
      .itemFontIcon{
        float: right;
        line-height: 1.2rem;
        margin-right: 0.25rem;
        i{
          display: block;
          font-size: 0.3rem;
        }
      }
    }
  }
}
</style>



