import Vue from 'vue'
import Vuex from 'vuex'
// import login from './modules/login'
Vue.use(Vuex);

//Vuex配置
const store = new Vuex.Store({
  state: {
    collection:'',//收藏数量
  },
  mutations : {
    collection( state, msg ){
      state.collection = msg;
    },
  }

});

export default store



