<template>
  <div class="pageBox payDistrBox">
    <div class="payModeBox">
      <p class="payModeTit">
        支付方式
      </p>
      <div class="after payModeList">
        <p>
          在线支付
        </p>
        <p class="is-active">
          货到付款
        </p>
      </div>
    </div>
    <div class="payModeBox distrMode">
      <p class="payModeTit">
        配送方式
      </p>
      <div class="payModeList">
        <p>
          在线支付
        </p>
        <p class="is-active">
          货到付款
        </p>
        <p>
          到店自提
        </p>
      </div>
      <div class="after youhj">
        <p class="left">
          立即送出（大约16:42送达）
        </p>
        <p class="right">
          修改时间
          <i class="icon iconfont icon-bread"></i>
        </p>
      </div>
    </div>
  </div>
</template>

<script>
    export default {
        data() {
            return {}
        },
        methods: {},
        computed: {}
    }
</script>

<style lang="scss">
  .payDistrBox{
    background-color: #f7f7f7;
    .payModeBox{
      background-color: #ffffff;
      padding: 0 0.3rem 0.3rem 0.3rem;
      margin-bottom: 0.1rem;
      font-size: 0.28rem;
      color: #3b3b3b;
      .payModeTit{
        line-height: 0.86rem;
      }
      .payModeList{
        p{
          float: left;
          text-align: center;
          border-radius: 6px;
          width: 2rem;
          height: 0.7rem;
          margin-right: 0.2rem;
          border: 1px solid #f0f0f0;
          line-height: 0.7rem;
        }
        .is-active{
          border-color: #de3031;
          color: #de3031;
        }
      }
    }
    .youhj{
      line-height: 0.88rem;
      font-size: 0.28rem;
      color: #3b3b3b;
      clear: both;
      .left{
        float: left;
      }
      .right{
        float: right;
        line-height: 0.88rem;
        i{
          font-size: 0.24rem;
          padding-left: 0.25rem;
        }
      }
    }
    .distrMode{
      padding-bottom: 0;
    }
  }


</style>



