<template>
  <div class="pageBox indexConBox">
    <div class="indexConHeader">
      <a class="indexbackOff" @click="fanhuiFun()">
        <i class="icon iconfont icon-guanbi1-copy"></i>
      </a>
      便捷生活
      <router-link to="myRelease" class="indexNews">
        <i class="icon iconfont icon-cebian-wodedingdan"></i>
      </router-link>
    </div>
    <!--banner start-->
    <div class="indexBanner">
      <mt-swipe :auto="4000">
        <mt-swipe-item v-for=" item in indexConvenientData.banner">
          <!--<mt-swipe-item >-->
          <router-link :to="{path:'commodityDetails',query: {id: item.id}}">
            <img :src="item.image " alt="" class="img-my">
          </router-link>
        </mt-swipe-item>
      </mt-swipe>
    </div>
    <!--banner end-->
    <div class="indexConListBox">
      <div class="indexConListTit">
        <ul class="indexConListTitDiv">
          <li v-for=" (item,$index) in indexConvenientData.classLife" @click="indexConvenListFun(item,$index)">
            {{item.className}}
          </li>
        </ul>
      </div>
      <ul class="indexConListHcart">
        <li class="after" v-for="item in indexConvenientFabu">
          <router-link :to="{path:'issueDetails',query: {id: item.id,type:item.type}}">
            <div class="indexConListImgBox">
              <img :src="item.images" alt="" class="img-my">
            </div>
            <div class="indexConListFontBox">
              <p class="indexConListFontP1">
                {{item.title}}
              </p>
              <p class="indexConListFontP2">
                ￥<span>{{item.price}}</span>
              </p>
            </div>
          </router-link>
        </li>
      </ul>
      <div class="indexConHr" v-if="indexConvenientFabu.length>=1">
        <router-link :to="{path:'moreList'}">
          查看更多   <i class="icon iconfont icon-bread"></i>
        </router-link>
      </div>
      <div style="overflow:scroll;">
        <mt-loadmore :bottom-method="loadBottom" :bottom-all-loaded="allLoaded" ref="loadmore"  :auto-fill="false">
          <ul class="indexConListHcart">
            <li class="after" v-for="item in indexConvenientxuqiu">
              <router-link :to="{path:'issueDetails',query: {id: item.id}}">
                <div class="indexConListImgBox">
                  <img :src="item.images" alt="" class="img-my">
                </div>
                <div class="indexConListFontBox">
                  <p class="indexConListFontP1">
                    {{item.title}}
                  </p>
                  <p class="indexConListFontP2">
                    ￥<span>{{item.price}}</span>
                  </p>
                </div>
              </router-link>
            </li>
          </ul>
          <div slot="bottom" class="mint-loadmore-bottom">
            <img src="../../assets/img/loding.gif" alt="">
          </div>
        </mt-loadmore>
      </div>

    </div>
    <div class="indexConBtn">
      <router-link :to="{path:'releaseType'}">
        <img src="@/assets/img/indexConBtn.png" alt="" class="img-my">
      </router-link>
    </div>
  </div>
</template>

<script>
  import Vue from 'vue'
  //  import { Loadmore } from 'mint-ui';
  //  Vue.component(Loadmore.name, Loadmore);
  export default {
    data() {
      return {
        indexConvenientData:{},//接收banner和分类
        indexConvenientFabu:{
          records:[]
        },//发布方
        indexConvenientxuqiu:{
          records:[]
        },//需求方
        moreXuqiu:[],
        allLoaded:true,//上拉加载标记
      }
    },
    mounted () {
      this.indexConvenientInit()
    },
    methods: {
      /**
       * 页面初始化函数
       */
      indexConvenientInit(){
        this.$http.post('/lifeClass/getLifeInfo')
          .then(res => {
            if (res.status === 200) {
              console.log('res.data', res.data);
              if(res.data.code===200){
                this.indexConvenientData=res.data.data;
                let idObj={
                  classId:this.indexConvenientData.classLife[0].id
                };
                this.indConListFun(idObj)
              }
            }
          })
      },
      /**
       * 分类点击事件
       * @param item
       * @param index
       */
      indexConvenListFun(item,index){
//        this.$nextTick(function() {
//          this.indexConvenientData.classLife.forEach(function(item) {
//            Vue.set(item,'outClassFlag',false)
//          });
//          Vue.set(item,'outClassFlag',true)
//        });
        let idObj={
          classId:item.id
        };
        this.indConListFun(idObj)
      },
      /**
       *分类查询数据
       */
      indConListFun(obj){
        this.$http.post('/lifeClass/getActivityByClassId',this.$qs.stringify(obj))
          .then(res => {
            if (res.status === 200) {
              if(res.data.code===200){
                console.log('res.data', res.data);
//                this.indexConvenientFabu=res.data.data.provide;
                let moreZArr=res.data.data.provide;
                for(let i=0;i<moreZArr.length;i++){
                  if(moreZArr[i].images.indexOf(",") !== -1 ){
                    let imgArr=moreZArr[i].images.split(',');
                    moreZArr[i].imgStr=imgArr[0]
                  }else {
                    moreZArr[i].imgStr=moreZArr[i].images
                  }
                }
                this.indexConvenientFabu=moreZArr

                let demandeArr=res.data.data.demand;
                for(let i=0;i<demandeArr.length;i++){
                  if(demandeArr[i].images.indexOf(",") !== -1 ){
                    let provideArr=demandeArr[i].images.split(',');
                    demandeArr[i].imgStr=provideArr[0]
                  }else {
                    demandeArr[i].imgStr=demandeArr[i].images
                  }
                }
//                this.indexConvenientxuqiu=provideArr
                this.indexConvenientxuqiu=demandeArr;
              }
            }
          })
      },
      /**
       * 返回上一个页面
       */
      fanhuiFun(){
        this.$router.go(-1)
      },
      /**
       * 上拉
       */
      loadBottom() {
        this.allLoaded=true
        this.$refs.loadmore.onBottomLoaded();
      },
    },
    computed: {}
  }
</script>
<style lang="scss">
  .indexConBox {
    background-color: #f7f7f7;
    padding-top: 0.9rem;
    .indexConHeader {
      position: fixed;
      left: 0;
      right: 0;
      top: 0;
      z-index: 5;
      background-color: #de3031;
      height: 0.9rem;
      color: #fff;
      font-size: 0.34rem;
      text-align: center;
      line-height: 0.9rem;
      i{
        font-size: 0.4rem;
      }
      .indexbackOff {
        position: absolute;
        left: 0;
        top: 0;
        bottom: 0;
        line-height: 0.9rem;
        padding: 0 0.2rem;
        color: #fff;
      }
      .indexNews {
        position: absolute;
        right: 0;
        top: 0;
        bottom: 0;
        line-height: 0.9rem;
        padding: 0 0.2rem;
        color: #fff;
        i{
          font-size: 0.4rem;
        }
      }
    }
    .indexBanner {
      height: 3rem;
      margin-bottom: 0.2rem;
      .mint-swipe-indicator {
        background-color: #c0d2f7;
        opacity: 1;
      }
      .mint-swipe-indicator.is-active {
        background: #ff6f20;
        width: 10px;
        height: 10px;
      }
      .indexBox .indexBanner .mint-swipe-indicator.is-active{
        margin-top: -1px;
      }
      .mint-swipe-indicators{
        bottom: -0.2rem;
      }
      img{
        width: 100%;
      }
    }
    .indexConListBox {
      background-color: #fff;
      .indexConListTit {
        overflow-x: scroll; /* 1 */
        overflow-y: hidden;
        padding: 0.3rem 0.28rem;
        border-bottom: 1px solid #d8d8d8;
        .indexConListTitDiv {
          display: flex;
          text-align: center;
        }
        li {
          max-width: 1.7rem;
          min-width: 1.7rem;
          margin-right: 0.2rem;
          text-align: center;
          line-height: 0.5rem;
          border-radius: 0.5rem;
          border: 1px solid #d8d8d8;
          flex: 1;
          box-sizing: border-box;
          font-size: 0.26rem;
          display: block;
          color: #333;
        }
      }
      .indexConListTit::-webkit-scrollbar { display: none; }
      //隐藏滚动条！
      .indexConListHcart {
        li {
          border-bottom: 1px solid #d8d8d8;
          padding: 0.3rem 0.28rem;
          .indexConListImgBox {
            width: 2rem;
            height: 1.6rem;
            border-radius: 6px;
            float: left;
            display: -webkit-flex;
            display: flex;
            img {
              margin: auto;
            }
          }
          .indexConListFontBox {
            float: left;
            width: 4.5rem;
            margin-left: 0.3rem;
            .indexConListFontP1 {
              font-size: 0.3rem;
              color: #333;
              height: 1.2rem;
              overflow: hidden;
              text-overflow: ellipsis;
              display: -webkit-box;
              -webkit-box-orient: vertical;
              -webkit-line-clamp: 3;
            }
            .indexConListFontP2 {
              color: #ff3f3f;
              font-size: 0.22rem;
              span {
                font-size: 0.28rem;
              }
            }
          }
        }
        /*li:last-child{*/
        /*border-bottom: none;*/
        /*}*/
      }
    }
    .indexConHr {
      a{
        display: block;
        width: 100%;
        height: 100%;
      }
      text-align: center;
      font-size: 0.24rem;
      color: #949494;
      line-height: 0.96rem;
      border-bottom: 1px solid #d8d8d8;
      i{
        font-size: 0.24rem;
      }
    }
    .indexConBtn{
      position: fixed;
      left: 50%;
      bottom: 0.3rem;
      width: 1.1rem;
      height: 1.1rem;
      margin: 0 -0.55rem;
    }
  }
</style>



