<template>
  <div class="pageBox indexConBox">
    <div class="indexConHeader" ref="header">
      <a class="indexbackOff" @click="fanhuiFun()">
        <i class="icon iconfont icon-guanbi1-copy"></i>
      </a>
      便捷生活
      <router-link to="myRelease" class="indexNews">
        <i class="icon iconfont icon-cebian-wodedingdan"></i>
      </router-link>
    </div>
    <!--banner start-->
    <div class="indexConListBox" >
      <div class="indexConListTit" ref="moreTit">
        <ul class="indexConListTitDiv">
          <li v-for=" (item,$index) in indexConvenientData.classLife" @click="indexConvenListFun(item,$index)">
            {{item.className}}
          </li>
        </ul>
      </div>
      <div class="moreListBox" ref="moreListBox">
        <mt-loadmore :bottom-method="loadBottom" :bottom-all-loaded="allLoaded" ref="loadmore"  :auto-fill="false">
          <ul class="indexConListHcart">
            <li class="after" v-for="item in indexConvenientArr">
              <router-link :to="{path:'issueDetails',query: {id: item.id,type:item.type}}">
                <div class="indexConListImgBox">
                  <img :src="item.imgStr" alt="" class="img-my">
                </div>
                <div class="indexConListFontBox">
                  <p class="indexConListFontP1">
                    {{item.title}}
                  </p>
                  <p class="indexConListFontP2">
                    ￥<span>{{item.price}}</span>
                  </p>
                </div>
              </router-link>
            </li>
          </ul>
          <div slot="bottom" class="mint-loadmore-bottom">
            <img src="../../../assets/img/loding.gif" alt="">
          </div>
        </mt-loadmore>
      </div>
    </div>
    <div class="indexConBtn">
      <router-link :to="{path:'releaseType'}">
        <img src="@/assets/img/indexConBtn.png" alt="" class="img-my">
      </router-link>
    </div>
  </div>
</template>

<script>
  import Vue from 'vue'
  export default {
    name:'moreList',
    data() {
      return {
        indexConvenientData:{},//接收banner和分类
        indexConvenientList:{},
        indexConvenientArr:[],//循环列表
        classId:'',
        allLoaded:false,//上拉加载标记

      }
    },
    mounted () {
      this.indexConvenientInit()
    },
    methods: {
      /**
       * 页面初始化函数
       */
      indexConvenientInit(){
        this.$http.post('/lifeClass/getLifeInfo')
          .then(res => {
            if (res.status === 200) {
              console.log('res.data', res.data);
              if(res.data.code===200){
                this.indexConvenientData=res.data.data;
                let idObj={
                  current:1,
                  classId:this.indexConvenientData.classLife[0].id,
                  size:10
                };
                this.indConListFun(idObj)
                this.classId=this.indexConvenientData.classLife[0].id
              }
            }
          })
      },
      /**
       * 分类点击事件
       * @param item
       * @param index
       */
      indexConvenListFun(item,index){
//        this.$nextTick(function() {
//          this.indexConvenientData.classLife.forEach(function(item) {
//            Vue.set(item,'outClassFlag',false)
//          });
//          Vue.set(item,'outClassFlag',true)
//        });
        this.allLoaded = false;
        this.indexConvenientArr=[];
        let idObj={
          classId:item.id,
          current:1,
          size:10
        };
        this.indConListFun(idObj);
        this.classId=item.id
      },

      indConListFun(obj){
        this.$http.post('/lifeClass/getActivityListByClassId',this.$qs.stringify(obj))
          .then(res => {
            if (res.status === 200) {
              if(res.data.code===200){
                console.log(res.data);
                this.indexConvenientList=res.data.data;
                let moreZArr=res.data.data.records;
                for(let i=0;i<moreZArr.length;i++){
                  if(moreZArr[i].images.indexOf(",") !== -1 ){
                    let imgArr=moreZArr[i].images.split(',');
                    moreZArr[i].imgStr=imgArr[0]
                  }else {
                    moreZArr[i].imgStr=moreZArr[i].images
                  }
                }
                if(res.data.data.records.length>0){
                  this.indexConvenientArr=this.indexConvenientArr.concat(moreZArr);
                  if(res.data.data.pages<=1){
                    this.allLoaded = true;
                  }else {
                    this.allLoaded = false;
                  }



                  let heightMx=document.documentElement.clientHeight;
                  let heightSearhed= this.$refs.header.offsetHeight;
                  let heightTotBox= this.$refs.moreTit.offsetHeight;
                  heightMx=heightMx-heightSearhed-heightTotBox
                  this.$refs.moreListBox.style.height =heightMx+'px';
                }else {
                  this.allLoaded=true
                }

              }
            }
          })
      },
      /**
       * 返回上一个页面
       */
      fanhuiFun(){
        this.$router.go(-1)
      },
      /**
       * 上拉
       */
      loadBottom() {
        console.log(123);
//        this.allLoaded=true
        let pages=Number(this.indexConvenientList.current)+1;
        let timeObj={
          classId:this.classId,
          current:pages,
          size:10
        };
        this.indConListFun(timeObj)
        this.$refs.loadmore.onBottomLoaded();
      },
    },
    computed: {}
  }
</script>
<style lang="scss">
  .indexConBox {
    background-color: #f7f7f7;
    padding-top: 0.9rem;
    .indexConHeader {
      position: fixed;
      left: 0;
      right: 0;
      top: 0;
      z-index: 5;
      background-color: #de3031;
      height: 0.9rem;
      color: #fff;
      font-size: 0.34rem;
      text-align: center;
      line-height: 0.9rem;
      i{
        font-size: 0.4rem;
      }
      .indexbackOff {
        position: absolute;
        left: 0;
        top: 0;
        bottom: 0;
        line-height: 0.9rem;
        padding: 0 0.2rem;
        color: #fff;
      }
      .indexNews {
        position: absolute;
        right: 0;
        top: 0;
        bottom: 0;
        line-height: 0.9rem;
        padding: 0 0.2rem;
        color: #fff;
        i{
          font-size: 0.4rem;
        }
      }
    }
    .indexConListBox {
      background-color: #fff;
      overflow-y: scroll;
      .indexConListTit {
        overflow-x: scroll; /* 1 */
        overflow-y: hidden;
        padding: 0.3rem 0.28rem;
        border-bottom: 1px solid #d8d8d8;
        .indexConListTitDiv {
          display: flex;
          text-align: center;
        }
        li {
          max-width: 1.7rem;
          min-width: 1.7rem;
          margin-right: 0.2rem;
          text-align: center;
          line-height: 0.5rem;
          border-radius: 0.5rem;
          border: 1px solid #d8d8d8;
          flex: 1;
          box-sizing: border-box;
          font-size: 0.26rem;
          display: block;
          color: #333;
        }
      }
      .indexConListTit::-webkit-scrollbar { display: none; }
      //隐藏滚动条！
      .indexConListHcart {
        li {
          border-bottom: 1px solid #d8d8d8;
          padding: 0.3rem 0.28rem;
          .indexConListImgBox {
            width: 2rem;
            height: 1.6rem;
            border-radius: 6px;
            float: left;
            display: -webkit-flex;
            display: flex;
            img {
              margin: auto;
            }
          }
          .indexConListFontBox {
            float: left;
            width: 4.5rem;
            margin-left: 0.3rem;
            .indexConListFontP1 {
              font-size: 0.3rem;
              color: #333;
              height: 1.2rem;
              overflow: hidden;
              text-overflow: ellipsis;
              display: -webkit-box;
              -webkit-box-orient: vertical;
              -webkit-line-clamp: 3;
            }
            .indexConListFontP2 {
              color: #ff3f3f;
              font-size: 0.22rem;
              span {
                font-size: 0.28rem;
              }
            }
          }
        }
        /*li:last-child{*/
        /*border-bottom: none;*/
        /*}*/
      }
    }
    .indexConHr {
      text-align: center;
      font-size: 0.24rem;
      color: #949494;
      line-height: 0.96rem;
      border-bottom: 1px solid #d8d8d8;
      i{
        font-size: 0.24rem;
      }
    }
    .indexConBtn{
      position: fixed;
      left: 50%;
      bottom: 0.3rem;
      width: 1.1rem;
      height: 1.1rem;
      margin: 0 -0.55rem;
    }
  }
</style>



