<template>
  <div class="pageBox moreShopBox">
    <!--header start-->
    <div class="indexHeader">
      <a class="indexbackOff" @click="fanhuiFun()">
        <i class="icon iconfont icon-guanbi1-copy"></i>
      </a>
      <div class="indexHeaderSerch">
        <form action="javascript:return true;">
          <input type="search" placeholder="请输入关键字" @keypress="searchFun">
          <i class="icon iconfont icon-sousuo"></i>
        </form>
      </div>
    </div>
    <div class="moreShopCont">
      <mt-loadmore :bottom-method="loadBottom" :bottom-all-loaded="allLoaded" ref="loadmore"  :auto-fill="false">
        <ul>
          <li v-for="item in moreShopArr">
            <router-link :to="{path:'shopDetails',query: {id: item.id}}">
              <div class="imgBox">
                <img :src="item.headImg" alt="" class="img-my">
              </div>
              <div class="fontBox">
                <p class="p1 ">
                  {{item.shopName}}

                </p>
                <p class="p2 name">
                  {{item.announcement}}
                </p>
              </div>
            </router-link>
          </li>
        </ul>
        <div slot="bottom" class="mint-loadmore-bottom">
          <img src="../../assets/img/loding.gif" alt="">
        </div>

      </mt-loadmore>

    </div>
    <!--header end-->
  </div>
</template>

<script>
  import Vue from 'vue';
//  import { Loadmore } from 'mint-ui';
//  Vue.component(Loadmore.name, Loadmore);
    export default {
      name:'moreShop',
        data() {
            return {
              allLoaded:false,//上拉加载标记
              moreShopData:{
                records:[]
              },
              moreShopArr:[]
            }
        },
      mounted () {
        let id;
        if(this.$route.query.id){
           id=this.$route.query.id
          console.log('1',id)
        }else {
           id=0;
          console.log('2',id)
        }
        let timeObj={
          modularId:id,
          current:1,
          size:"10",
        };
        this.moreShopInit(timeObj)
      },
        methods: {
          /**
           * 页面初始化函数
           */
          moreShopInit(obj){

            this.$http.post('/modularPortal/shopListByModularId',this.$qs.stringify(obj))
              .then(res => {
                if (res.status === 200) {
                  console.log('res.data', res.data);
                  if(res.data.code===200){
                    this.moreShopData=res.data.data
                    if(res.data.data!=null){
                      this.moreShopArr=this.moreShopArr.concat(res.data.data.records)
                      if(res.data.data.pages<=1){
                        this.allLoaded = true;
                      }else {
                        this.allLoaded = false;
                      }
                    }else {
                      this.allLoaded = true;
                    }
                  }

                }
              })
          },
          /**
           * 返回上一个页面
           */
          fanhuiFun(){
            this.$router.go(-1)
          },
          /**
           * 搜索框
           */
          searchFun(){
            var keycode = event.keyCode;
            if(keycode=='13') {
              alert(123)
              this.moreShopArr=[];
              event.preventDefault();
            }

          },
          /**
           * 上拉
           */
          loadBottom() {
            console.log(123)
            this.allLoaded=true
            let id;
            if(this.$route.query.id){
              id=this.$route.query.id
              console.log('1',id)
            }else {
              id=0;
              console.log('2',id)
            }
            let pages=Number(this.moreShopData.current)+1;
            let timeObj={
              modularId:id,
              current:pages,
              size:"10",
            };
            this.moreShopInit(timeObj)
            this.$refs.loadmore.onBottomLoaded();
          },
        },
        computed: {}
    }
</script>

<style lang="scss">
.moreShopBox{
  background-color: #f7f7f7;
  padding-top: 0.9rem;
  /*padding-bottom: 0.96rem;*/
  /*header start*/
  .indexHeader{
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    z-index: 5;
    background-color: #de3031;
    height: 0.9rem;
    .indexbackOff{
      line-height: 0.9rem;
      font-size: 0.34rem;
      color: #ffffff;
      float: left;
      width: 0.8rem;
      text-align: center;
    }
    .indexHeaderSerch{
      float: left;
      width:6.3rem;
      height: 0.6rem;
      line-height: 0.9rem;
      margin: 0.15rem 0;
      position: relative;
      input{
        border-radius: 0.5rem;
        width: 100%;
        display: block;
        padding: 0.16rem 0.3rem;
        font-size: 0.24rem;
        color: #bfbfbf;
      }
      i{
        position: absolute;
        right: 0.3rem;
        top: 0;
        line-height: 0.6rem;
        font-size: 0.3rem;
        color: #bfbfbf;
        background-color: #fff;
      }
    }
    .indexHeaderNews{
      float: right;
      color: #ffffff;
      padding: 0 0.2rem;
      i{
        font-size: 0.4rem;
        display: block;
        margin: 0.23rem 0;
      }
    }
  }
  ul{
    background-color: #ffffff;
    li{
      padding: 0.3rem 0.2rem 0.3rem 0.3rem;
      border-bottom: 1px solid #f0f0f0;
      a{
        display: flex;
        justify-content:flex-start;
      }
      .imgBox{
        width: 1.6rem;
        height: 1.6rem;
        border-radius: 6px;
        display: -webkit-flex;
        display: flex;
        margin-right: 0.4rem;
        img{
          margin: auto;
        }
      }
      .fontCont{
        display: flex;
        justify-content:space-between;
      }
      .p1{
        font-size: 0.3rem;
        color: #333333;
      }
      .p2{
        font-size: 0.26rem;
        color: #939393;
        margin-top: 0.2rem;
      }
      /*.p3{*/
        /*font-size: 0.3rem;*/
        /*color: #ff3f3f;*/
      /*}*/
      /*.p4{*/
        /*font-size:  0.24rem;*/
        /*color: #939393;*/
      /*}*/
      .name{
        display: -webkit-box;
        -webkit-box-orient: vertical;
        -webkit-line-clamp:2;
        overflow: hidden;
        max-width:4.8rem;
        /*height: 0.8rem;*/
      }
    }
  }
}
</style>



