<template>
  <div class="pageBox issueDetailsBox">
    <div class="indexConHeader">
      <a class="indexbackOff" @click="fanhuiFun()">
        <i class="icon iconfont icon-guanbi1-copy"></i>
      </a>
      详情
      <router-link to="index" class="indexNews">
        <i class="icon iconfont icon-cebian-wodedingdan"></i>
      </router-link>
    </div>
    <div class="issueDetailsUserCimmit">
      <div class="after issueDetailsUserBox">
        <div class="issueDetailsUserImgBox">
          <img :src="issueDetData.user.headImg" alt="" class="img-my">
        </div>
        <div class="issueDetailsUserNameBox">
          <p class="issueDetailsUserName">
            {{issueDetData.user.nickName}}
          </p>
          <p class="issueDetailsUserData">
            {{issueDetData.publishTime}}
          </p>
        </div>
        <div class="issueDetailsUserCity" v-if="issueDetData.activityTransactionArea!=null">
          <i class="icon iconfont icon-dizhi"></i>
          <span>{{issueDetData.activityTransactionArea.transactionArea}}</span>
        </div>
      </div>
      <div class="after issueDetailsRess">
        <p>
          <span class="left">联系人</span> <span class="gray">{{issueDetData.name}}</span>
        </p>
        <p>
          <span class="left rig">电话</span> <span  class="gray">{{issueDetData.phone}}</span>
        </p>
        <p>
          <span class="left">价格</span> <span  class="red">{{issueDetData.price}}</span>
        </p>
        <p v-if="issueDetData.carName!=null">
          <span class="left rig">车辆</span>  <span  class="gray">{{issueDetData.carName}}</span>
        </p>
        <p v-if="issueDetData.startTime!=null || issueDetData.endTime !=null">
          <span class="left">时间</span> <span  class="red">{{issueDetData.startTime}}----{{issueDetData.endTime}}</span>
        </p>

      </div>
      <div class="issueDetailsTufont">
        <p>
          {{issueDetData.content}}
        </p>
        <div class="issueDetailsTufont">
          <img v-for="item in imgArr" :src="item" alt="" class="img-my" >
        </div>
      </div>
    </div>
    <div class="issueDetailsleave">
      <P class="issueDetailsleaveTit">
        留言
      </P>
      <ul>
        <li v-for="item in getCommentList">
          <div class="after issueDetailsleaveUserBox">
            <div class="issueDetailsleaveUserImg">
              <img :src="item.user.headImg" alt="" class="img-my">
            </div>
            <div class="issueDetailsleaveUserFont">
              <p class="name">
                <!--{{item}}-->
                {{item.user.nickName}}
              </p>
              <p class="data">
                {{item.publishTime}}
              </p>
            </div>
            <p class="leaveFont">
              {{item.comment}}
            </p>
          </div>
        </li>
      </ul>
    </div>
    <!--留言板-->
    <div class="after leaveInputBox">
      <form action="javascript:return true;">
        <input type="search" placeholder="写留言..." @keypress="searchFun" @blur="liuyanFun" v-model="name">
      </form>
      <p>
        <i class="icon iconfont icon-message"></i>
        88
      </p>
    </div>
  </div>

</template>

<script>
  import { Toast } from 'mint-ui';
  export default {
    data () {
      return {
        issId:'',//url上面带的id
        issueDetData:{//活动信息
          user:{},
          activityTransactionArea:{
            transactionArea:' '
          }
        },
        name:'',
        imgArr:[],//图片数组
        getCommentList:[],//留言数组
        userFormData:{
          id:"",
        }
      }
    },
    mounted () {
      this.issId=this.$route.query.id;
      this.isssDetInit();
      this.liuyanListFun();
    },
    methods: {
      isssDetInit(){
        //请求活动信息
        let objId={
          id:this.issId
        };
        this.$http.post('/activity/toMarketInfo',this.$qs.stringify(objId))
          .then(res => {
            if (res.status === 200) {
              if(res.data.code===200){
                console.log(res.data);
                this.issueDetData=res.data.data;
                this.imgArr=res.data.data.images.split(",")
              }
            }
          });


      },
      //请求活动留言
      liuyanListFun(){
        let objclassId={
          activityId:this.issId
        };
        this.$http.post('/activity/getMarketCommentById',this.$qs.stringify(objclassId))
          .then(res => {
            if (res.status === 200) {
              if(res.data.code===200){
                console.log(res.data);
                this.getCommentList=res.data.data.records
              }
            }
          })
      },
      /**
       * 返回上一个页面
       */
      fanhuiFun(){
        this.$router.go(-1)
      },
      /**
       * 输入框
       */
      searchFun(){
        var keycode = event.keyCode;
        if(keycode=='13') {
          alert(123)
          event.preventDefault();
        }
      },
      /**
       * 发布留言
       */
      liuyanFun(){
        let objId={
          comment:this.name,
          activityId:this.issId
        };
        this.ajaxLiuyanFun(objId)
      },
      /**
       *
       */
      ajaxLiuyanFun(objId){
        this.$http.post('/activity/addComment',this.$qs.stringify(objId))
          .then(res => {
            if (res.status === 200) {
              if(res.data.code===200){

                Toast({
                  message:'操作成功',
                  duration: 1000
                });
                this.liuyanListFun();
              }
            }
          })
      }
    },
    computed: {}
  }
</script>
<style lang="scss">
  .issueDetailsBox {
    background-color: #f7f7f7;
    padding-top: 0.9rem;
    padding-bottom: 0.98rem;
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;

    .indexConHeader {
      position: fixed;
      left: 0;
      right: 0;
      top: 0;
      z-index: 5;
      background-color: #de3031;
      height: 0.9rem;
      color: #fff;
      font-size: 0.34rem;
      text-align: center;
      line-height: 0.9rem;
      i{
        font-size: 0.4rem;
      }
      .indexbackOff {
        position: absolute;
        left: 0;
        top: 0;
        bottom: 0;
        line-height: 0.9rem;
        padding: 0 0.2rem;
        color: #fff;
      }
      .indexNews {
        position: absolute;
        right: 0;
        top: 0;
        bottom: 0;
        line-height: 0.9rem;
        padding: 0 0.2rem;
        color: #fff;
      }
    }
    .issueDetailsUserCimmit{
      padding: 0.3rem 0 0.3rem 0.3rem;
      background-color: #ffffff;
      margin-bottom: 0.2rem;
      .issueDetailsUserBox{
        border-bottom: 1px solid #f0f0f0;
        padding: 0.2rem;
        .issueDetailsUserImgBox{
          float: left;
          width: 0.8rem;
          height: 0.8rem;
          border-radius: 50%;
          img{
            width: 0.8rem;
            height: 0.8rem;
            border-radius: 50%;
          }
        }
        .issueDetailsUserNameBox{
          float: left;
          margin-left: 0.2rem;
          padding-top: 0.1rem;
          .issueDetailsUserName{
            font-size: 0.3rem;
            color: #3b3b3b;
          }
          .issueDetailsUserData{
            font-size: 0.22rem;
            color: #939393;
          }
        }
        .issueDetailsUserCity{
          float: right;
          margin-right: 0.3rem;
          font-size: 0.26rem;
          color: #272727;
          i{
            font-size: 0.3rem;
            color: #ff661a;
          }
        }
      }
      .issueDetailsRess{
        padding: 0.3rem 0.3rem 0.3rem 0;
        border-bottom: 1px solid #f0f0f0;
        p{
          width: 50%;
          float: left;
          margin-bottom: 0.2rem;
          font-size: 0.28rem;
          line-height: 0.28rem;
          padding: 0.1rem 0;
          color: #949494;
          .gray{
            color: #3b3b3b;
          }
          .red{
            color: #ff3f3f;
          }
          span{
            float: left;
            display: block;
          }
          .left{
            width: 1rem;
          }
          .rig{
            width: 0.9rem;
            padding-left: 0.1rem;
          }
        }
        p:last-child{
          width: 100%;
          margin-bottom: 0;
        }
      }
      .issueDetailsTufont{
        padding: 0.3rem 0.3rem 0 0;
        p{
          line-height: 0.5rem;
          font-size: 0.32rem;
          color: #3b3b3b;
          margin-bottom: 0.2rem;
        }
        .issueDetailsTufont{
          img{
            margin-bottom: 0.2rem;
          }
        }
      }
    }
    .issueDetailsleave{
      padding-left: 0.3rem;
      background-color: #ffffff;
      .issueDetailsleaveTit{
        font-size: 0.3rem;
        color: #3b3b3b;
        line-height: 0.8rem;
        border-bottom: 1px solid #f0f0f0;
      }
      ul{
        li{
          padding: 0.3rem 0.3rem 0.3rem 0;
          border-bottom: 1px solid #f0f0f0;
          .issueDetailsleaveUserBox{
            width: 100%;
            height: 100%;
            .issueDetailsleaveUserImg{
              width: 0.6rem;
              height: 0.6rem;
              border-radius: 50%;
              overflow: hidden;
              float: left;
              margin-right: 0.2rem;
              img{
                width: 0.6rem;
                height: 0.6rem;
                border-radius: 50%;
              }
            }
            .issueDetailsleaveUserFont{
              float: left;
              .name{
                color: #3b3b3b;
                font-size: 0.3rem;
              }
              .data{
                color: #939393;
                font-size: 0.22rem;
              }
            }
            .leaveFont{
              margin-top: 0.1rem;
              width: 100%;
              line-height: 0.44rem;
              clear: both;
              font-size: 0.26rem;
              color: #3b3b3b;
            }
          }
        }
        li:last-child{
          border-bottom: none;
        }

      }
    }
    .leaveInputBox{
      position: fixed;
      left: 0;
      right: 0;
      bottom: 0;
      line-height: 0.98rem;
      border-top: 1px solid #f0f0f0;
      background-color: #ffffff;
      form{
        display: block;
        float: left;
        width: 5.6rem;
        box-sizing: border-box;
      }
      input{
        width: 100%;
        height: 100%;
        border-radius: 0.5rem;
        background-color: #f4f4f4;

        display: block;
        padding: 0.18rem 0.4rem 0.18rem 0.4rem;
        margin: 0.16rem 0 0.16rem 0.27rem;

      }
      p{
        font-size: 0.24rem;
        color: #3b3b3b;
        width: 1.5rem;
        text-align: center;
        float: left;
        i{
          font-size: 0.36rem;
        }
      }
    }
  }
</style>



