<template>
  <div class="pageBox moreSpecialBox">
    <!--header start-->
    <div class="searchHeader" ref="header">
      <a class="indexbackOff" @click="fanhuiFun()">
        <i class="icon iconfont icon-guanbi1-copy"></i>
      </a>
      <p>精选推荐</p>
      <a class="searchHeaderNews" @touchend="lineOrcolFun">

        <i class="icon iconfont icon-hengxiangliebiao" v-if="storageData.lineORcolFlag"></i>
        <i class="icon iconfont icon-shuxiangliebiao" v-else="storageData.lineORcolFlag==false"></i>
      </a>
    </div>
    <div class="searchList moreSpeBox" ref="MoreSpecialBox">
      <mt-loadmore :bottom-method="loadBottom" :bottom-all-loaded="allLoaded" ref="loadmore"  :auto-fill="false">
        <ul :class="storageData.lineORcolFlag ? 'searchLine':'searchColumn'" class=" after">
          <li v-for="item in moreSpecialArr"  >
            <router-link :to="{path:'commodityDetails',query: {id: item.shopGoodsId}}">
              <div class="searchLineImgBox">
                <img :src="item.mainImage" alt="" class="img-my">
              </div>
              <div class="searchLineFontBox">
                <p class="searchLineFont">
                  {{item.goodsName}}
                </p>
                <p class="serOriginalPrice" v-show="storageData.lineORcolFlag===false">
                  原价：<s>{{item.originalPrice}}</s>
                </p>
                <div class="after">
                  <p class="left">
                    ￥<span>{{item.salesPrice}}</span>
                  </p>
                  <p class="right">
                   {{item.saleAmont}}人付款
                  </p>
                </div>
              </div>
            </router-link>
          </li>
          <!--<li v-for="item in moreSpecialList.records" v-if="morespEcilFlag===false">-->
            <!--<router-link :to="{path:'commodityDetails',query: {id: item.shopGoodsId}}">-->
              <!--<div class="searchLineImgBox">-->
                <!--<img :src="item.mainImage" alt="" class="img-my">-->
              <!--</div>-->
              <!--<div class="searchLineFontBox">-->
                <!--<p class="searchLineFont">-->
                  <!--{{item.goodsName}}-->
                <!--</p>-->
                <!--<p class="serOriginalPrice" v-show="storageData.lineORcolFlag===false">-->
                  <!--原价：<s>{{item.originalPrice}}</s>-->
                <!--</p>-->
                <!--<div class="after">-->
                  <!--<p class="left">-->
                    <!--￥<span>{{item.salesPrice}}</span>-->
                  <!--</p>-->
                  <!--<p class="right">-->
                    <!--{{item.saleAmont}}人付款-->
                  <!--</p>-->
                <!--</div>-->
              <!--</div>-->
            <!--</router-link>-->
          <!--</li>-->
        </ul>
        <div slot="bottom" class="mint-loadmore-bottom">
          <img src="../../assets/img/loding.gif" alt="">
        </div>

      </mt-loadmore>
    </div>
  </div>
</template>

<script>
    export default {
      data () {
        return {
          allLoaded:true,//上拉加载标记
          ajaxUrl:'',
          moreSpecialList:'',//储存从首页过来的更多的接口
          morespEcilFlag:false,
          moreSpecialData:{//储存从2级首页过来的更多的接口
            records:[
              {
                shopGoodsSize:[
                  {
                    originalPrice:'',
                    salesPrice:'',
                  }
                ]
              }
            ]
          },
          moreSpecialArr:[],
          storageData:{//用来存储一些标记
            lineORcolFlag:true, //为true===竖 false===行
            togetherPopupFlag:false, //控制综合弹窗标记
            screenPopupFlag:false, //控制筛选弹窗标记
          }
        }
      },
      mounted () {
        let id;
        let timeObj;
        if(this.$route.query.id){
          /**
           * 这个接口是二级首页跳转过来查询的
           */
          id=this.$route.query.id
        timeObj={
            modularId:id,
            current:1,
            size:"10",
          };
          this.ajaxUrl='/modularPortal/shopGoodsListByModularId'
          this.morespEcilFlag=true
        }else {
          /**
           * 这个接口是首页跳转过来查询的
           */
          timeObj={
            current:1,
            size:"10",
          };
          this.ajaxUrl='/activity/getIsRecommend'
          this.morespEcilFlag=false
        }
        this.moreSpecialInit(timeObj,this.ajaxUrl)
        let heightMx=document.documentElement.clientHeight;
        let heightSearhed= this.$refs.header.offsetHeight;
        heightMx=heightMx-heightSearhed
        this.$refs.MoreSpecialBox.style.height =heightMx+'px';
      },
      methods: {
        /**
         * 页面初始化函数
         */
        moreSpecialInit(obj,url){
          this.$http.post(url,this.$qs.stringify(obj))
            .then(res => {
              if (res.status === 200) {
                console.log('res.data', res.data);
                if(res.data.code===200){
                  this.moreSpecialData=res.data.data
                  if(res.data.data.records!==null){
                    this.moreSpecialArr =this.moreSpecialArr.concat(res.data.data.records)
                    console.log( this.moreSpecialArr)
                    if(res.data.data.pages<=1){
                      this.allLoaded = true;
                    }else {
                      console.log(132)
                      this.allLoaded = false;
                    }
                  }else {
                    this.allLoaded = true;
                  }
                }

              }
            })
        },
        /**
         * 控制列表横向或者纵向
         */
        lineOrcolFun(){
          this.storageData.lineORcolFlag = !this.storageData.lineORcolFlag
        },
        /**
         * 上拉
         */
        loadBottom() {
//          console.log(123)
//          this.allLoaded=true
          let id;
          let timeObj;
          let pages=Number(this.moreSpecialData.current)+1;
          if(this.morespEcilFlag){
            /**
             * 这个接口是二级首页跳转过来查询的
             */
            id=this.$route.query.id;
             timeObj={
              modularId:id,
              current:pages,
              size:"10",
            };
            this.ajaxUrl='/modularPortal/shopGoodsListByModularId'
          }else {
            /**
             * 这个接口是首页跳转过来查询的
             */
             timeObj={
              current:pages,
              size:"10",
            };
            this.ajaxUrl='/activity/getIsRecommend'
          }
          this.moreSpecialInit(timeObj,this.ajaxUrl)
          this.$refs.loadmore.onBottomLoaded();
        },
        /**
         * 返回上一个页面
         */
        fanhuiFun(){
          this.$router.go(-1)
        },
      },
        computed: {}
    }
</script>

<style lang="scss">
.moreSpecialBox{
  background-color: #f7f7f7;
  padding-top: 0.9rem;
  /*header start*/
  .searchHeader{
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    z-index: 5;
    background-color: #de3031;
    height: 0.9rem;
    text-align: center;
    p{
      line-height: 0.9rem;
      font-size: 0.30rem;
      color: #ffffff;
      width: 5.9rem;
      float: left;
    }
    .searchbackOff{
      line-height: 0.9rem;
      font-size: 0.34rem;
      color: #ffffff;
      float: left;
      width: 0.8rem;
      text-align: center;
    }
    .indexbackOff{
      line-height: 0.9rem;
      font-size: 0.34rem;
      color: #ffffff;
      float: left;
      width: 0.8rem;
      text-align: center;
    }
    .searchHeaderNews{
      float: right;
      color: #ffffff;
      padding: 0 0.2rem;
      i{
        font-size: 0.4rem;
        display: block;
        margin: 0.23rem 0;
      }
    }
  }
  .searchClassTotBox{
    position: fixed;
    left: 0;
    right: 0;
    top: 0.9rem;
    height: 0.9rem;
    line-height: 0.9rem;
    display: -webkit-flex;
    display: flex;
    background-color: #ffffff;
    p{
      /*flex:1;*/
      margin: auto;
      font-size: 0.3rem;
      text-align: center;
      color: #3d3d3d;
      padding: 0 0.3rem;
      .icon-paixugaodi{
        font-size: 0.25rem;
      }
      .icon-shaixuan{
        font-size: 0.28rem;
      }

    }
    .is-active{
      color: #de3031;
    }
  }

  .searchList{
    ul:after{
      display: block;
      content: "";
      clear: both;
    }
    /*横向样式*/
    .searchLine{
      padding: 0.1rem 0.05rem;
      li{
        float: left;
        margin: 0 0.05rem 0.1rem 0.05rem;
        width: 3.6rem;
        border-radius: 5px;
        a{
          display: block;
          width: 100%;
          height: 100%;
          .searchLineImgBox{
            width: 3.6rem;
            height: 3.3rem;
            display: -webkit-box;
            display: -ms-flexbox;
            display: flex;
            background-color: #ffffff;
            img{
              margin: auto;
            }
          }
          .searchLineFontBox{
            background-color: #ffffff;
            border-bottom-right-radius: 5px;
            border-bottom-left-radius: 5px;
            padding: 0.1rem;
          }
          .searchLineFont{
            font-size: 0.24rem;
            color: #3b3b3b;
            overflow: hidden;
            text-overflow: ellipsis;
            display: -webkit-box;
            -webkit-box-orient: vertical;
            -webkit-line-clamp: 2;
            height: 0.64rem;
          }
          .after{
            margin-top: 0.1rem;
            .left{
              color: #ff3f3f;
              font-size: 0.22rem;
              float: left;
              span{
                font-size: 0.3rem;
              }
            }
            .right{
              float: right;
              font-size: 0.2rem;
              color: #bebebe;
            }
          }
        }
      }
    }
    /*纵向样式*/
    .searchColumn{
      background-color: #ffffff;
      li{
        border-bottom: 1px solid #f0f0f0;
        padding: 0.3rem;
        a{
          display: block;
          width: 100%;
          height: 100%;
          .searchLineImgBox{
            width: 1.6rem;
            height: 1.6rem;
            border-radius: 4px;
            display: -webkit-box;
            display: -ms-flexbox;
            display: flex;
            float: left;
            margin-right: 0.4rem;
            img{
              margin: auto;

            }
          }
          .serOriginalPrice{
            font-size: 0.24rem;
            color: #939393;
            margin-top: 0.2rem;
          }
          .searchLineFontBox{
            float: left;
            width: 4.8rem;
            .searchLineFont{
              font-size: 0.24rem;
              color: #3b3b3b;
              overflow: hidden;
              text-overflow: ellipsis;
              display: -webkit-box;
              -webkit-box-orient: vertical;
              -webkit-line-clamp: 2;
            }
            .after{
              margin-top: 0.1rem;
              .left{
                color: #ff3f3f;
                font-size: 0.22rem;
                float: left;
                span{
                  font-size: 0.3rem;
                }
              }
              .right{
                float: right;
                font-size: 0.2rem;
                color: #bebebe;
              }
            }
            /*}*/
          }
        }
      }
      li:first-child{
        border-top: 1px solid #f0f0f0;
      }
      li:last-child{
        border-bottom: none;
      }
      li:after{
        display: block;
        content: '';
        clear: both;
      }
    }
  }
  .moreSpeBox{
    /*position: fixed;*/
    /*left: 0;*/
    /*right: 0;*/
    /*top: 0;*/
    /*bottom: 0;*/

    overflow-y: scroll;
    /*height: 500px;*/
    -webkit-overflow-scrolling:touch;
    background-color: #f7f7f7;
  }
}
</style>



