<template>
  <div class="pageBox shouhouBox">
    <div class="shTuihouBox" style="display: none">
      <p class="p1">
        退款金额 <span>¥169.00</span>
      </p>
      <p class="p2">
        最多￥169.00，含发货邮费￥0.00
      </p>
    </div>
    <div class="shhuanhuoBox shhuanhuoTitBox">
      <div class="after shhuanhuoTit">
        <div class="left">
          <p class="p1">
          <span>
            程晓玲
          </span>
            <span>
            13400008915
          </span>
          </p>
          <p class="p2">
            <i class="icon iconfont icon-dizhi1"></i>
            锦绣阳光A区3栋一单元9楼19号
          </p>
        </div>
        <div class="right">
          <i class="icon iconfont icon-bread"></i>
        </div>
      </div>
    </div>
  <!--公用内容开始-->
    <div class="shouhouCont">
      <div class="after title">
        <p class="left">
          请选择原因
        </p>
        <p class="right">
          <a href="tel:0147-88469258">
            <i class="icon iconfont icon-dianhua"></i>
           <span>联系客服</span>
          </a>
        </p>
      </div>
      <div class="conditionList">
        <p>
          包装坏了
        </p>
        <p>
          漏东西了
        </p>
        <p class="is-active">
          味道不对
        </p>
      </div>
      <div class="textearaBox">
        <textarea placeholder="将具体的情况告诉我们，我们将尽快解决此事"></textarea>
      </div>
      <div class="after pictureBox">
        <div class="pictureImg">
          <!--<img src="" alt="">-->
          <i class="icon iconfont icon-jia2"></i>
          <input type="file">
        </div>
        <p class="pictureFont">
          添加照片
        </p>
      </div>
    </div>
    <div class="shouhouBottom">
      <p>
        申请售后
      </p>
    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {}
    },
    methods: {},
    computed: {}
  }
</script>

<style lang="scss">
.shouhouBox{
  background-color: #f7f7f7;
  .shTuihouBox{
    padding-left: 0.2rem;
    background-color: #ffffff;
    .p1{
      border-bottom: 1px solid #e7e7e7;
      font-size: 0.28rem;
      color: #3b3b3b;
      line-height: 0.8rem;
      padding-left: 0.15rem;
      span{
        color: #ff2d2d;
        padding-left: 0.2rem;
      }
    }
    .p2{
      font-size: 0.24rem;
      padding-left: 0.15rem;
      color: #939393;
      line-height: 0.8rem;
    }
  }
  .shhuanhuoTitBox{
    padding-left: 0.3rem;
    background-color: #ffffff;
  }
  .shhuanhuoTit{
    padding: 0.3rem 0.3rem 0.3rem 0 ;
    border-bottom: 1px solid #f0f0f0;
    .left{
      float: left;
      .p1{
        font-size: 0.3rem;
        color: #3b3b3b;
        margin-bottom: 0.06rem;
      }
      .p2{
        font-size: 0.26rem;
        color: #3b3b3b;
        i{
          font-size: 0.32rem;
          color: #bbbbbb;
        }
      }
    }
    .right{
      float: right;
      i{
        display: block;
        line-height: 0.8rem;
        font-size: 0.32rem;
      }
    }
  }
  .shouhouCont{
    .title{
      line-height: 0.8rem;
      font-size: 0.24rem;
      padding: 0 0.3rem 0 0.4rem;
      .left{
        float: left;
        color: #a0a0a0;
      }
      .right{
        float: right;
        color: #949494;
        i{
          color: #949494;
          font-size: 0.3rem;
          float: left;
          display: block;
        }
        span{
          float: left;
          display: block;
          padding-left: 0.2rem;
        }
      }
    }
    .conditionList{
      padding: 0 0.05rem;
      p{
        float: left;
        width: 3rem;
        text-align: center;
        line-height: 0.7rem;
        font-size: 0.24rem;
        color: #a0a0a0;
        border-radius: 0.5rem;
        border: 1px solid #dcdcdc;
        background-color: #ffffff;
        margin: 0 0.35rem 0.4rem 0.35rem;
      }
      .is-active{
        background-color: #de3031;
        color: #ffffff;
        border-color:#de3031 ;
      }
    }
    .textearaBox{
      clear: both;
      margin-bottom: 0.7rem;
      textarea{
        width: 6.6rem;
        height: 2.5rem;
        padding: 0.4rem;
        margin: auto;
        display: block;
        resize: none;
      }
    }
    .pictureBox{
      padding:0 0.4rem;
      .pictureImg{
        float: left;
        width: 1.6rem;
        height: 1.6rem;
        background-color: #ffffff;
        border-radius: 6px;
        line-height: 1.6rem;
        text-align: center;
        position: relative;
        /*display: -webkit-flex;*/
        /*display: flex;*/
        i{
          font-size: 0.6rem;
          display: block;
          color: #de3031;
        }
        input{
          position: absolute;
          left: 0;
          right: 0;
          top: 0;
          bottom: 0;
          opacity: 0;
          display: block;
        }
      }
      .pictureFont{
        float: left;
        line-height: 1.6rem;
        margin-left: 0.4rem;
        font-size: 0.3rem;
        color: #949494;
      }
    }
  }
.shouhouBottom{
  position: fixed;
  left: 0;
  right: 0;
  bottom: 0.3rem;
  p{
    line-height: 0.7rem;
    width: 6.6rem;
    border-radius: 0.5rem;
    background-color: #de3031;
    color: #ffffff;
    font-size: 0.3rem;
    margin: auto;
    text-align: center;
  }
}
}
</style>



