<template>
  <div class="pageBox myEvaluateBox">
    <ul>
      <li v-for="item in myEvalArr">
        <div class="title">
          <div class="imgBox">
            <img :src="item.goodsImg[0]" alt="" class="img-my">
          </div>
          <p class="evalCommNmae">
            {{item.goodsName}}
          </p>
        </div>
        <p class="evalue">
          {{item.content}}
        </p>
        <p class="time">
          {{item.createTime}}
        </p>
      </li>
    </ul>
  </div>
</template>

<script>
    export default {
        data() {
            return {
              myEvalData:'',
              myEvalArr:[]
            }
        },
      mounted () {
        this.myEvaluateListInit()
      },
        methods: {
          /**
           * 页面初始化
           */
          myEvaluateListInit(){
            this.$http.get('/evaluate/list/user')
              .then(res => {
                if (res.status === 200) {
                  if(res.data.code===200){
                    console.log(res.data);
                    this.myEvalData=res.data.data;
                    this.myEvalArr=res.data.data.records
                  }
                }
              })
          },
        },
        computed: {}
    }
</script>

<style lang="scss">
.myEvaluateBox{
  background-color: #f7f7f7;
  ul{
    background-color: #ffffff;
    padding-left: 0.3rem;
    li{
      padding: 0.2rem 0.3rem 0.2rem 0;
      border-bottom: 1px solid #f0f0f0;
      .title{
        display: flex;
        justify-content:flex-start;
        background-color: #f7f7f7;
        margin-bottom: 0.2rem;
        .imgBox{
          width: 0.9rem;
          height: 0.9rem;
          display: -webkit-flex;
          display: flex;
          img{
            margin: auto;
          }
        }
        .evalCommNmae{
          font-size: 0.26rem;
          color: #3b3b3b;
          line-height: 0.9rem;
          margin-left: 0.1rem;
          overflow: hidden;
          text-overflow:ellipsis;
          white-space: nowrap;
          width: 5.7rem;
        }
      }
      .evalue{
        font-size: 0.28rem;
        color: #3b3b3b;
        margin-bottom: 0.1rem;
      }
      .time{
        font-size: 0.20rem;
        color: #ababab;
      }
    }
  }
}
</style>



