<template>
  <div class="pageBox commentBox">
    <div class="commentCont">
      <div class="after commentCommodity">
        <div class="imgBox">
          <img src="@/assets/img/indexTu3.png" alt="" class="img-my">
        </div>
        <p class="font">
          和风手绘古朴釉下彩陶艺小酱碗点心糕点 碟盘
        </p>
      </div>
      <textarea name="" class="commentContText" placeholder="请您多说说需求说明，便于其他人更了解哦！"></textarea>
      <p class="commentContNumber">
        0/500
      </p>
      <ul class="after commentImgBox">
        <li>
          <img src="@/assets/img/banner.png" alt="" class="img-my">
          <i class="icon iconfont icon-guanbi colen"></i>
        </li>
        <div class="commentFile">
          <i class="icon iconfont icon-xiangji zxj"></i>
          <input type="file" capture="camera" accept="image/*" />
        </div>
      </ul>

    </div>
    <div class="after commentIconBox">
      <p>
        <i class="icon iconfont icon-haoping"></i>
        <span>好评</span>
      </p>
      <p>
        <i class="icon iconfont icon-zhongping"></i>
        <span>中评</span>
      </p>
      <p>
        <i class="icon iconfont icon-chaping1"></i>
        <span>差评</span>
      </p>
    </div>
    <div class="commentScore">
      <div class="after commentScoreTit">
        <p class="left">
          整体评分
        </p>
        <p class="right">
          满分请给五星额
        </p>
      </div>
      <div class="commentScoreMidfy">
        <div class="after">
          <p class="font">
            商品质量
          </p>
          <p class="iconBox">
            <i v-for="(star,index) in 5" @click="setStar('scoreFlag',index)" :class="scoreFlag >= index ? 'icon-xingxing':'icon-xingxing1'"  class="icon iconfont"></i>
          </p>
        </div>
        <div class="after">
          <p class="font">
            送货员服务
          </p>
          <p class="iconBox">
            <i v-for="(star,index) in 5" @click="setStar('scoreFlag2',index)" :class="scoreFlag2 >= index ? 'icon-xingxing':'icon-xingxing1'"  class="icon iconfont"></i>
          </p>
        </div>
        <div class="after">
          <p class="font">
            客服服务
          </p>
          <p class="iconBox">
            <i v-for="(star,index) in 5" @click="setStar('scoreFlag3',index)" :class="scoreFlag3 >= index ? 'icon-xingxing':'icon-xingxing1'"  class="icon iconfont"></i>
          </p>
        </div>
      </div>
    </div>
    <div class="commentBottom">
      <p>
        提交评价
      </p>
    </div>
  </div>
</template>

<script>
    export default {
        data() {
            return {
              scoreFlag: -1, //商品质量
              scoreFlag2: -1, //送货员服务
              scoreFlag3: -1, //客服服务
            }
        },
        methods: {
          setStar(star,i){
            this[star] = i;
          }
        },
        computed: {}
    }
</script>

<style lang="scss">
.commentBox{
  background-color: #f7f7f7;
  .commentCont{
    width: 100%;
    padding:0.3rem 0.3rem;
    background-color: #ffffff;
    border-bottom: 1px solid #e5e5e5;
    .commentCommodity{
      padding: 0.1rem;
      background-color: #f7f7f7;
      margin-bottom: 0.3rem;
      .imgBox{
        float: left;
        width: 1rem;
        height: 1rem;
        border-radius: 6px;
        margin-right: 0.3rem;
        img{
          width: 1rem;
          height: 1rem;
          border-radius: 6px;
        }
      }
      .font{
        color: #3b3b3b;
        font-size: 0.28rem;
        overflow:hidden;
        text-overflow:ellipsis;
        display:-webkit-box;
        -webkit-box-orient:vertical;
        -webkit-line-clamp:2;
        max-width: 100%;
      }
    }
    .commentContText{
      width: 100%;
      height: 2.3rem;
      border: none;
    }
    .commentContNumber{
      color: #939393;
      font-size: 0.24rem;
      text-align: right;
      margin-bottom: 0.2rem;
    }
    .commentImgBox{
      li{
        position: relative;
        width: 1.34rem;
        height: 1.34rem;
        /*overflow: hidden;*/
        /*display: -webkit-flex;*/
        /*display: flex;*/
        border-radius: 6px;
        margin-right: 0.2rem;
        float: left;
        img{
          /*margin: auto;*/
          width: 1.34rem;
          height: 1.34rem;
          border-radius: 6px;
        }
        .colen{
          padding: 0.1rem;
          font-size: 0.34rem;
          position: absolute;
          right: -0.24rem;
          top: -0.24rem;
          color: #fe3e3e;

        }
      }
      .commentFile{
        position: relative;
        width: 1.34rem;
        height: 1.34rem;
        overflow: hidden;
        /*display: -webkit-flex;*/
        /*display: flex;*/
        text-align: center;
        line-height: 1.34rem;
        border-radius: 6px;
        border:1px solid #e5e5e5;
        float: left;
        .zxj{
          font-size: 0.5rem;
          display: block;
        }
        input{
          position: absolute;
          left: 0;
          right: 0;
          top: 0;
          bottom: 0;
          opacity: 0;
        }
      }
    }
  }
  .commentIconBox{
    line-height: 1rem;
    background-color: #ffffff;
    margin-bottom: 0.2rem;
    p{
      font-size: 0.24rem;
      color: #939393;
      width: 2.5rem;
      float: left;
      padding-left: 0.67rem;
      i{
        color: #939393;
        font-size: 0.52rem;
        display: block;
        float: left;
        margin-right: 0.2rem;
      }

      span{
        display: block;
        float: left;
      }
    }
  }
  .commentScore{
    background-color: #ffffff;
    .commentScoreTit{
      line-height: 1rem;
      padding: 0 0.3rem;
      border-bottom: 1px solid #e5e5e5;
      .left{
        float: left;
        font-size: 0.32rem;
        color: #333333;
      }
      .right{
        float: right;
        font-size: 0.28rem;
        color: #939393;
      }
    }
    .commentScoreMidfy{
      padding: 0 0.3rem;
      .after{
        line-height: 0.7rem;
      }
      .font{
        float: left;
        font-size: 0.28rem;
        color: #939393;
      }
      .iconBox{
        float: right;
        i{
          display: block;
          float: left;
        text-align: center;
          width: 0.6rem;
        }
        .icon-xingxing1{
          color: #939393;
        }
        .icon-xingxing{
          color: #ffcc1d;
        }
      }
    }
  }
  .commentBottom{
    position: fixed;
    left: 0;
    right: 0;
    bottom:0;
    height: 1rem;
    border-top: 1px solid #e5e5e5;
    display: -webkit-flex;
    display: flex;
    background-color: #ffffff;
    p{
      margin: auto;
      width: 2.5rem;
      height: 0.7rem;
      color: #ffffff;
      font-size: 0.28rem;
      background-color: #de3031;
      text-align: center;
      line-height: 0.7rem;
      border-radius: 0.5rem;
    }
  }
}
</style>



