<template>
  <div class="pageBox couponsBox">
    <ul>
      <li class="coupons-active" v-for="item in couponsData">
        <div class="couponsCont">
          <div class="timeBox">
            <p class="name">
              满减券
            </p>
            <p class="time">
              有效期至{{item.endTime}}
            </p>
          </div>
          <div class="priceP">
            ¥<span>{{item.discount}}</span>
          </div>
        </div>
        <p class="couponsedu">
          满{{item.fullPrice}}元可使用
        </p>
      </li>
    </ul>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        couponsData:''
      }
    },
    mounted () {
      this.couponsInit()
    },
    methods: {
      /**
       * 页面初始化
       */
      couponsInit(){
        this.$http.post('/coupon/listByUserId')
          .then(res => {
            if (res.status === 200) {
              if(res.data.code===200){
                console.log(res.data)
                this.couponsData=res.data.data
              }
            }
          })
      },
    },
    computed: {}
  }
</script>

<style lang="scss">
.couponsBox{
  padding: 0.24rem 0.3rem;
  background-color: #f7f7f7;
  position: fixed;
  left: 0;
  top:0;
  width: 100%;
  height: 100%;
  overflow-y: scroll;
  ul{
    li{
      padding: 0.35rem 0.4rem;
      background-color: #ffffff;
      margin-bottom: 0.35rem;
      .couponsCont{
        width: 100%;
        display: flex;
        justify-content:space-between;

        .name{
          font-size: 0.34rem;
        }
        .time{
          font-size: 0.24rem;
        }
        .priceP{
          font-size: 0.34rem;
          span{
            font-size: 0.64rem;
          }

        }
      }
      .couponsedu{
        font-size: 0.28rem;
        color: #939393;
        margin-top: 0.2rem;
      }
    }
    .coupons-active{
      background: url("../../assets/img/coupons_tu1.png") no-repeat center;
      background-size: 100% 100%;
      .couponsCont{
        color: #de3031;
      }
    }
    .coupons-expired{
      background: url("../../assets/img/coupons_tu2.png") no-repeat center;
      background-size: 100% 100%;
      .couponsCont{
        color: #939393;
      }
    }
  }
}
</style>



