
<template>
  <div class="pageBox secondKillBox">

    <!--横向滑动  start-->
    <div class="indexActivityBox">
      <ul class="after">
        <li class="indexActivityItem"  v-for="(item,$index) in timeList" @click="seconKillListFun(item,$index)" :class="{'is-active': item.activeflag }">
         <div class="secondKillCont">
           <p class="secondKillTime">
             {{item.strTime}}
           </p>
           <p class="secondKillState" v-if="item.status===0">
             未开始
           </p>
           <p class="secondKillState" v-else-if="item.status===1">
             进行中
           </p>
           <p class="secondKillState" v-else="item.status===2">
             已结束
           </p>
         </div>
        </li>
      </ul>
    </div>
    <!--横向滑动  end-->
    <div class="indexRecommend">
      <!--<ul class="after">-->
        <mt-loadmore :bottom-method="loadBottom" :bottom-all-loaded="allLoaded" ref="loadmore"  :auto-fill="false">
          <ul class="after">
            <li v-for="item in commentList">
              <a @click="routerFun(item)">
                <div class="imgBox">
                  <img :src="item.mainImage" alt="" class="img-my">
                </div>
                <div class="fontBox">
                  <p class="font">
                    {{item.goodsName}}
                  </p>
                  <div class="after">
                    <p class="left">
                      ¥{{item.goodsPrice}}
                    </p>
                    <p class="right">
                      {{item.saleAmont}}人付款
                    </p>
                  </div>
                </div>
                </a>
            </li>
          </ul>
          <div slot="bottom" class="mint-loadmore-bottom">
            <img src="../../assets/img/loding.gif" alt="">
          </div>

        </mt-loadmore>

    </div>
  </div>
</template>
<script>
  import Vue from 'vue';
//  import { Loadmore } from 'mint-ui';
//  Vue.component(Loadmore.name, Loadmore);
  import { Toast } from 'mint-ui';
  import * as util from '../../utils/utils.js'
  export default {
    name: 'index',
    data () {
      return {
        allLoaded:false,//上拉加载标记
        timeList:[], //时间列表
        timeId:'', //当前时间段
        commNumber:'0',//商品计数
        secondKillFlag:'',//该时间端下面商品状态过期没
        commentObj:{
          records:[],//商品列表
        }, //商品列表对象
        commentList:[],//存储商品
      }
    },
    mounted () {
      this.seconKillInit()

    },
    methods: {
      /**
       * 页面初始化函数
       * 获取时间列表
       */
      seconKillInit(){
        this.$http.post('/shopRushBuy/getKillTime')
          .then(res => {
            if (res.status === 200) {
              console.log('res.data', res.data);
              if(res.data.code===200){
                this.timeList=res.data.data;
                for(let  i=0; i< this.timeList.length;i++){
                  let st=new Date(this.timeList[i].startTime.replace(/-/,"/"));
                  this.timeList[i].strTime=util.formatDate( st,'hh:mm');
                  if(this.timeList[i].status ===1){
                    Vue.set(this.timeList[i],'activeflag',true);
                    this.seconKillComListFun(this.timeList[i].id,1);
//                    let sta=this.timeList[i].startTime
                    let end=this.timeList[i].endTime;
                    this.timeDaojishiFun(end.replace(/-/,"/"))
                    this.secondKillFlag=this.timeList[i].status
                  }else {
                    Vue.set(this.timeList[i],'activeflag',false)
                  }
                }
              }

            }
          })
      },
      /**
       * 时间列表点击函数
       * @param index 时间下标 做demo效果用
       * @param item 对象Obj 根据时间请求数据
       */
      seconKillListFun(item, index){
        this.$nextTick(function () {
          this.timeList.forEach(function (item) {
            Vue.set(item,'activeflag',false)
          });
          Vue.set(item,'activeflag',true);
          this.commentList=[];
          this.seconKillComListFun(item.id,1);
//        let end='2018-06-12 19:00:00'
        this.timeDaojishiFun(item.endTime.replace(/-/,"/"))
          this.timeId=item.id
          this.secondKillFlag=item.status
        });
      },
      /**
       * 获取商品列表
       * @param id
       * @param pages
       */
      seconKillComListFun(id,pages){
        let timeObj={
          id:id,
          current:pages,
          size:"10",
        };
        this.$http.post('/shopRushBuy/findKillShopGoods',this.$qs.stringify(timeObj))
          .then(res => {
            if (res.status === 200) {
              console.log('res.data', res.data);
              if(res.data.code===200){
                if(res.data.data!={}){
                  this.commentObj=res.data.data;
                  if(res.data.data!==null){
                    this.commentList=this.commentList.concat(res.data.data.records);
                    if(res.data.data.pages<=1){
                      this.allLoaded = true;
                    }else {
                      this.allLoaded = false;
                    }
                  }

                }else {
                  this.allLoaded = true;
                }
              }

            }
          })
      },
      /**
       * 上拉
       */
      loadBottom() {
        let pages=Number(this.commentObj.current)+1;
        this.seconKillComListFun(this.timeId,pages)
        this.$refs.loadmore.onBottomLoaded();
      },
      /**
       * 倒计时
       * @param endTime 倒计时
       */
      timeDaojishiFun(endTime){
        let date1=new Date();  //开始时间
        let date2=new Date(endTime);    //结束时间
        if(date1<date2){
          let date3=date2.getTime()-date1.getTime();  //时间差的毫秒数
          let arrTime= Math.floor(date3/1000);
          let funName2= setInterval(() => {
            if(arrTime>=1){
              arrTime--
            }else {
              this.seconKillInit()
              clearInterval(funName2);
              return false
            }
            console.log(arrTime)
          },1000)
        }else {
          console.log('当前时间比结束时间大')
        }
      },
      routerFun(row){
        if(this.secondKillFlag===2){
          Toast({
            message:'秒杀已结束',
            duration: 1000
          });
        }else {
          this.$router.push({ path: 'commodityDetails',query: {id: row.id,iskill:1}});
        }

      }
    },

  }

</script>

<style lang="scss">

  .secondKillBox{
    background-color: #f7f7f7;
    /*横向滑动  start*/
    .indexActivityBox{
      overflow-x: scroll; /* 1 */
      overflow-y: hidden;
      background-color: #333333;
      position: fixed;
      left: 0;
      right: 0;
      z-index: 55;
      top: 0;
      ul{
        display:flex;
        text-align: center;

      }
      .indexActivityItem{
        /*float: left;*/
        width:1.5rem;
        height: 0.94rem;
        flex:1;
        box-sizing:border-box;
        font-size: 0.26rem;
        .secondKillCont{
          width: 1.3rem;
          height: 0.7rem;
          color: #8b8b8b;
          margin: 0.12rem 0.1rem;
          border-radius: 6px;
          .secondKillTime{
            font-size: 0.3rem;
          }
          .secondKillState{
            font-size: 0.22rem;
          }
        }
      }
      .is-active{
        .secondKillCont{
          background-color: #ff5d54;
          color: #ffffff;
        }

      }
    }
    .indexActivityBox::-webkit-scrollbar{display: none;} //隐藏滚动条！
    /*横向滑动  end*/

    .indexRecommend{
      position: absolute;
      left: 0;
      right: 0;
      top: 0;
      bottom: 0;
      height: 100%;
     padding-top: 1.05rem;
      overflow: scroll;
      background: #f0f0f0;
      .indexFontTit{
        color: #bebebe;
      }
      ul{
        padding: 0 0.05rem;
        li{
          float: left;
          margin:0 0.05rem 0.1rem 0.05rem;
          width: 3.6rem;
          border-radius: 5px;
          a{
            display: block;
            width: 100%;
            height: 100%;
            border-radius: 5px;
            .imgBox{
              width: 3.6rem;
              height: 3.3rem;
              display: -webkit-flex;
              display: flex;
              background-color: #ffffff;
              img{
                margin: auto;
              }
            }
            .font{
              color: #3b3b3b;
              font-size: 0.24rem;
              height: 0.64rem;
              display: -webkit-box;
              -webkit-box-orient: vertical;
              -webkit-line-clamp: 2;
              overflow: hidden;
            }
            .fontBox{
              background-color: #ffffff;
              padding: 0.1rem;
            }
            .after{
              padding: 0.1rem 0 ;
            }
            .left{
              float: left;
              font-size: 0.28rem;
              color: #ff3f3f;
            }
            .right{
              float: right;
              font-size: 0.2rem;
              color: #bebebe;
            }
          }
        }
      }
    }
  }
</style>



