<template>
  <div class="pageBox loginBox">
    <mt-header title="登录">

      <a class="indexbackOff" @click="fanhuiFun()" slot="left">
        <mt-button icon="back"></mt-button>
      </a>

        <mt-button  slot="right">  <router-link  :to="{path:'forgotPassword',query:{name: 1}}">注册</router-link>
        </mt-button>

    </mt-header>
    <div class="loginFormBox">
      <input type="text" v-model="login.phone" placeholder="手机号/邮箱/昵称" >
      <input type="password" v-model="login.password" placeholder="密码">
      <div class="loginFormFontBox after">
        <router-link  :to="{path:'LoginText'}" class="left">手机短信登录</router-link>
        <router-link  :to="{path:'forgotPassword',query: {name: 2}}" class="right">忘记密码</router-link>
      </div>
      <p class="loginFormBtn" @click="submitForm()">
        登录
      </p>
    </div>
    <p class="loginFont">
      点击登录，即表示同意 <a >《用户协议》</a>
    </p>
  </div>
</template>

<script>
  import { Toast } from 'mint-ui';
  import * as util from '../../utils/regular.js'
  import md5 from 'js-md5';
export default {
  name: 'login',
  data: function () {
    return {
      token: '',
      login: {
        phone: '',
        password: ''
      },
    }
  },
  methods: {
    /**
     * 在这个点击提交事件
     *
     */
    submitForm () {
    let mobiles=util.mobiles(this.login.phone);
    if(mobiles  && this.login.password){
      this.doLogin()
      console.log("提交")
    }else if(mobiles!=true){
      Toast({
        message:mobiles,
        position: 'top',
        duration: 1000
      });
    }else {
      Toast({
        message:'请输入密码',
        position: 'top',
        duration: 1000
      });
    }
    },
    /**
     * 提交函数
     */
    doLogin () {
      let password=md5(this.login.password);
      let obj={
        phone: this.login.phone,
        password: password
      }
      this.$http.post('/user/login', this.$qs.stringify(obj))
        .then(res => {
          console.log('res.data', res)
          if (res.status === 200) {
            if (res.data.code === 200) {
              localStorage.setItem("userId",  res.data.data.access_id);
              localStorage.setItem("token", res.data.data.access_token);
              this.$router.push({ path: 'index' });
//              var user = localStorage.getItem("user");
//              console.log(user)
            } else {
              Toast({
                message:res.data.msg,
                duration: 1000
              });
            }
          }
        })

    },
    /**
     * 返回上一个页面
     */
    fanhuiFun(){
      this.$router.go(-1)
    }

  },

}

</script>

<style  lang="scss">

.loginBox{
  background-color: #ffffff;
  .mint-header{
    background-color: transparent;
    color: #3b3b3b;
    font-size: 0.34rem;
    height:0.9rem;
    border-bottom: 1px solid #e8e8e8;
  }
  .loginMode{
    padding: 0.68rem 1.4rem;
    display: -webkit-flex;
    display: flex;
    .loginModeItem{
      margin:0 auto;
      text-align: center;

      img{
        width: 0.9rem;
        height: 0.9rem;
      }
      p{
        font-size: 0.24rem;
        color: #939393;
        margin-top: 0.24rem;
      }
    }
  }
  .loginFormBox{
    padding: 0.6rem 0.45rem 0.6rem 0.45rem;

    input{
      padding: 0.19rem 0.4rem;
      border-radius: 0.5rem;
      background-color: #f6f6f6;
      color: #949494;
      font-size: 0.28rem;
      display: block;
      width: 100%;
      margin-bottom: 0.3rem;

    }
    input:first-child{
      margin-bottom: 0.4rem;
    }
    .loginFormFontBox{
      font-size: 0.24rem;
      margin-bottom: 0.45rem;
      .left{
        float: left;
        color: #de3031;
      }
      .right{
        float: right;
        color: #939393;
      }
    }
    .loginFormBtn{
      font-size: 0.3rem;
      background-color: #de3031;
      border-radius: 0.5rem;
      line-height: 0.7rem;
      margin-bottom: 0.28rem;
      text-align: center;
      color: #fff;
    }
  }
  .loginFont{
    font-size: 0.24rem;
    color: #939393;
    text-align: center;
    a{
      color: #de3031;
    }
  }
}
</style>



