<script>

  import bottom from '@/components/bottom'
  import city from '@/components/city'
export default {
  name: 'index',
  components: {
    bottom,
    city
  },
  data () {
    return {
      cityFlag:false,//选择城市弹窗
      newsPopFlag:false,
      cityName:'成都市',
      tianqi:'',
      modifyfun:[],
      indexData:{
        news:[ //新闻
          {
            content:'',
            title:''
          }
        ],
        specialTheme:[
          {
            id:'',
            mainPic:'',
          }
        ]
      },
      indexDataTwo:{

        specialTheme:[
          {
            id:'',
            mainPic:'',
          }
        ]
      },
    }
  },
  mounted () {
    this.indexInit()
    this.cityFun()
  },
  methods: {
    /**
     * 页面初始化函数
     */
    indexInit(){
      this.$http.post('/activity/getAll')
        .then(res => {
          if (res.status === 200) {
            console.log('res.data', res.data);
            if(res.data.code===200){
              this.indexData=res.data.data;
            }
          }
        });
      this.$http.post('/activity/getAllBelow')
        .then(res => {
          if (res.status === 200) {
            console.log('res.data', res.data);
            if(res.data.code===200){
              this.indexDataTwo=res.data.data;
              this.indexDataTwo.likeCimm=res.data.data.shopGoods.reverse()
            }

          }
        })
    },
    /**
     * 控制新闻弹窗
     */
    newsShowFun(){
      this.newsPopFlag=!this.newsPopFlag
    },
    /**
     *  并打开弹窗
     * @param data
     */
    indexCityFun(data){
      this.cityFlag=true
    },
    /**
     * 子组件选中过后的
     * @param data
     */
    clkCitFun(data){
      this.cityName=data.name.countyName
      let obj={
        countyName:this.cityName
      };
      this.sysTiamqiFun(obj)
    },
    /**
     * 查看天气
     * @param obj
     */
    sysTiamqiFun(obj){
      this.$http.post('/sys/weatherCity/weather/query',this.$qs.stringify(obj))
        .then(res => {
          if (res.status === 200) {
            console.log('res.data', res.data);
            if(res.data.code===200){
              this.tianqi=res.data.data.temp
            }
          }
        })
    },
    /**
     * 查看地址
     */
    cityFun(){
      this.$http.post('/weatherCityOpen/getOpenCityList')
        .then(res => {
          if (res.status === 200) {
            console.log('res.data', res.data);
            if(res.data.code===200){
              this.modifyfun=res.data.data;
              this.cityName=res.data.data[0].countyName;
              let obj={
                countyName:this.cityName
              };
              this.sysTiamqiFun(obj)
            }
          }
        })
    }
  },

}

</script>

