<template>
  <div class="pageBox releaseTypeBox">
    <div class="indexConHeader">
      <a class="indexbackOff" @click="fanhuiFun()">
        <i class="icon iconfont icon-guanbi1-copy"></i>
      </a>
      便捷生活
      <router-link to="myRelease" class="indexNews">
        <i class="icon iconfont icon-cebian-wodedingdan"></i>
      </router-link>
    </div>
    <div class="releaseTypeList">
      <p class="releaseTypeListTit">
        选择发布类别
      </p>
      <ul>
        <li class="after" v-for="item in releaseData">
          <router-link :to="{path: item.id === 1 ? 'releaseVehicle': 'releaseLife' ,query: {id: item.id,midType:1}}">
            <p>{{item.className}}</p>
            <i class="icon iconfont icon-bread"></i>
          </router-link>
        </li>
      </ul>
    </div>


  </div>

</template>

<script>
  export default {
    data() {
      return {
        releaseData:{}
      }
    },
    mounted () {
      this.relesaseTypeInit()
    },
    methods: {
      /**
       * 页面初始化函数
       */
      relesaseTypeInit(){
        this.$http.post('/lifeClass/findLifeClass')
          .then(res => {
            if (res.status === 200) {
              if(res.data.code===200){
                console.log('res.data', res.data);
                this.releaseData=res.data.data
              }
            }
          })
      },
      /**
       * 返回上一个页面
       */
      fanhuiFun(){
        this.$router.go(-1)
      },
    },
    computed: {}
  }
</script>
<style lang="scss">

  .releaseTypeBox {
    background-color: #f7f7f7;
    padding-top: 0.9rem;
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    .indexConHeader {
      position: fixed;
      left: 0;
      right: 0;
      top: 0;
      z-index: 5;
      background-color: #de3031;
      height: 0.9rem;
      color: #fff;
      font-size: 0.34rem;
      text-align: center;
      line-height: 0.9rem;
      i{
        font-size: 0.4rem;
      }
      .indexbackOff {
        position: absolute;
        left: 0;
        top: 0;
        bottom: 0;
        line-height: 0.9rem;
        padding: 0 0.2rem;
        color: #fff;
      }
      .indexNews {
        position: absolute;
        right: 0;
        top: 0;
        bottom: 0;
        line-height: 0.9rem;
        padding: 0 0.2rem;
        color: #fff;
        i{
          font-size: 0.4rem;
        }
      }
    }
.releaseTypeList{
  .releaseTypeListTit{
    font-size: 0.28rem;
    height: 0.7rem;
    color: #9a9a9a;
    line-height: 0.7rem;
    padding: 0 0.2rem;
  }
  ul{
    padding-left: 0.2rem;
    background-color: #ffffff;
    overflow-y: scroll;
    -webkit-overflow-scrolling: touch;
    li{
      line-height: 1rem;
      height: 1rem;
      border-bottom: 1px solid #f0f0f0;
      a{
        display: block;
        width: 100%;
        height: 100%;
      }
      p{
        color: #676767;
        font-size: 0.28rem;
        float: left;
      }
      i{
        color: #676767;
        font-size: 0.28rem;
        float: right;
        margin-right: 0.2rem;
      }
    }
    li:last-child{
      border-bottom: none;
    }
  }
}
  }
</style>



