
<template>
  <div class="pageBox searchBox">
    <!--header start-->
    <div class="searchHeader" ref="heightSearhed">
      <a class="searchbackOff" @click="fanhuiFun()">
        <i class="icon iconfont icon-guanbi1-copy"></i>
      </a>
      <div class="searchHeaderSerch">
        <!--<form action="javascript:return true;" @submit="searchFun">-->
          <input type="text" placeholder="请输入关键字" @blur="searchFun" v-model="searFormData.name">
          <i class="icon iconfont icon-sousuo"></i>
        <!--</form>-->
      </div>
      <a class="searchHeaderNews" @touchend="lineOrcolFun">
        <i class="icon iconfont icon-hengxiangliebiao" v-if="storageData.lineORcolFlag"></i>
        <i class="icon iconfont icon-shuxiangliebiao" v-else="storageData.lineORcolFlag==false"></i>
      </a>
    </div>
    <div class="searchClassTotBox" v-show="searchFlag" ref="heightTotBox">
      <p v-for="(item,index) in titleList" @click="titleLstClickFun(item)" :class="{'is-active': item.colorFlag}">
        {{item.name}} <i :class="item.icon"></i>
      </p>
    </div>
    <div class="searchList " ref="searchList">
      <ul :class="storageData.lineORcolFlag ? 'searchLine':'searchColumn'">
      <mt-loadmore :bottom-method="loadBottom" :bottom-all-loaded="allLoaded" ref="loadmore"  :auto-fill="false">
          <li v-for="item in commitArr">
            <router-link :to="{path:'commodityDetails',query: {id: item.id}}">
              <div class="searchLineImgBox">
                <img :src="item.mainImage" alt="" class="img-my">
              </div>
              <div class="searchLineFontBox">
                <p class="searchLineFont">
                 {{item.goodsName}}
                </p>
                <p class="serOriginalPrice" v-show="storageData.lineORcolFlag===false">
                  原价：<s>￥{{item.originalPrice}}</s>
                </p>
                <div class="after">
                  <p class="left">
                    ￥<span>{{item.salesPrice}}</span>
                  </p>
                  <p class="right">
                    {{item.saleAmont}}人付款
                  </p>
                </div>
              </div>
            </router-link>
          </li>
        <div slot="bottom" class="mint-loadmore-bottom">
          <img src="../../assets/img/loding.gif" alt="">
        </div>
      </mt-loadmore>
      </ul>
    </div>
    <!--筛选弹窗-->
    <div class="searchScreenBox" v-show="storageData.screenPopupFlag" >
      <div class="searchScreen">
        <div class="searchScreenService">
          <p class="tit">
            选择服务
          </p>
          <div class="searchScreenServiceCont">
            <p v-for="item in fuwuArr" @click="fuwuClickFun(item)" :class="{ 'active': item.fuwuFlag}">
              {{item.name}}
            </p>
          </div>
        </div>
        <div class="searchScreenPrice">
          <p class="tit">
            价格区间
          </p>
          <div class="searchScreenPriceInput">
            <div class="inputBox">
              <input type="number" placeholder="最低价" v-model="lowPrice">
            </div>
            <div class="inputHr">
              <i class="icon iconfont icon-jian"></i>
            </div>
            <div class="inputBox">
              <input type="number" placeholder="最高价" v-model="highPrice">
            </div>
            <div class="searchScreenPriceBtns">
              <div class="sspItem " v-for="item in priceQujianArr" @click="jiageClickFun(item)" :class="{'active': item.priceQujian}">
                <p class="price">{{item.lowPrice}}-{{item.highPrice}}</p>
                <p class="number">{{item.percentage}}的选择</p>
              </div>
            </div>
          </div>
        </div>
        <div class="subBtns">
          <p class="sspReset" @click="jiageResetFun">
            重置
          </p>
          <p class="sspSure" @click="jiageSureFun">
            确认
          </p>
        </div>
      </div>
    </div>
    <div class="searchScreenBox" v-show="storageData.togetherPopupFlag" >
      <div class="searchScreen "  style=" border-top: 1px solid #f0f0f0;">
        <div class="searchBrandBox">
          <p class="tit">
            选择品牌
          </p>
          <ul class="searchBrand">
            <li v-for="item in pingpaiArr" @click="pingpaiClickFun(item)" :class="{'active':item.pingpaiFlag}">
              {{item.name}}
            </li>
          </ul>
        </div>
        <div class="subBtns">
          <p class="sspReset" @click="pinpaiRestFun">
            重置
          </p>
          <p class="sspSure" @click="pinpaiSubFun">
            确认
          </p>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  import Vue from 'vue'
  import bottom from '@/components/bottom'
  import { Toast } from 'mint-ui';
  export default {
    name: 'search',
    components: {
      bottom
    },
    data () {
      return {
        typeId:'',//typeId
        jiageType:'',//排序方式type
        searchFlag:false,//控制筛选条件框
        priceQujian:false,
        priceQujianArr:[],//价格区间数组
        pingpaiArr:[],//品牌数组
        pingpaiFlag:false,
        fuwuFlag:false,
        allLoaded:false,//上拉加载标记
        commitData:{},//商品对象
        searchFafaFlag:'0',//标记方式 0---默认查询 1-品牌 2-排序方式 3-价格
        commitArr:[],//商品数组
        pingpaiId:'',//品牌id
        fuwId:'',//服务ID
        fuwuArr:[
          {
            name:'包邮',
            type:'1'
          },
          {
            name:'促销',
            type:'2'
          },
          {
            name:'新品',
            type:'3'
          },
        ],
        searFormData:{
          current:1,//页数
          status:1,//1商品 2店铺
          type:1,//1=价格降序/0升序,2=销量
          name:'',//关键字
          attributeId:'',//属性ID
          shopModularId:'',//模块ID
          categoryId:'',//类型Id
          brandId:'',//品牌ID
          address:'',//区域地址
          goodsType:'',//1=包邮,2=促销,3=新品
          lowPrice:'',//最低价格
          highPrice:'',//最高价格
          userId:localStorage.getItem('userId')
        },
        lowPrice:'',
        highPrice:'',
        storageData:{//用来存储一些标记
          lineORcolFlag:true, //为true===竖 false===行
          togetherPopupFlag:false, //控制综合弹窗标记
          screenPopupFlag:false, //控制筛选弹窗标记
        },
        titleList:[//模拟数据
          {
            name:"综合",
            id:"1",
            type:null,
            icon:null,
            colorFlag:false
          },
          {
            name:"价格",
            id:"2",
            type:-1,
            icon:'icon iconfont icon-paixugaodi',
            colorFlag:false
          },
          {
            name:"销量",
            id:"3",
            type:2,
            icon:null,
            colorFlag:false
          },
          {
            name:"筛选",
            id:"4",
            icon:'icon iconfont icon-shaixuan',
            colorFlag:false,
            type:null,
          },
        ],

      }
    },
    mounted () {
      this.typeId=this.$route.query.id;
      if(this.$route.query.modularId){
        this.modularId=this.$route.query.modularId;
      }
     if(this.typeId){
       this.searchFlag=true;
       this.searchInit();
        this.searFormData.categoryId=this.typeId;
       this.searchAjaxFun0('/shopGoods/getShopList',this.searFormData);
       this.searchFafaFlag=0
     }else {
       this.searchFlag=false
     }
    },
    methods: {
      /**
       *页面初始化
       */
      searchInit(){
        console.log(this.typeId);
        /**
         *查询品牌
         */
        this.$http.get('/goodsBrand/getBrandByCategoryId?categoryId='+this.typeId)
          .then(res => {
            if (res.status === 200) {
              if(res.data.code===200){
                    console.log('查询品牌', res.data.data);
                this.pingpaiArr=res.data.data
              }
            }
          });
        /**
         * 查询价格区间
         */
        this.$http.get('/statisticsPrice/getPriceArea')
          .then(res => {
            if (res.status === 200) {
              if(res.data.code===200){
                console.log('价格区间', res.data.data);
                this.priceQujianArr=res.data.data
              }
            }
          })
      },
      /**
       * 控制列表横向或者纵向
       */
      lineOrcolFun(){
        this.storageData.lineORcolFlag = !this.storageData.lineORcolFlag
      },
      /**
       *分类头部点击事件
       * @param data ==id
       */
      titleLstClickFun(data){
        console.log(data);
        this.$nextTick(function() {
          this.titleList.forEach(function(data) {
            Vue.set(data,'colorFlag',false)
          });
          Vue.set(data,'colorFlag',true)
        });
        if(data.id==1){
          this.storageData.screenPopupFlag=false;
          this.storageData.togetherPopupFlag = !this.storageData.togetherPopupFlag
        }else if(data.id==4){
          this.storageData.togetherPopupFlag=false;
          this.storageData.screenPopupFlag = !this.storageData.screenPopupFlag
        } else {
          this.storageData.togetherPopupFlag=false;
          this.storageData.screenPopupFlag=false;
          if( data.type===1){
            data.type=-1
          }else if( data.type===-1){
            data.type=1
          }

//          if(this.searchFafaFlag===0){
//
//          }else if(this.searchFafaFlag===1){
//
//          }else if(this.searchFafaFlag===2){
//
//          }else if(this.searchFafaFlag===3){
//
//          }
          this.jiageType=data.type;
          this.commitArr=[];
//          let obj={
//            current:'1',
//            size:'10',
//            type:this.jiageType,
//            categoryId:this.typeId
//          };
          this.searFormData.current=1
          this.searFormData.type=this.jiageType;
          this.searFormData.categoryId=this.typeId;
          this.searchAjaxFun0('/shopGoods/getShopList',this.searFormData);
//          this.searchAjaxFun0('/shopGoods/getShopGoodsOnCategoryBy',obj)
          this.searchFafaFlag=0
        }
      },
      /**
       * 返回上一个页面
       */
      fanhuiFun(){
        this.$router.go(-1)
      },
      /**
       * 服务点击事件
       * @param item
       */
      fuwuClickFun(item){
        console.log(item)
        this.$nextTick(function() {
          this.fuwuArr.forEach(function(item) {
            Vue.set(item,'fuwuFlag',false)
          });
          Vue.set(item,'fuwuFlag',true)
          this.fuwId=item.type
        });
      },
      /**
       * 价格区间点击事件
       */
      jiageClickFun(row){
        console.log(row)
        this.$nextTick(function() {
          this.priceQujianArr .forEach(function(row) {
            Vue.set(row,'priceQujian',false)
          });
          Vue.set(row,'priceQujian',true)
        });
        this.lowPrice=row.lowPrice;
        this.highPrice=row.highPrice
      },
      /**
       * 价格区间点击事件
       */
      pingpaiClickFun(row){
        console.log(row)
        this.$nextTick(function() {
          this.pingpaiArr .forEach(function(row) {
            Vue.set(row,'pingpaiFlag',false)
          });
          Vue.set(row,'pingpaiFlag',true)
          this.pingpaiId=row.id
        });
      },
      /**
       * 品牌确认提交
       */
      pinpaiSubFun(){
        if(this.pingpaiId){
          this.commitArr=[];
          this.searchFafaFlag=1;
//          let obj={
//            current:'1',
//            size:'10',
//            brandId:this.pingpaiId
//          };
          this.searFormData.current=1
          this.searFormData.brandId=this.pingpaiId;
          this.searFormData.categoryId=this.typeId;
          this.searchAjaxFun0('/shopGoods/getShopList',this.searFormData);
//          this.searchAjaxFun1('/goodsBrand/getShopGoodsByBrandId',obj)
        }else {
          Toast({
            message:'请选择品牌',
            duration: 1000
          });
        }

      },
      /**
       * 品牌重置
       */
      pinpaiRestFun(){
        this.pingpaiArr .forEach(function(row) {
          Vue.set(row,'pingpaiFlag',false)
        });
        this.pingpaiId=''
      },
      /**
       * 价格确认提交
       */
      jiageSureFun(){
        if( this.fuwId||this.highPrice){
          this.commitArr=[];
//          let obj={
//            current:'1',
//            size:'10',
//            goodsType:this.fuwId,
//            lowPrice:this.lowPrice,
//            highPrice:this.highPrice,
//            categoryId:this.typeId
//          };
//          this.searchAjaxFun0('/shopGoods/getShopGoodsByServiceOrPrice',obj)
          this.searFormData.current=1
          this.searFormData.goodsType=this.fuwId;
          this.searFormData.lowPrice=this.lowPrice;
          this.searFormData.highPrice=this.highPrice;
          this.searFormData.categoryId=this.typeId;
          this.searchAjaxFun0('/shopGoods/getShopList',this.searFormData);
          this.searchFafaFlag=3
        }else {
          Toast({
            message:'请选择筛选条件',
            duration: 1000
          });
        }

      },
      /**
       * 价格重置
       */
      jiageResetFun(){
        this.priceQujianArr .forEach(function(row) {
          Vue.set(row,'priceQujian',false)
        });
        this.fuwuArr .forEach(function(row) {
          Vue.set(row,'fuwuFlag',false)
        });
        this.fuwId='';
        this.lowPrice='';
        this.highPrice='';
      },
      /**
       * 查询商品事件0
       */
      searchAjaxFun0(url,obj){
        let objZs={};
        for(let item in obj){
          if(obj[item]){
            objZs[item] = obj[item]
          }
        }
        console.log(objZs)
        this.$http.post(url,this.$qs.stringify(objZs))
          .then(res => {
            if (res.status === 200) {
              if(res.data.code===200){
                console.log('查询商品事件0 ', res.data.data);
                this.commitData=res.data.data;
                this.storageData.togetherPopupFlag=false;
                this.storageData.screenPopupFlag=false;
                if(res.data.data.records.length>0){
                  this.commitArr=this.commitArr.concat(res.data.data.records);
                  if(res.data.data.pages<=1||res.data.data.records.length<10){
                    this.allLoaded = true;
                  }else {
                    this.allLoaded = false;
                  }
                  let heightMx=document.documentElement.clientHeight;
                  let heightSearhed= this.$refs.heightSearhed.offsetHeight;
                  let heightTotBox= this.$refs.heightTotBox.offsetHeight;
                  heightMx=heightMx-heightSearhed-heightTotBox
                  this.$refs.searchList.style.height =heightMx+'px';
                }else {
                  this.allLoaded = true;
                }
              }
            }
          })
      },
      /**
       * 上拉
       */
      loadBottom() {
        console.log(123)
        let pages=Number(this.commitData.current)+1;
        this.searFormData.current=pages
        this.searchAjaxFun0('/shopGoods/getShopList',this.searFormData);
        this.$refs.loadmore.onBottomLoaded();
      },
      /**
       * 搜索框
       */
      searchFun(){
        this.commitArr=[];
        this.searFormData.current=1;
        this.searFormData.goodsType='';
        if(this.modularId){
          this.searFormData.shopModularId=this.modularId;
        }else {
          this.searFormData.shopModularId='';
        }

        this.searFormData.attributeId='';
        this.searFormData.brandId='';
        this.searFormData.address='';
        this.searFormData.goodsType='';
        this.searFormData.lowPrice='';
        this.searFormData.highPrice='';
        this.searchAjaxFun0('/shopGoods/getShopList',this.searFormData);
      },
    },
    watch:{
      searFormData:{//深度监听，可监听到对象、数组的变化
        handler(val, oldVal){
          console.log(val);
          console.log(oldVal);
          if(this.modularId){
            this.searchFlag=false;
            this.storageData.togetherPopupFlag=false;
            this.storageData.screenPopupFlag=false;
          }else {
            if(val.name.length>=1){
              this.searchFlag=false;
              this.storageData.togetherPopupFlag=false;
              this.storageData.screenPopupFlag=false;
              this.commitArr=[];
              let heightMx=document.documentElement.clientHeight;
              let heightSearhed= this.$refs.heightSearhed.offsetHeight;
              heightMx=heightMx-heightSearhed
              this.$refs.searchList.style.height =heightMx+'px';
            }else {
              this.searchFlag=true;
              let heightMx=document.documentElement.clientHeight;
              let heightSearhed= this.$refs.heightSearhed.offsetHeight;
              let heightTotBox= this.$refs.heightTotBox.offsetHeight;
              heightMx=heightMx-heightSearhed-heightTotBox
              this.$refs.searchList.style.height =heightMx+'px';
            }
          }


        },
        deep:true
      }
    },
    computed: {
      handler() {
        return this.searFormData
      }
    }
  }
</script>

