          padding: 0.3rem 0.4rem;
          border-bottom: 1px solid #f0f0f0;
          border-top: 1px solid #f0f0f0;
          .tit{
            color: #3b3b3b;
            font-size: 0.28rem;
            margin-bottom: 0.3rem;
          }
          .searchScreenServiceCont{
            display: -webkit-flex;
            display: flex;
            p{
              width: 2rem;
              height: 0.7rem;
              text-align: center;
              line-height: 0.7rem;
              margin: auto 0.35rem auto 0;
              background-color: #f6f6f6;
              border-radius: 4px;
              font-size: 0.28rem;
              color: #3d3d3d;
            }
            p:last-child{
              margin-right: 0;
            }
            p.active{
              color: #de3031;
              background-color: #f9dada;
            }
          }
        }
        .searchScreenPrice{
          padding: 0.3rem 0.4rem;
          .tit{
            color: #3b3b3b;
            font-size: 0.28rem;
            margin-bottom: 0.3rem;
          }
          .searchScreenPriceInput{
            .inputBox{
              float: left;
              background-color: #f6f6f6;
              border-radius: 4px;
              line-height: 0.7rem;
              width: 3.12rem;
              height: 0.7rem;
              margin-bottom: 0.2rem;
              input{
                display: block;
                width: 100%;
                background-color: transparent;
                padding: 0.2rem 0;
                text-align: center;
              }
            }
            .inputHr{
              float: left;
              line-height: 0.7rem;
              font-size: 0.3rem;
              margin-bottom: 0.2rem;
              padding:  0 0.07rem;
            }
            .searchScreenPriceBtns{
              width: 100%;
              clear: both;
              display: -webkit-flex;
              display: flex;

              .sspItem{
                width: 2rem;
                padding-top: 0.15rem;
                padding-bottom: 0.15rem;
                border-radius: 5px;
                background-color: #f6f6f6;
                text-align: center;
                margin: auto 0.44rem auto 0;
                .price{
                  font-size: 0.28rem;
                  color: #3d3d3d;
                }
                .number{
                  font-size: 0.28rem;
                  color: #3d3d3d;
                }
              }
              .sspItem.active{
                background-color: #f9dada;
                .number{
                  color: #de3031;
                }
                .price{
                  color: #de3031;
                }
              }
              .sspItem:last-child{
                margin-right: 0;
              }
            }
          }
        }
        .searchBrandBox{
          padding: 0.3rem 0.4rem 0 0.4rem;
          .tit{
            color: #3b3b3b;
            font-size: 0.28rem;
            margin-bottom: 0.3rem;
          }
          .searchBrand:after{
            display: block;
            clear: both;
            content: '';
          }
          .searchBrand{
            li{
              width: 50%;
              font-size: 0.28rem;
              color: #999999;
              float: left;
              line-height: 0.8rem;
              overflow: hidden;
              text-overflow:ellipsis;
              white-space: nowrap;
            }
            li.active{
              color: #de3031;
            }
          }
        }
        .subBtns{
          padding: 0.35rem 0.3rem;
          display: -webkit-flex;
          display: flex;
          p{
            border-radius: 0.5rem;
            margin: auto;
            width: 2.8rem;
            height: 0.7rem;
            text-align: center;
            line-height: 0.7rem;
            font-size: 0.3rem;
            color: #3d3d3d;
          }
          .sspReset{
            border: 1px solid #f0f0f0;
          }
          .sspSure{
            background-color: #de3031;
            color: #ffffff;
          }
        }
      }
    }
    .mint-loadmore-bottom {
      clear: both;
    }
  }
</style>



