<template>
  <div class="pageBox shopCommodityClassBox" v-show="shopClassFlag" >
    <div class="indexConHeader">
      <a  class="indexbackOff" @click="hide">
        <i class="icon iconfont icon-guanbi1-copy"></i>
      </a>
      商品分类

    </div>
    <div class="releaseTypeList">
      <ul>
        <li class="after" v-for="item in modify.arr">
          <router-link :to="{path:'searchShop',query: {id: item.id,shopId:modify.shoppId}}">
            <p>{{item.name}}</p>
            <i class="icon iconfont icon-bread"></i>
          </router-link>
        </li>
      </ul>
    </div>


  </div>

</template>

<script>
  export default {
    data() {
      return {}
    },
    props: ['shopClassFlag', 'modify'],
    methods: {
      // 关闭弹窗
      hide(){
        this.$emit('update:shopClassFlag')
      },
      hideFun (row) {

        this.$emit('subReturnFuns',{row});
        this.hide ()
      },
    },
    computed: {}
  }
</script>
<style lang="scss">
  .shopCommodityClassBox {
    background-color: #f7f7f7;
    padding-top: 0.9rem;
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    .indexConHeader {
      position: fixed;
      left: 0;
      right: 0;
      top: 0;
      z-index: 5;
      background-color: #de3031;
      height: 0.9rem;
      color: #fff;
      font-size: 0.34rem;
      text-align: center;
      line-height: 0.9rem;
      .indexbackOff {
        position: absolute;
        left: 0;
        top: 0;
        bottom: 0;
        line-height: 0.9rem;
        padding: 0 0.2rem;
        color: #fff;
      }
      .indexNews {
        position: absolute;
        right: 0;
        top: 0;
        bottom: 0;
        line-height: 0.9rem;
        padding: 0 0.2rem;
        color: #fff;
      }
    }
    .releaseTypeList{
      .releaseTypeListTit{
        font-size: 0.28rem;
        height: 0.7rem;
        color: #9a9a9a;
        line-height: 0.7rem;
        padding: 0 0.2rem;
      }
      ul{
        padding-left: 0.2rem;
        background-color: #ffffff;
        li{
          line-height: 1rem;
          height: 1rem;
          border-bottom: 1px solid #f0f0f0;
          a{
            display: block;
            width: 100%;
            height: 100%;
          }
          p{
            color: #676767;
            font-size: 0.28rem;
            float: left;
          }
          i{
            color: #676767;
            font-size: 0.28rem;
            float: right;
            margin-right: 0.2rem;
          }
        }
        li:last-child{
          border-bottom: none;
        }
      }
    }
  }
</style>



