<template>
  <div class="pageBox myReleaseBox">
    <div class="indexConHeader">
      <a class="indexbackOff" @click="fanhuiFun()">
        <i class="icon iconfont icon-guanbi1-copy"></i>
      </a>
      我发布的
    </div>

    <div class="indexConListBox">
      <ul class="indexConListHcart">
        <li class="after" v-for="item in myReleaseList">
          <router-link :to="{path: 'issueDetails' ,query: {id: item.id}}">
            <div class="after mypeleaseListBox">
              <div class="indexConListImgBox">
                <img :src="item.images" alt="" class="img-my">
              </div>
              <div class="indexConListFontBox">
                <p class="indexConListFontP1">
                  {{item.content}}
                </p>
                <p class="indexConListFontP2">
                  {{item.publishTime}}
                </p>
              </div>
            </div>
          </router-link>
          <div class="mypeleaseBtnBox after">
            <p class="delect" @click="deleteFun(item.id)">
              删除
            </p>
            <p class="modify">
              <router-link :to="{path: item.classId === 1 ? 'releaseVehicle': 'releaseLife' ,query: {midType:2,id: item.classId,myReId:item.id}}">
                编辑
              </router-link>
            </p>
          </div>
        </li>
      </ul>
    </div>

  </div>
</template>

<script>
  import { Toast } from 'mint-ui';
  export default {
    data() {
      return {
        myReleaseList:[]
      }
    },
    mounted () {
      this.myReleaseInit()
    },
    methods: {
      /**
       * 初始化函数
       */
      myReleaseInit(){
        let objId={
          type:2
        }
        this.$http.post('/activity/findMyActivity',this.$qs.stringify(objId))
          .then(res => {
            if (res.status === 200) {
              if(res.data.code===200){
                console.log('res.data', res.data);
                this.myReleaseList=res.data.data.records
                for(let i=0; i<this.myReleaseList.length;i++){
                  if(this.myReleaseList[i].images !== null){
                    let imgArr=[];
                    imgArr=this.myReleaseList[i].images.split(",")
                    this.myReleaseList[i].images=imgArr[0]
                  }
                }
              }
            }
          })
      },
      /**
       * 删除函数
       * @id id
       */
      deleteFun(id){
        let _id=id;
        let objId={
          activityId:id
        }
        this.$http.post('/activity/delActivity',this.$qs.stringify(objId))
          .then(res => {
            if (res.status === 200) {
              if(res.data.code===200){
                Toast({
                  message:'操作成功',
                  duration: 1000
                });
                let loop = this.myReleaseList.length;    //循环次数
                for(let i=0;i<loop;i++){
                  console.log(this.myReleaseList[i].id)
                  console.log(_id)
                  if(this.myReleaseList[i].id===_id){
                    this.myReleaseList.splice(i,1);
                    if(i>=1){
                      i = i-1;
                      console.log(i)
                      loop=loop-1
                    }
                  }
                }
              }else {
                Toast({
                  message:'操作失败',
                  duration: 1000
                });
              }
            }
          })
      },
      /**
       * 返回上一个页面
       */
      fanhuiFun(){
        this.$router.go(-1)
      },
    },
    computed: {}
  }
</script>
<style lang="scss">
  .myReleaseBox {
    background-color: #f7f7f7;
    padding-top: 0.9rem;
    .indexConHeader {
      position: fixed;
      left: 0;
      right: 0;
      top: 0;
      z-index: 5;
      background-color: #de3031;
      height: 0.9rem;
      color: #fff;
      font-size: 0.34rem;
      text-align: center;
      line-height: 0.9rem;
      i{
        font-size: 0.4rem;
      }
      .indexbackOff {
        position: absolute;
        left: 0;
        top: 0;
        bottom: 0;
        line-height: 0.9rem;
        padding: 0 0.2rem;
        color: #fff;
      }
    }
    .indexBanner {
      height: 3rem;
      margin-bottom: 0.2rem;
      .mint-swipe-indicator {
        background-color: #c0d2f7;
        opacity: 1;
      }
      .mint-swipe-indicator.is-active {
        background: #ff6f20;
        width: 10px;
        height: 10px;
      }
    }
    .indexConListBox {
      .indexConListHcart {
        li {
          background-color: #fff;
          padding: 0 0 0 0.28rem;
          margin-bottom: 0.2rem;
          a{
            display: block;
            width: 100%;
            height: 100% ;
          }
          .mypeleaseListBox {
            border-bottom: 1px solid #f0f0f0;
            padding: 0.3rem 0;
          }
          .indexConListImgBox {
            width: 2rem;
            height: 1.6rem;
            border-radius: 6px;
            float: left;
            display: -webkit-flex;
            display: flex;
            img {
              margin: auto;
            }
          }
          .indexConListFontBox {
            float: left;
            width: 4.5rem;
            margin-left: 0.3rem;
            .indexConListFontP1 {
              font-size: 0.3rem;
              color: #333;
              height: 1.2rem;
              overflow: hidden;
              text-overflow: ellipsis;
              display: -webkit-box;
              -webkit-box-orient: vertical;
              -webkit-line-clamp: 3;
            }
            .indexConListFontP2 {
              color: #939393;
              font-size: 0.22rem;
            }
          }
        }

      }
      .mypeleaseBtnBox{
        padding: 0.25rem 0.3rem;
        float: right;
        p{
          float: left;
          width: 1.7rem;
          height: 0.5rem;
          text-align: center;
          line-height: 0.5rem;
          border-radius: 0.5rem;
          font-size: 0.26rem;

        }
        .delect{
         color:#333333 ;
          border:1px solid #f0f0f0;
        }
        .modify{
          border:1px solid #de3031;
          margin-left: 0.3rem;
          a{
            color: #de3031;
            width: 100%;
            display: block;
            height: 100%;
          }
        }
      }
    }

  }
</style>



