<template>
  <div class="pageBox myCollectionBox">
    <mt-loadmore :bottom-method="loadBottom" :bottom-all-loaded="allLoaded" ref="loadmore"  :auto-fill="false">
      <ul>
        <li v-for="item in myCollectionArr">
          <div class="imgBox">
            <img :src="item.mainImage" alt="" class="img-my">
          </div>
          <div class="fontBox">
            <p class="p1 name">
               {{item.goodsName}}
            </p>
            <p class="p2 yjprice">
              原价：<s>¥98.5</s>
            </p>
            <div class="fontCont after">
              <p class="p3">
                ¥{{item.priceKill}}
              </p>
              <p class="p4">
                {{item.saleAmont}}人付款
              </p>
            </div>
          </div>
        </li>
      </ul>
      <div slot="bottom" class="mint-loadmore-bottom">
        <img src="../../assets/img/loding.gif" alt="">
      </div>
    </mt-loadmore>

  </div>
</template>

<script>
    export default {
        data() {
            return {
              allLoaded:false,//上拉加载标记
              myCollectionData:'',
              myCollectionArr:[],
            }
        },
      mounted () {
        let obj={
          current:'1',
          size:'10',
        };
        this.myCollectionInit(obj)
      },
        methods: {
          /**
           * 页面初始化
           */
          myCollectionInit(obj){
            this.$http.post('/collect/getList',this.$qs.stringify(obj))
              .then(res => {
                if (res.status === 200) {
                  if(res.data.code===200){
                    console.log(res.data)
                    this.myCollectionData=res.data.data
                    if(res.data.data!=null){
                      this.myCollectionArr=this.myCollectionArr.concat(res.data.data.records)
                      if(this.myCollectionData.pages<=1){
                        this.allLoaded=true
                      }else {
                        this.allLoaded=false
                      }
                    }else {
                      this.allLoaded=true
                    }
                  }
                }
              })
          },
          /**
           * 上拉
           */
          loadBottom() {
            let pages=Number(this.myCollectionData.current)+1;
            let obj={
              current:pages,
              size:'10',
            }
            this.myCollectionInit(obj)
            this.$refs.loadmore.onBottomLoaded();
          },
        },
        computed: {}
    }
</script>

<style lang="scss">
.myCollectionBox{
  background-color: #f7f7f7;
  ul{
    background-color: #ffffff;
    li{
      display: flex;
      justify-content:flex-start;
      padding: 0.3rem 0.2rem 0.3rem 0.3rem;
      border-bottom: 1px solid #f0f0f0;
      .imgBox{
        width: 1.6rem;
        height: 1.6rem;
        border-radius: 6px;
        display: -webkit-flex;
        display: flex;
        margin-right: 0.4rem;
        img{
        margin: auto;
        }
      }
      .fontCont{
        min-width: 4.8rem;
      }
      .after:after{
        display: block;
        clear: both;
        content: "";
      }
      .p1{
        font-size: 0.3rem;
        color: #333333;
      }
      .p2{
        font-size: 0.24rem;
        color: #939393;
        padding: 0.1rem 0;
      }
      .p3{
        font-size: 0.3rem;
        color: #ff3f3f;
        float: left;
      }
      .p4{
        font-size:  0.24rem;
        color: #939393;
        float: right;
      }
      .name{
        display: -webkit-box;
        -webkit-box-orient: vertical;
        -webkit-line-clamp: 2;
        overflow: hidden;
        max-width:4.8rem;
        height: 0.8rem;
      }
    }
  }
}
</style>



