<template>
  <div class="pageBox indexUsedBox">
    <div class="indexConHeader">
      <router-link to="index" class="indexbackOff">
        <i class="icon iconfont icon-guanbi1-copy"></i>
      </router-link>
      二手市场
      <router-link to="usedReleaseList" class="indexNews">
        <i class="icon iconfont icon-cebian-wodedingdan"></i>
      </router-link>
    </div>
    <!--banner start-->
    <div class="indexUsedBanner">
      <mt-swipe :auto="4000">
        <mt-swipe-item v-for=" item in indexUsDedData.banner">
          <router-link :to="{path:'commodityDetails',query: {id: item.id}}">
            <img :src="item.image" alt="" class="img-my">
          </router-link>
        </mt-swipe-item>
      </mt-swipe>
    </div>
    <!--banner end-->
    <div class="issueDetailsleave">
      <ul>
        <li v-for="item in  liebiaoArr">
          <router-link :to="{path: 'usedDetails' ,query: {id: item.id}}">
          <div class="after issueDetailsleaveUserBox">
            <div class="issueDetailsleaveUserImg">
              <img :src="item.user.headImg" alt="" class="img-my">
            </div>
            <div class="issueDetailsleaveUserFont">
              <p class="name">
                {{item.user.nickName}}
              </p>
              <p class="data">
                {{item.publishTime}}
              </p>
            </div>
            <p class="leaveFont">
              {{item.content}}
            </p>
          </div>
          <ul class="after indxUsedImgUl">
            <li v-for="img in item.imgArr">
              <img :src="img" alt="" class="img-my">
            </li>
          </ul>
          <p class="indxUsedLike">
            <i class="icon iconfont icon-message"></i>
            {{item.commentCount}}
          </p>
          </router-link>
        </li>
      </ul>
    </div>
    <div class="indexConBtn">
      <router-link :to="{path:'usedRelease',query:{midType:1}}">
        <img src="@/assets/img/indexConBtn.png" alt="" class="img-my">
      </router-link>
    </div>
  </div>

</template>

<script>
  export default {
    data() {
      return {
        indexUsDedData:{
          activity:{
            records:[]
          }
        },//接收banner
        imgArr:[],//图片数组
        liebiaoArr:[
//          {
//            "id":51,
//            "phone":"852",
//            "name":"说的",
//            "title":null,
//            "price":0,
//            "fixedPrice":0,
//            "content":"咯爱啦啦阿萨德789456",
//            "startTime":null,
//            "endTime":null,
//            "publishTime":"2018-07-04 17:09:49",
//            "isDel":0,
//            "type":3,
//            "userId":237,
//            "rank":null,
//            "classId":null,
//            "modularId":null,
//            "carId":null,
//            "isRecommend":0,
//            "images":"https://jsuke.oss-cn-shenzhen.aliyuncs.com/image/1530695385154279039.png",
//            "status":null,
//            "transactionAreaId":null,
//            "user":{
//              "id":237,
//              "lastLoginTime":"2018-07-04 09:52:42",
//              "canUse":1,
//              "nickName":"U客友",
//              "phone":"18780154101",
//              "level":0,
//              "sex":1,
//              "headImg":"https://jsuke.oss-cn-shenzhen.aliyuncs.com/default/shop_default.jpg",
//              "password":"e10adc3949ba59abbe56e057f20f883e",
//              "loginIp":"192.168.0.135",
//               "wxToken":null,
//              "weiboToken":null,
//              "qqToken":null,
//              "isSecondaryMarket":null,
//              "isAvailable":null,
//              "address":null,
//              "createTime":"2018-06-22 17:24:21",
//              "updateTime":"2018-06-22 17:24:21"
//             },
//      "commentCount":4
//    },
//          {
//            user:{
//              headImg:"321",
//              nickName:"321",
//            }
//          },
        ],
      }
    },
    mounted () {
      this.indexUsedInit()
    },
    methods: {
      /**
       * 页面初始化函数
       */
      indexUsedInit(){
        this.$http.post('/activity/getToMarket')
          .then(res => {
            if (res.status === 200) {
              console.log('res.data', res.data.data);
              if(res.data.code===200){
                this.liebiaoArr=res.data.data.activity.records;
                this.indexUsDedData=res.data.data;
                for(let i=0;i< this.liebiaoArr.length;i++){
//                  if()
                  if(this.liebiaoArr[i].images.indexOf(',')!==-1){
                    this.liebiaoArr[i].imgArr=this.liebiaoArr[i].images.split(",")
                  }else {
                    this.liebiaoArr[i].imgArr=[]
                    this.liebiaoArr[i].imgArr.push(this.liebiaoArr[i].images)
                  }

                }


                console.log( this.liebiaoArr)
              }
            }
          })
      },
    },
    computed: {}
  }
