<style lang="scss">
  .indexUsedBox {
    background-color: #f7f7f7;
    padding-top: 0.9rem;
    padding-bottom: 0.98rem;
    .indexConHeader {
      position: fixed;
      left: 0;
      right: 0;
      top: 0;
      z-index: 5;
      background-color: #de3031;
      height: 0.9rem;
      color: #fff;
      font-size: 0.34rem;
      text-align: center;
      line-height: 0.9rem;

      .indexbackOff {
        position: absolute;
        left: 0;
        top: 0;
        bottom: 0;
        line-height: 0.9rem;
        padding: 0 0.2rem;
        color: #fff;
        i{
          font-size: 0.4rem;
        }
      }
      .indexNews {
        position: absolute;
        right: 0;
        top: 0;
        bottom: 0;
        line-height: 0.9rem;
        padding: 0 0.2rem;
        color: #fff;
        i{
          font-size: 0.48rem;
        }
      }
    }
    .indexUsedBanner{
      height: 3rem;
      .mint-swipe-indicator{
        background-color: #c0d2f7;
        opacity: 1;
      }
      .mint-swipe-indicator.is-active {
        background: #ff6f20;
        width: 10px;
        height: 10px;
      }
    }
    .issueDetailsleave{
      margin-top: 0.2rem;
      .issueDetailsleaveTit{
        font-size: 0.3rem;
        color: #3b3b3b;
        line-height: 0.8rem;
        border-bottom: 1px solid #f0f0f0;
      }
      ul{
        li{
          padding: 0.3rem 0.3rem 0.3rem 0.3rem;
          background-color: #ffffff;
          margin-bottom: 0.2rem;
          .issueDetailsleaveUserBox{
            width: 100%;
            height: 100%;
            .issueDetailsleaveUserImg{
              width: 0.6rem;
              height: 0.6rem;
              border-radius: 50%;
              overflow: hidden;
              float: left;
              margin-right: 0.2rem;
              img{
                width: 0.6rem;
                height: 0.6rem;
                border-radius: 50%;
              }
            }
            .issueDetailsleaveUserFont{
              float: left;
              .name{
                color: #3b3b3b;
                font-size: 0.3rem;
              }
              .data{
                color: #939393;
                font-size: 0.22rem;
              }
            }
            .leaveFont{
              margin-top: 0.1rem;
              width: 100%;
              line-height: 0.44rem;
              clear: both;
              font-size: 0.26rem;
              color: #3b3b3b;
              line-height: 0.6rem;
            }
          }
          .indxUsedImgUl{
            li{
              width: 2.23rem;
              height: 2.23rem;
              margin-right: 0.1rem;
              float: left;
              margin-bottom: 0.1rem;
              display: -webkit-flex;
              display: flex;
              border: none;
              padding: 0;
              /*img{*/
                /*margin: auto;*/
              /*}*/
            }
            li:nth-child(3){
              margin-right: 0;
            }
            li:nth-child(6){
              margin-right: 0;
            }
            li:nth-child(9){
              margin-right: 0;
            }
            li:nth-child(12){
              margin-right: 0;
            }

          }
          .indxUsedLike{
            font-size: 0.24rem;
            color: #3b3b3b;
           padding-top: 0.2rem;
            text-align: right;
           clear: both;
            i{
              font-size: 0.36rem;
            }
          }
        }
        li:last-child{
          border-bottom: none;
        }

      }
    }
    .indexConBtn{
      position: fixed;
      left: 50%;
      bottom: 0.3rem;
      width: 1.1rem;
      height: 1.1rem;
      margin: 0 -0.55rem;
    }
    .mint-swipe-indicators{
      bottom: -0.2rem;
    }
  }
</style>



