<template>
  <div class="pageBox modifyPaswBox">
    <input type="password" placeholder="输入您现在的密码" v-model="modifyPassData.oldPassword">
    <input type="password"  placeholder="输入新的密码" v-model="newPasswordOne">
    <input type="password"  placeholder="确认新的密码" v-model="modifyPassData.newPassword">
    <p class="subBtns" @touchend="modiftPassSub">
      确定
    </p>
  </div>
</template>

<script>
  import { Toast } from 'mint-ui';
  import md5 from 'js-md5';
    export default {
        data() {
            return {
              newPasswordOne:'',//第一次输入新密码
              modifyPassData:{
                oldPassword:'',
                newPassword:'',
                userId:localStorage.getItem("userId")
              }
            }
        },
        methods: {
          modiftPassSub(){
            if(this.newPasswordOne&&this.modifyPassData.oldPassword&&this.modifyPassData.newPassword){
              if(this.newPasswordOne==this.modifyPassData.newPassword){
                let oldPassword=md5(this.modifyPassData.oldPassword);
                let newPassword=md5(this.modifyPassData.newPassword);
                let obj={
                  oldPassword:oldPassword,
                  newPassword:newPassword,
                  userId:localStorage.getItem("userId")
                }
                this.$http.post('/user/editPasswordByOld', this.$qs.stringify(obj))
                  .then(res => {
                    console.log('res.data', res)
                    if (res.status === 200) {
                      if (res.data.code ===200) {
                        Toast({
                          message:'修改成功',
                          duration: 1000
                        });
                        this.$router.push({ path: 'mySetUp' });
                      } else {
                        Toast({
                          message:'操作失败',
                          duration: 1000
                        });
                      }
                    }
                  })

              }else {
                Toast({
                  message:'两次密码不一致',
                  duration: 1000
                });
              }
            }else {
              Toast({
                message:'请填写完整',
                duration: 1000
              });
            }
          }
        },
        computed: {}
    }
</script>

<style lang="scss">

  .modifyPaswBox{
    background-color: #ffffff;
    input{
      display: block;
      width: 6.6rem;
      background-color: #f6f6f6;
      font-size: 0.28rem;
      margin: 0.4rem auto 0 auto;
      padding:0.19rem 0.4rem;
      border-radius: 0.5rem;
    }
    .subBtns{
      width: 6.6rem;
      background-color: #de3031;
      font-size: 0.3rem;
      margin: 0.4rem auto 0.4rem auto;
      border-radius: 0.5rem;
      line-height: 0.7rem;
      color:#ffffff;
      text-align: center;
    }
  }
</style>



