<template>

    <div class="mapBox">
        <div class="map">
             <div class="seach">
                <div class="seach_box">
                 <div class="seachInt glow1">
                     <input class="seaInt" v-model="seachText" type="seach" placeholder="请输入您的位置">
                 </div>
                 <div class="seachIcon glow0" @click="seachShow"><i class="iconfont icon-sousuo"></i></div>
                </div>
             </div>
            <div id="allmaps" style="width:100%;height:100%">

            </div>
            <div @click="confirmAddress" class="confirmBtn">
                <span>确认地址</span>
            </div>
        </div>
    </div>
</template>

<script>
//    import BMap from 'BMap'
    import {MP} from '../../utils/map.js'
    import { Toast } from 'mint-ui';
    export default {
        data() {
            return {
                center:{
                    lng:104.075796,lat: 30.659684
                }, //经度 纬度
                seachText: '',
                addressInfo: {},
                ak:'ZMNAs00uoVxukP1zZii9qZeeCU9T86ZA'
            }
        },
          mounted(){
            this.$nextTick(function(){
                var _this = this;
                MP(_this.ak).then(BMap => {
                  //在此调用api
                  this.ready();

                })
            })
          },
        methods: {
            positionshow:function(position){
            },
            confirmAddress: function() {
                console.log('this.addressInfo',this.addressInfo)
              if(this.addressInfo.address){
                this.$emit('subReturnFuns',this.addressInfo);
//                this.$store.commit('addAddress',this.addressInfo);
//                this.$router.push('/AddressInfo');
              }else {
                Toast('请选择地址');
              }

            },
            seachShow: function() {
                var _this = this;
                var map = new BMap.Map('allmaps');
                var point = new BMap.Point(this.center.lng, this.center.lat);
                map.centerAndZoom(point, 15);
                //              map.addControl(new BMap.MapTypeControl());
                map.enableScrollWheelZoom(true);
                map.enableDoubleClickZoom(true);
                //              var marker = new BMap.Marker(point);
                //              map.addOverlay(marker);
                map.addEventListener('click', function(e) {
                    _this.center.lng = e.point.lng;
                    _this.center.lat = e.point.lat;
                    _this.addressInfo.lng = e.point.lng;
                    _this.addressInfo.lat = e.point.lat;
                    map.clearOverlays();
                    var mk = new BMap.Marker(e.point);
                    map.addOverlay(mk);
                    map.panTo(e.point);
                    var point = new BMap.Point(e.point.lng, e.point.lat);
                    var gc = new BMap.Geocoder();
                    gc.getLocation(point, function(rs) {
                        console.log(rs)
                        if( rs.surroundingPois.length>=1){
                          _this.seachText = rs.address +  rs.surroundingPois[0].title;
                          _this.addressInfo.address = _this.seachText;
                        }else {
                          _this.addressInfo.address=rs.address
                        }
                        var addComp = rs.addressComponents;
                    })
                });
                var local = new BMap.LocalSearch(map, {
                    renderOptions: {
                        map: map
                    }
                });
                local.search(this.seachText);
            },
            ready: function() {
              if(navigator.geolocation){
                navigator.geolocation.getCurrentPosition(this.positionshow);
              }else{
                alert('该浏览器不支持')
              }
                var _this = this;
                var map = new BMap.Map('allmaps');

                var point = new BMap.Point(this.center.lng, this.center.lat);
                map.centerAndZoom(point, 15);
                var marker = new BMap.Marker(point);
                map.addOverlay(marker);

                navigator.geolocation.getCurrentPosition(function(position) {
                    _this.center.lng = position.coords.longitude;
                    _this.center.lat = position.coords.latitude;
                    var point = new BMap.Point(position.coords.longitude, position.coords.latitude);
                    map.centerAndZoom(point, 15);
                    var marker = new BMap.Marker(point);
                    map.addOverlay(marker);
                });
                //              map.addControl(new BMap.MapTypeControl());
                map.enableScrollWheelZoom(true);
                map.enableDoubleClickZoom(true);
                //
                map.addEventListener('click', function(e) {
                    _this.center.lng = e.point.lng;
                    _this.center.lat = e.point.lat;
                    _this.addressInfo.lng = e.point.lng;
                    _this.addressInfo.lat = e.point.lat;
                    map.clearOverlays();
                    var mk = new BMap.Marker(e.point);
                    map.addOverlay(mk);
                    map.panTo(e.point);
                    var point = new BMap.Point(e.point.lng, e.point.lat);
                    var gc = new BMap.Geocoder();
                    gc.getLocation(point, function(rs) {
                        console.log(rs)
                        _this.seachText = rs.address + rs.surroundingPois[0].title;
                        _this.addressInfo.address = _this.seachText;
                        var addComp = rs.addressComponents;
                    })
                });
            }
        },
        components: {

        }
    }

</script>




<style lang="scss" >
  .mapBox{
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 66;
  }
    .map {
      width:100%;
      height:100%;
        .confirmBtn {
            width: 80%;
            height: 0.7rem;
            position: absolute;
            z-index: 1;
            bottom: 0.1rem;
            left: 10%;
            background: #2dadff;
            color: #fff;
            text-align: center;
            line-height: 0.7rem;
          font-size: 0.3rem;
        }
    }

    .seach {
        position: absolute;
        z-index: 1;
        top: 0.1rem;
        width: 100%;
        .seach_box {
            height: 0.7rem;
            width: 80%;
            display: flex;
            justify-content: space-between;
            margin: auto;
            background: #fff;
            .seachInt {
                width: 100%;
                height: 100%;
                .seaInt {
                    height: 100%;
                    width: 100%;
                    box-sizing: border-box;
                    padding-left: 0.2rem;
                    font-size:0.3rem;
                  display: block;
                }
            }
            .seachIcon {
                width: 0.7rem;
                height: 100%;
                background: #2dadff;
                display: flex;
                justify-content: center;
                align-items: center;
                i {
                    color: #fff;
                }
            }
        }
    }

</style>



