<template>
  <div class="pageBox loginTextBox">
    <mt-header title="登录">
      <a class="indexbackOff" @click="fanhuiFun()" slot="left">
        <mt-button icon="back"></mt-button>
      </a>
      <mt-button  slot="right">  <router-link  :to="{path:'forgotPassword',query:{name: 1}}">注册</router-link>
      </mt-button>
    </mt-header>
    <div class="loginFormBox">
      <div class="loginFormDivBox after">
        <input type="text" v-model="login.phone" placeholder="手机号">
        <p @touchend="mobileFun" class="timeHide"  v-show="localData.yamFlag===true">
          验证
        </p>
        <p  class="timeShow"  v-show="localData.yamFlag===false">
          {{localData.count}}
        </p>

      </div>
      <input type="text" v-model="login.code" placeholder="验证码">
      <div class="loginFormFontBox after">
        <router-link  :to="{path:'/'}" class="left">账号密码登录</router-link>
      </div>
      <p class="loginFormBtn" @click="submitForm">
        登录
      </p>
    </div>
    <p class="loginFont">
      点击登录，即表示同意 <a >《用户协议》</a>
    </p>
  </div>

</template>

<script>
  import { Toast } from 'mint-ui';
  import * as util from '../../utils/regular.js'
  export default {
    name: 'login',
    data: function () {
      return {
        localData:{
          count:'', //60s倒计时
          yamFlag:true, //标记一下状态
          timer:null,
        },
        login: {
          phone: '',
          code: ''
        },
      }
    },
    methods: {
      /**
       * 获取验证码
       */
      mobileFun () {
        let mobile=util.mobiles(this.login.phone);
        if(mobile===true){
          this.$http.post('/sms/sendByUserLogin', this.$qs.stringify({phone:this.login.phone}))
            .then(res => {
              console.log('res.data', res)
              if (res.status === 200) {
                if (res.data.code === 200) {
                  const TIME_COUNT = 60;
                  if (!this.timer) {
                    this.localData.count = TIME_COUNT;
                    this.localData.yamFlag = false;
                    this.localData.timer = setInterval(() => {
                      if (this.localData.count > 0 && this.localData.count <= TIME_COUNT) {
                        this.localData.count--;
                      } else {
                        this.localData.yamFlag = true;
                        clearInterval(this.localData.timer);
                        this.localData.timer = null;
                      }
                    }, 1000)
                  }//if
                } else {

                }
              }
            })

        }else {
          Toast({
            message:mobile,
            position: 'top',
            duration: 1000
          });
        }
      },
      /**
       * 点击提交表单
       */
      submitForm () {
        let mobiles=util.mobiles(this.login.phone);
        if(mobiles  && this.login.code){
          this.doLogin()
          console.log("提交")
        }else if(mobiles!=true){
          Toast({
            message:mobiles,
            position: 'top',
            duration: 1000
          });
        }else {
          Toast({
            message:'请输入验证码',
            position: 'top',
            duration: 1000
          });
        }
      },
      doLogin () {
        this.$http.post('/user/loginByCode', this.$qs.stringify(this.login))
          .then(res => {
            console.log('res.data', res)
            if (res.status === 200) {
              if (res.data.code === 200) {
                localStorage.setItem("userId",  res.data.data.access_id);
                localStorage.setItem("token", res.data.data.access_token);
                this.$router.push({ path: 'index' });
//              var user = localStorage.getItem("user");
//              console.log(user)
              } else {

              }
            }
          })
      },
      /**
       * 返回上一个页面
       */
      fanhuiFun(){
        this.$router.go(-1)
      }
    }
  }
</script>

<style  lang="scss">
  .loginTextBox{
    background-color: #ffffff;
    .mint-header{
      background-color: transparent;
      color: #3b3b3b;
      font-size: 0.34rem;
      height:0.9rem;
      border-bottom: 1px solid #e8e8e8;
    }
    .loginMode{
      padding: 0.68rem 1.4rem;
      display: -webkit-flex;
      display: flex;
      .loginModeItem{
        margin:0 auto;
        text-align: center;

        img{
          width: 0.9rem;
          height: 0.9rem;
        }
        p{
          font-size: 0.24rem;
          color: #939393;
          margin-top: 0.24rem;
        }
      }
    }
    .loginFormBox{
      padding: 0.6rem 0.45rem 0.6rem 0.45rem;
      input{
        padding: 0.19rem 0.4rem;
        border-radius: 0.5rem;
        background-color: #f6f6f6;
        color: #949494;
        font-size: 0.28rem;
        display: block;
        width: 100%;
        margin-bottom: 0.3rem;
      }
      .loginFormDivBox{
        margin-bottom: 0.4rem;
        input{
          width: 4.6rem;
          float: left;
          margin-bottom: 0;
        }
        p{
          height: 0.7rem;
          line-height: 0.70rem;
          border-radius: 0.5rem;
          font-size: 0.28rem;
          width: 1.76rem;
          float: right;
          text-align: center;
        }
        .timeShow{
          border:1px solid #f7f7f7;
          color: #939393;
          background-color: #f7f7f7;
        }
        .timeHide{
          border:1px solid #de3031;
          color: #de3031;
        }
      }
      .loginFormFontBox{
        font-size: 0.24rem;
        margin-bottom: 0.45rem;
        .left{
          float: left;
          color: #de3031;
        }
        .right{
          float: right;
          color: #939393;
        }
      }
      .loginFormBtn{
        font-size: 0.3rem;
        background-color: #de3031;
        border-radius: 0.5rem;
        line-height: 0.7rem;
        margin-bottom: 0.28rem;
        text-align: center;
        color: #fff;
      }
    }
    .loginFont{
      font-size: 0.24rem;
      color: #939393;
      text-align: center;
      a{
        color: #de3031;
      }
    }
  }
</style>



