<template>
  <div class="pageBox releaseBox">
    <div class="indexConHeader">
      <router-link to="index" class="indexbackOff">
        <i class="icon iconfont icon-guanbi1-copy"></i>
      </router-link>
      发布
      <router-link to="index" class="indexNews">
        <i class="icon iconfont icon-cebian-wodedingdan"></i>
      </router-link>
    </div>
    <div class="releaseCont">
      <textarea name="" class="releaseContText" placeholder="请您多说说需求说明，便于其他人更了解哦！"></textarea>
      <p class="releaseContNumber">
            0/500
      </p>
      <ul class="after releaseImgBox">
        <li>
          <img src="@/assets/img/banner.png" alt="" class="img-my">
          <i class="icon iconfont icon-guanbi colen"></i>
        </li>
        <div class="releaseFile">
          <i class="icon iconfont icon-xiangji zxj"></i>
          <input type="file" capture="camera" accept="image/*" />
        </div>
      </ul>

    </div>
    <div class="releaseFormBox">
      <div class="releaseForm">
        <p>
          联系人
        </p>
        <input type="text" placeholder="你的姓名">
      </div>
      <div class="releaseForm">
        <p>
          联系电话
        </p>
        <input type="text" placeholder="你的手机号码">
      </div>
    </div>
    <div class="releaseBtn">
      <p>
        发布
      </p>
    </div>
  </div>

</template>

<script>
  export default {
    data() {
      return {}
    },
    methods: {},
    computed: {}
  }
</script>
<style lang="scss">
  .releaseBox {
    background-color: #f7f7f7;
    padding-top: 0.9rem;
    .indexConHeader {
      position: fixed;
      left: 0;
      right: 0;
      top: 0;
      z-index: 5;
      background-color: #de3031;
      height: 0.9rem;
      color: #fff;
      font-size: 0.34rem;
      text-align: center;
      line-height: 0.9rem;
      .indexbackOff {
        position: absolute;
        left: 0;
        top: 0;
        bottom: 0;
        line-height: 0.9rem;
        padding: 0 0.2rem;
        color: #fff;
      }
      .indexNews {
        position: absolute;
        right: 0;
        top: 0;
        bottom: 0;
        line-height: 0.9rem;
        padding: 0 0.2rem;
        color: #fff;
      }
    }
    .releaseCont{
      width: 100%;
      padding:0.3rem 0.2rem;
      background-color: #ffffff;
      margin-bottom: 0.2rem;
      .releaseContText{
        width: 100%;
        height: 2.3rem;
        border: none;
      }
      .releaseContNumber{
        color: #939393;
        font-size: 0.24rem;
        text-align: right;
        margin-bottom: 0.2rem;
      }
      .releaseImgBox{
        li{
          position: relative;
          width: 1.34rem;
          height: 1.34rem;
          /*overflow: hidden;*/
          /*display: -webkit-flex;*/
          /*display: flex;*/
          border-radius: 6px;
          margin-right: 0.2rem;
          float: left;
          img{
          /*margin: auto;*/
            width: 1.34rem;
            height: 1.34rem;
            border-radius: 6px;
          }
          .colen{
            padding: 0.1rem;
            font-size: 0.34rem;
            position: absolute;
            right: -0.24rem;
            top: -0.24rem;
            color: #fe3e3e;

          }
        }
        .releaseFile{
          position: relative;
          width: 1.34rem;
          height: 1.34rem;
          overflow: hidden;
          /*display: -webkit-flex;*/
          /*display: flex;*/
         text-align: center;
          line-height: 1.34rem;
          border-radius: 6px;
          border:1px solid #e5e5e5;
          float: left;
         .zxj{
           font-size: 0.5rem;
           display: block;
         }
          input{
            position: absolute;
            left: 0;
            right: 0;
            top: 0;
            bottom: 0;
            opacity: 0;
          }
        }
      }
    }
    .releaseFormBox{
      padding: 0 0 0 0.3rem;
      background-color: #ffffff;
      .releaseForm{
        width: 100%;
        height: 1rem;
        line-height: 1rem;
        border-bottom: 1px solid #f0f0f0;
        p{
          float: left;
          font-size: 0.28rem;
          color: #3b3b3b;

        }
        input{
          float: right;
          display: block;
          font-size: 0.28rem;
          padding: 0.334rem;
          text-align: right;
        }
      }
    }
    .releaseBtn{
      position: absolute;
      left: 0;
      right: 0;
      bottom: 0;
      padding: 0.1rem 0 0.15rem 0;
      background-color: #ffffff;
      p{
        width: 1.7rem;
        height: 0.7rem;
        text-align: center;
        background-color: #de3031;
        color: #ffffff;
        font-size: 0.28rem;
        line-height: 0.7rem;
        border-radius: 0.5rem;
        margin: auto;
      }
    }
  }
</style>



