<template>
  <div class="pageBox classIficatBox">
    <!--banner start-->
    <div class="classIficatBanner" >
      <mt-swipe :auto="4000" :show-indicators="false">
        <mt-swipe-item v-for=" item in classIfiBanner">
          <router-link :to="{path:'commodityDetails',query: {id: item.id}}">
            <img :src="item.image" alt="" class="img-my">
          </router-link>
        </mt-swipe-item>
      </mt-swipe>
    </div>
    <!--banner end-->
    <div class="classIficatCont after">
      <div class="classIficatLeft after">
        <ul>
          <li v-for="(item,$index) in classIfiData" :class="{'active':item.classFlag==true}" @click="oneTitcdFun(item,item.id)">
            {{item.name}}
          </li>
        </ul>
      </div>
      <div class="classIficatRight">
        <div class="classIficatListBox" v-for="item in this.classIfiOneData"  v-show="item.childMenus!=null">
          <p>
            {{item.name}}
          </p>
          <ul class="after">
            <li v-for="itemTwo in item.childMenus">
              <router-link :to="{path:'search',query: {id: itemTwo.id}}">
                <div>
                  <img :src="itemTwo.icon" alt="" class="img-my">
                </div>
                <p>
                  {{itemTwo.name}}
                </p>
              </router-link>
            </li>
          </ul>
        </div>
      </div>
    </div>
    <bottom></bottom>
  </div>
</template>
<script>
  import bottom from '@/components/bottom'
  import Vue from 'vue'
    export default {
      name:"classification",
      components: {
        bottom
      },
        data() {
            return {
              classFlag:false,//class标记
              classIfiBanner:[],//banner
              classIfiData:[],//列表
              classIfiOneData:[
                {
                  childMenus:[]
                }
              ],//一级菜单
              classIfiTwoData:[],//二级级菜单
            }
        },
      mounted () {
        this.classInit()
      },
        methods: {
          /**
           * 页面初始化函数
           */
          classInit(){
            this.$http.get('/goodsCategory/getAllCategory')
              .then(res => {
                if (res.status === 200) {
                  console.log('res.data', res.data);
                  if(res.data.code===200){
                    this.classIfiBanner=res.data.data.banner;
                    this.classIfiData=res.data.data.goodsCategory;
                    Vue.set(this.classIfiData[0],'classFlag',true)
                    this.classCommFun(this.classIfiData[0].id)
                  }
                }
              })
          },
          /**
           * 点击菜单查询
           * @param item
           * @param id
           */
          oneTitcdFun(item, id){
            this.$nextTick(function() {
              this.classIfiData.forEach(function(item) {
                Vue.set(item,'classFlag',false)
              });
              Vue.set(item,'classFlag',true)
            });
            this.classCommFun(id)

          },
          /**
           * 根据id查询分类下所有小分类
           */
          classCommFun(id){
            for(let i=0;i<this.classIfiData.length;i++){
              if(this.classIfiData[i].id === id){
                console.log(this.classIfiData[i].childMenus)
                this.classIfiOneData=this.classIfiData[i].childMenus
              }
            }
          }
        },
        computed: {}
    }
</script>
<style lang="scss">
.classIficatBox{
  background-color: #f7f7f7;
  /*padding: 0.9rem 0 0.96rem;*/
  padding: 3rem 0  0.96rem 0;
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  overflow-y: scroll;
  .classIficatBanner{
    height: 3rem;
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    z-index: 5;
    .mint-swipe-indicator{
      background-color: #c0d2f7;
      opacity: 1;
    }
    .mint-swipe-indicator.is-active {
      background: #ff6f20;
      width: 10px;
      height: 10px;
    }
    .img-my{
      width: 100%;
    }
    .indexBox .indexBanner .mint-swipe-indicator.is-active{
      margin-top: -1px;
    }
    .mint-swipe-indicators{
      bottom: -0.2rem;
    }
  }
  .classIficatCont{
    padding: 0.2rem 0.2rem 0.2rem 0;
    position: relative;
    .classIficatLeft{
      position: fixed;
      left: 0;
      top: 3.2rem;
      bottom: 1.1rem;
      overflow: auto;
      width: 1.8rem;
      height: auto;
      background-color: #ffffff;
      /*float: left;*/
      ul{
        li{
          text-align: center;
          font-size: 0.26rem;
          color: #2b2b2b;
          line-height: 1rem;
          border-right: 1px solid #eaeaea;
          border-bottom: 1px solid #eaeaea;

        }
        li:first-child{
          border-top: 1px solid #eaeaea;
        }
        .active{
          color: #de3031;
          background-color: #f7f7f7;
          border-right: 1px solid transparent;
        }
        /*li.active:first-child{*/
          /*border-top: 1px solid transparent;*/
        /*}*/
      }
    }
    .classIficatLeft::-webkit-scrollbar{display: none;} //隐藏滚动条！
    .classIficatRight{
      width: 5.3rem;
      float: right;
      overflow-y: scroll;
      .classIficatListBox{
        margin-bottom: 0.1rem;
        p{
          font-size: 0.26rem;
          color: #2b2b2b;
          line-height: 0.6rem;
        }
        ul{
          padding: 0.3rem 0 0.1rem 0;
          background-color: #ffffff;
          li{
            width: 33%;
            margin-bottom: 0.2rem;
            float: left;
            a{
              width: 100%;
              height: 100%;
              display: block;
              div{
                width: 1.8rem;
                height: 1.8rem;
                margin: auto;
                display: -webkit-flex;
                display: flex;
                background-color: #fff;
                img{
                  margin: auto;
                }
              }
              p{
                color: #2b2b2b;
                font-size: 0.26rem;
                text-align: center;
              }
            }
          }
        }
      }
    }
  }


}
</style>



