<template>
  <div class="pageBox mySetUpBox">
    <div class="mySetUpUserTit after  ">
      <p class="left p1">
        头像
      </p>
      <div class="right userImgBox" >
        <img :src="mySetUpData.headImg" alt="" class="img-my" id="img">
        <input type="file" @change="upload" accept="image/*" capture="camera"/>
      </div>
    </div>
    <div class="mySetUpCont after ">
      <div class=" after iconRight" @touchend="nicheng">
        <p class="left p1">
          昵称
        </p>
        <p class="right p2">
          {{mySetUpData.nickName}}
        </p>
      </div>
      <div class=" after iconRight " @touchend="relVeSelectFun('pickerData')">
        <p class="left p1">
          性别
        </p>
        <p class="right p2" v-if="mySetUpData.gender==-1">
          保密
        </p>
        <p class="right p2" v-else-if="mySetUpData.gender==0" >
          女
        </p>
        <p class="right p2" v-else-if="mySetUpData.gender==1" >
          男
        </p>
      </div>
      <div class=" after ">
        <p class="left p1">
          手机
        </p>
        <p class="right p2">
          {{mySetUpData.phone}}
        </p>
      </div>
    </div>
    <div class="midfyPwasBox" >
      <router-link  :to="{path:'modifyPassword'}" class="after iconRight" >
        <p class="left p1">
          修改密码
        </p>
      </router-link>
      <router-link  :to="{path: realNameFlag==-1|| realNameFlag==-2 ? 'realName':'javascript:;'}" class="after iconRight" >
        <p class="left p1">
          实名认证
        </p>
        <p class="right p2" v-if="realNameFlag==-1">
          审核未通过
        </p>
        <p class="right p2" v-else-if="realNameFlag==0">
          审核中
        </p>
        <p class="right p2" v-else-if="realNameFlag==1">
          审核通过
        </p>
        <p class="right p2" v-else-if="realNameFlag==-2">
          未认证
        </p>
      </router-link>
    </div>
    <div class="musetupBtns" @touchend="exitLoginFun">
      <p>
        退出登录
      </p>
    </div>
    <picker :pickerFlag.sync="pickerFlag" :modify="modifyfun" ref="child" @subReturnFuns="clkPicFun"></picker>
    <!--修改昵称弹窗-->
    <div class="midifyNiChengBox" v-show="nichengFlag">
      <div class="midifyNiChengCont">
        <div class="after midifyNiChengTit">
          <p class="Close"  @touchend="nicheng">
            取消
          </p>
          <p class="sure" @touchend="mySetNichengQr">
            确认
          </p>
        </div>
        <div class="midifyNiCheng">
          <input type="text" placeholder="请输入昵称" v-model="mySetUpData.nickName">
        </div>
      </div>
    </div>
    <!--修改手机号弹窗-->
    <!--<div class="midifyNiChengBox" v-show="phoneFlag">-->
      <!--<div class="midifyNiChengCont">-->
        <!--<div class="after midifyNiChengTit">-->
          <!--<p class="Close"  @touchend="phoneHid">-->
            <!--取消-->
          <!--</p>-->
          <!--<p class="sure" @touchend="phoneSub">-->
            <!--确认-->
          <!--</p>-->
        <!--</div>-->
        <!--<div class="midifyNiCheng">-->
          <!--<input type="tel" placeholder="请输入手机号" v-model="mySetUpData.phone">-->
        <!--</div>-->
      <!--</div>-->
    <!--</div>-->
  </div>
</template>

<script>
  import picker from '@/components/picker'
    export default {
      components: {
        picker
      },
        data() {
            return {
              nichengFlag:false,//昵称弹窗标记
              phoneFlag:false,//手机号弹窗标记
              pickerFlag:false,//性别select标记
              realNameFlag:'-2',//实名认证状态
              mySetUpData:{//接收用户信息
                headImg:'',
                nickName:'',
                gender:'1',
                phone:'',
              },
              modifyfun: {
                title: '',
                slots: [
                  {
                    flex: 1,
                    values: [
                      {
                        value:0,
                        name:"女"
                      },
                      {
                        value:1,
                        name:"男"
                      },
                      ],
                    className: 'slot1',
                  }
                ],
              },
            }
        },
      mounted () {
        this. mySetUpinit()
      },
        methods: {
        /**
         *页面初始化函数
         */
          mySetUpinit(){
            this.$http.post('/user/getInfo')
              .then(res => {
                if (res.status === 200) {
//                  console.log('res.data', res.data)
                  if(res.data.code===200){
                    this.mySetUpData=res.data.data
                    console.log(this.mySetUpData)
                  }
                }
              })
          //查询认证信息
          this.$http.post('userAuthentication/getStatusByUserId')
            .then(res => {
              if (res.status === 200) {
                console.log('res.data', res.data.data)

                if(res.data.code===200){
                  this.realNameFlag=res.data.data
                }else if(res.data.code===-1){
                  this.realNameFlag=res.data.code
                }

              }
            })
          },

          /**
           * 图片预览函数
           * @param c
           * @param d
           */
          upload(e){
            let file = e.target.files[0];
            let param = new FormData(); //创建form对象
            param.append('file',file,file.name);//通过append向form对象添加数据
//            param.append('chunk','0');//添加form表单中其他数据
            console.log(param.get('file')); //FormData私有类对象，访问不到，可以通过get判断值是否传进去
            this.$http.post('/upload/imgToOSS',param)
              .then(res=>{
                if (res.status === 200) {
                  if(res.data.code===200){
                    this.mySetUpData.headImg=res.data.data
                    console.log(res.data.data)
                    this.mySetUpFun('headImg')
                  }
                }
              })
          },
          /**
           * 给子组件传值得函数 并打开弹窗
           * @param data
           */
          relVeSelectFun(data){
            this.modifyfun.title=data;
            this.modifyfun.names='name'
            this.pickerFlag=true
          },
          /**
           * 子组件选中过后的
           * @param data
           */
          clkPicFun(data){
            console.log(data)
            this.mySetUpData.gender=data.row[0].value
            this.mySetUpFun('gender')
          },
          /**
           *修改昵称
           */
          nicheng(){
            this.nichengFlag = ! this.nichengFlag
          },
          mySetNichengQr(){
            this.mySetUpFun('nickName')
            this.nicheng()
          },
//          /**
//           *修改手机号
//           */
//          phoneHid(){
//            this.phoneFlag = ! this.phoneFlag
//          },
//          phoneSub(){
//            this.mySetUpFun('phone')
//            this.phoneHid()·
//          },
          /**
           *修改用户信息
           */
          mySetUpFun(_key){
            let myObj={};
            for(let item in this.mySetUpData){
              if(_key===item){
                myObj[item]=this.mySetUpData[item]
              }
            }
            console.log(myObj);
            this.$http.post('/user/edit', this.$qs.stringify(myObj))
              .then(res => {
                if (res.status === 200) {
                  console.log('res.data', res.data)
                  if(res.data.code===200){

                  }

                }
              })

          },
//          退出登录
          exitLoginFun(){
            window.localStorage.clear('token')
            console.log(window.localStorage.getItem('token'))
            this.$router.push({ path: '/' });
          }
        },
        computed: {}
    }
</script>

<style lang="scss">

.mySetUpBox{
  background-color: #f7f7f7;
  position: fixed;
  left: 0;
  right: 0;
  bottom: 0;
  top:0;
  width: 100%;
  height: 100%;
  overflow-y: scroll;
  .iconRight{
    width: 100%;
    position: relative;
  }
  .iconRight:before{
    font-family: "iconfont" !important;
    font-size: 0.32rem;
    font-style: normal;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    content: "\E63C";
    position: absolute;
    right: 0.28rem;
    top: 50%;
    margin: -0.16rem 0;
    display: block;
  }
  .left{
    float: left;
  }
  .right{
    float: right;
  }
  .mySetUpUserTit{
    background-color: #ffffff;
    padding:0 0.7rem 0 0.3rem;
    margin: 0.2rem 0;
   .p1{
     line-height: 1.3rem;
   }
    .userImgBox{
      margin: 0.15rem 0;
      position: relative;
      overflow: hidden;
      img{
        width: 1rem;
        height: 1rem;
        border-radius: 50%;
      }
      input{
        position: absolute;
        left: 0;
        right: 0;
        opacity: 0;
        display: block;
        top: 0;
        bottom:0;
      }
    }
  }
  .p1{
    font-size: 0.28rem;
    color: #261e1b;
  }
  .p2{
    font-size: 0.28rem;
    color: #272727;
  }
  .p3{
    font-size: 0.28rem;
    color: #949494;
  }
  .mySetUpCont{
    background-color: #ffffff;
    margin-bottom: 0.2rem;
    .after{
      border-bottom: 1px solid #ededed;
      padding: 0 0.7rem 0 0.3rem;
      p{
        line-height: 1rem;
      }
    }
  }
  .midfyPwasBox{
    background-color: #ffffff;
    .after{
      border-bottom: 1px solid #ededed;
      padding: 0 0.7rem 0 0.3rem;
      display: block;
      p{
        line-height: 1rem;
      }
    }
  }
  .musetupBtns{
    margin: 0.6rem 0;
    p{
      line-height: 0.7rem;
      width: 6.9rem;
      border-radius: 0.5rem;
      background-color: #de3031;
      color: #ffffff;
      font-size: 0.3rem;
      margin: auto;
      text-align: center;
    }

  }
  .midifyNiChengBox{
    position: fixed;
    left: 0;
    right: 0;
    bottom: 0;
    top: 0;
    z-index: 50;
    background-color: rgba(0,0,0,.5);
    border-top: 1px solid #cccccc;
    .midifyNiChengCont{
      position: absolute;
      left: 0;
      right: 0;
      bottom: 0;
    }
    .midifyNiChengTit{
      font-size: 0.24rem;
      line-height: 0.6rem;
      background-color: #f7f7f7;
      /*padding: 0 0.2rem;*/
      padding: 0.1rem;
      p{
        width: 1rem;
        border-radius: 4px;
        text-align: center;
      }
      .Close{
        float: left;
        border: 1px solid #ccc;
        background-color: #ffffff;
      }
      .sure{
        float: right;
        background-color: #de3031;
        color: #ffffff;
      }
    }
    .midifyNiCheng{
      background-color: #fff;
      padding: 0.3rem;
      border-top: 1px solid #f0f0f0;
      border-bottom: 1px solid #f0f0f0;
      input{
        padding: 0.15rem 0.3rem;
        display: block;
        border:1px solid  #f0f0f0;
        width: 100%;
      }
    }
  }
}
</style>



