<template>
  <div class="pageBox myInvitationBox">
    <div class="myInImgBox">
      <img src="@/assets/img/myYaoq_tu2.png" alt="" class="img-my">
    </div>
    <div class="myInYqBox">
      <p class="title">
        我的邀请
      </p>
      <div class="after myIncontiner">
        <router-link to="myConnections" >
          <p class="fontName">
            成功邀请
          </p>
          <p class="number">
            {{renshu}} <span>人</span>
          </p>
        </router-link>
        <router-link to="rewardHongbao" >
          <p class="fontName">
            奖励红包
          </p>
          <p class="number">
            {{hogbao}}
          </p>
        </router-link>

      </div>
    </div>
    <div class="erweimaBox">
      <img src="@/assets/img/index_tu4.png" alt="" class="img-my">
    </div>
    <div class="myInjieshi">
      <p>
        1、首次推荐有效用户，即可获得5元现金奖励；
      </p>
      <p>
        1、首次推荐有效用户，即可获得5元现金奖励；
      </p>
      <p>
        1、首次推荐有效用户，即可获得5元现金奖励；
      </p>
    </div>
  </div>
</template>

<script>
    export default {
        data() {
            return {
              renshu:'',
              hogbao:'100'
            }
        },
      mounted () {
        this.myInvitationInit()
      },
        methods: {
          /**
           * 页面初始化
           */
          myInvitationInit(){
            this.$http.post('/userInvitationPay/getCount')
              .then(res => {
                if (res.status === 200) {
                  if(res.data.code===200){
                    console.log(res.data);
                    this.renshu=res.data.data
//                    this.bankCardData=res.data.data;
//                    this.myEvalArr=res.data.data.records
                  }
                }
              })
          },
        },
        computed: {}
    }
</script>

<style lang="scss">
.myInvitationBox{

  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  overflow-y: auto;
  background: url("../../assets/img/myYaoq_tu1.png") no-repeat center;
  padding-top: 0.4rem;
  background-size: 100% 100%;
  .myInImgBox{
    width: 6.2rem;
    max-height: 4.5rem;
    margin: auto;
  }
  .myInYqBox{
    width: 6.2rem;
    margin: 0.2rem auto 0.3rem auto;
    border-radius: 6px;
    border: 1px solid #ffffff;
    overflow: hidden;
    .title{
      font-size: 0.32rem;
      color: #ffffff;
      line-height: 0.66rem;
      padding-left: 0.3rem  ;
      background-color: #dc2c35;
    }
    .myIncontiner{
      background-color: #ffffff;
      padding: 0.3rem 0;
      a{
        display: block;
        width: 50%;
        text-align: center;
        float: left;
        .fontName{
          font-size: 0.34rem;
          color: #a4a4a4;
        }
        .number{
          font-size: 0.44rem;
          color: #dc2c35;
          span{
            font-size: 0.32rem;
          }
      }
      }
      a:first-child{
        border-right: 1px dashed #d8d8d8;
      }
    }
  }
  .erweimaBox{
    width: 3rem;
    height: 3rem;
    border-radius: 6px;
    padding: 0.2rem;
    background-color: #ffffff;
    margin: auto;
  }
  .myInjieshi{
    width: 6.2rem;
    margin:0.3rem auto;

    p{
      font-size: 0.28rem;
      color: #ffffff;
      margin-bottom: 0.2rem;
    }
  }
}
</style>



