<script>
  import Vue from 'vue';
  import shopCommodityClass from '../shopCommodityClass/shopCommodityClass';
  import { Loadmore } from 'mint-ui';
  Vue.component(Loadmore.name, Loadmore);
  import * as util from '../../utils/utils.js'
    export default {
      components:{
        shopCommodityClass,
      },
        data() {
            return {
              selected:"2",
              shopStarNum:'',
              allLoaded:false,//上拉加载标记
              shopClassFlag:false,//店铺内的商品分类弹窗分类
              falseShFlag:false,
              list:10,
              htmlStr:'<p>店内环境</p><img src="http://img0.imgtn.bdimg.com/it/u=1765474568,392718820&fm=27&gp=0.jpg" alt="" class="img-my"><p>商家简介</p><p > 商家简介商家简介商家简介商家简介</p><img src="https://ss2.bdstatic.com/70cFvnSh_Q1YnxGkpoWK1HF6hhy/it/u=940341784,3429441188&fm=27&gp=0.jpg" alt="" class="img-my">',
              storageData:{//用来存储一些标记
                lineORcolFlag:true, //为true===竖 false===行
                togetherPopupFlag:false, //控制综合弹窗标记
                screenPopupFlag:false, //控制筛选弹窗标记
              },
              titleList:[//二级菜单栏
                {
                  name:"综合",
                  id:null,
                  icon:null,
                  colorFlag:false
                },
                {
                  name:"价格",
                  id:"1",
                  icon:'icon iconfont icon-paixugaodi',
                  colorFlag:false
                },
                {
                  name:"销量",
                  id:"2",
                  icon:null,
                  colorFlag:false
                },
                {
                  name:"新品",
                  id:"3",
                  icon:null,
                  colorFlag:false
                },
              ],
              shopDetData:{},//店铺信息
              modifyfun:{
                shopId:''
              },
              shopSearchData:{
                shopId:"",
                type:''
              },
              shopArr:[]
            }
        },
      mounted () {
        this.shopSearchData.shopId=this.$route.query.id;
        this.shopDetInit();
        let id=this.$route.query.id;
        let obj={
          shopId:id,
          type:''
        }
        this.shopCommit(obj)
//        this.shopCommitFun(this.shopSearchData)
      },
        methods: {
          /**
           * 控制列表横向或者纵向
           */
          lineOrcolFun(){
            this.storageData.lineORcolFlag = !this.storageData.lineORcolFlag
          },
          /**
           *分类头部点击事件
           * @param data ==id
           */
          titleLstClickFun(data){
            this.shopArr=[]
            console.log(data);
            this.$nextTick(function() {
              this.titleList.forEach(function(data) {
                Vue.set(data,'colorFlag',false)
              });
              Vue.set(data,'colorFlag',true)
            });
            if( data.id===1){
              data.id=-1
            }else if( data.id===-1){
              data.id=1
            }
            let id=this.$route.query.id;
            let obj={
              shopId:id,
              type:data.id
            }
            this.shopCommit(obj)
          },
          /**
           * 页面初始化函数
           */
          shopDetInit(){
            let id=this.$route.query.id;
            let idObj={
              shopId:id
            };
            /**
             * 请求店铺信息
             */
            this.$http.post('/shop/getShopById',this.$qs.stringify(idObj))
              .then(res => {
                if (res.status === 200) {
                  console.log('res.data', res.data);
                  if(res.data.code===200){
                    this.shopDetData=res.data.data;
                    this.shopStarNum=5-Number(this.shopDetData.starNum);
                    console.log(res.data.data.startTime);
                    let st=new Date(res.data.data.startTime.replace(/-/,"/"))
                    let et=new Date(res.data.data.endTime.replace(/-/,"/"));
                    this.shopDetData.startTime=util.formatDate( st,'hh:mm');
                    this.shopDetData.endTime=util.formatDate( et,'hh:mm');
                    this.shopDetData.phone='tel:'+this.shopDetData.phone
                  }
                }
              })

          },

          /**
           * 上拉
           */
          loadBottom() {
            console.log("123")
            this.allLoaded = true;
            this.$refs.loadmore.onBottomLoaded();
          },
          /**
           * 打开子组件时调用
           */
          shopClassPop(){
            let shopIdObj={
              shopId:this.shopDetData.id
            }
            this.$http.post('/shop/getShopAttributeByShopId',this.$qs.stringify(shopIdObj))
              .then(res => {
                if (res.status === 200) {
                  console.log('res.data', res.data);
                  if(res.data.code===200){
                  this.modifyfun.arr=res.data.data
                  this.modifyfun.shoppId=this.$route.query.id
                    this.shopClassFlag =true
                  }
                }
              })
          },
          /**
           * 获取商品列表
           */
        shopCommit(obj){
            this.$http.post('/shopGoods/getShopGoodsBy',this.$qs.stringify(obj))
              .then(res => {
                if (res.status === 200) {
                  console.log('res.data', res.data);
                  if(res.data.code===200){
                    this.shopArr=res.data.data.records

                  }
                }
              })
        },
        },
        computed: {}
    }
</script>

