                    </p>
                    <p class="orderListPrice">
                      ￥84.5
                    </p>
                    <p class="orderListModel">
                      2018-02-21 11:12:31
                    </p>
                  </div>
                </div>
                <div class="after CalculationBox">
                  <div class="after CalculationBtnBox">
                    <p class="grayColor">
                      删除
                    </p>
                    <p class="redColor">
                      编辑
                    </p>

                  </div>
                </div>

              </div>
            </li>

          </ul>

        </mt-tab-container-item>
        <mt-tab-container-item id="2">

        </mt-tab-container-item>
        <mt-tab-container-item id="3">

        </mt-tab-container-item>
        <mt-tab-container-item id="4">

        </mt-tab-container-item>
        <mt-tab-container-item id="5">

        </mt-tab-container-item>
      </mt-tab-container>
    </div>

  </div>
</template>
<script>

  export default {
    name: 'order',

    data() {
      return {
        selected:"1"
      }
    },
    methods: {},
    computed: {}
  }
