<style lang="scss">
  .indexRuralActivityBox{
    background-color: #f7f7f7;
    .mint-navbar{
      margin-bottom: 0.2rem;
      padding: 0 0.2rem;
    }
    .mint-navbar .mint-tab-item {
      padding: 0.24rem 0;
    }
    .mint-navbar .mint-tab-item.is-selected {
      border-bottom: 0.05rem solid #de3031;
      color: #de3031;
      margin-bottom:0.06rem;
    }
    .mint-tab-item-label{
      font-size: 0.28rem;
    }
    .orderCartListTit{
      line-height: 0.84rem;
      border-bottom: 1px solid #cccccc;
      padding: 0 0 0 0.2rem;
      background-color: #ffffff;
      .orderName{
        float: left;
        position: relative;
        font-size: 0.24rem;
        padding-left: 0.2rem;
        display: block;
        width: 4.72rem;
        overflow: hidden;
        text-overflow:ellipsis;
        white-space: nowrap;
        i{
          font-size: 0.24rem;
        }

      }
      .orderCoupon{
        float: right;
        font-size: 0.26rem;
        padding: 0 0.2rem;
        color: #de3031;
      }

    }
    .orderList{
      margin-bottom: 0.2rem;
      background-color: #ffffff;
      .orderListBox{
        padding: 0.3rem  0.2rem 0.1rem 0.2rem;
        border-bottom: 1px solid #ebebeb;
        .orderListImgBox{
          width: 1.5rem;
          height: 1.5rem;
          display: -webkit-flex;
          display: flex;
          float: left;
          img{
            margin: auto;
          }
        }
        .orderListFontBox{
          float: left;
          width: 5rem;
          margin-left: 0.3rem;
          .orderListExplain{
            font-size: 0.28rem;
            color: #3b3b3b;
            max-width: 100%;
            overflow:hidden;
            text-overflow:ellipsis;
            display:-webkit-box;
            -webkit-box-orient:vertical;
            -webkit-line-clamp:2;
          }
          .orderListModel{
            font-size: 0.22rem;
            color: #939393;
          }
          .orderListPrice{
            color: #de3031;
            font-size: 0.28rem;
          }
        }

      }
      .CalculationBox{
        padding: 0.3rem 0.2rem 0.26rem 0.2rem;
        .CalculationBtnBox{
          float: right;
          p{
            width: 1.7rem;
            height: 0.5rem;
            line-height: 0.5rem;
            text-align: center;
            border-radius: 0.5rem;
            font-size: 0.26rem;
            margin-left:0.3rem ;
            float: left;
          }
          .grayColor{
            color: #333333;
            border:1px solid #d8d8d8
          }
          .redColor{
            color: #de3031;
            border:1px solid #de3031
          }
        }
      }


    }
  }
</style>



