<template>
  <div class="pageBox indexExpressBox">
    <!--banner start-->
    <div class="indexBanner">
      <mt-swipe :auto="4000">
        <mt-swipe-item v-for="item in indexExpreBanner">
          <img :src="item.image" alt="" class="img-my">
          <!--<router-link :to="{path:'commodityDetails',query: {id: item.id}}">-->
           <!---->
          <!--</router-link>-->
        </mt-swipe-item>

      </mt-swipe>
    </div>
    <!--banner end-->
    <div class="indexExpressCont ">
      <div class="iconRight">
        <div class="imgBox">
          <img src="@/assets/img/indexExpress_tu2.png" alt="" class="img-my">
        </div>
        <div class="FontBox">
          <P class="p1">
            寄快递
          </P>
          <P class="p2">
            我要寄件
          </P>
        </div>
      </div>
      <div class="iconRight ">
        <div class="imgBox">
          <img src="@/assets/img/indexExpress_tu1.png" alt="" class="img-my">
        </div>
        <div class="FontBox">
          <P class="p1 xidan">
            同城跑腿
          </P>
          <P class="p2">
            去下单
          </P>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
    export default {
        data() {
            return {
              indexExpreBanner:[],
            }
        },
      mounted () {
        this.indexExpreInit()
      },
        methods: {
          indexExpreInit(){
            this.$http.post('/banner/expressRun')
              .then(res => {
                if (res.status === 200) {
                  console.log('res.data', res.data);
                  if(res.data.code===200){
                    this.indexExpreBanner=res.data.data
                  }
                }
              })
          }
        },
        computed: {}
    }
</script>

<style lang="scss">
  .indexExpressBox{
    background-color: #f7f7f7;
    position: fixed;
    left: 0;
    right: 0;
    bottom: 0;
    top: 0;
    .indexBanner{
      height: 3rem;
      .mint-swipe-indicator{
        background-color: #c0d2f7;
        opacity: 1;
      }
      .mint-swipe-indicator.is-active {
        background: #ff6f20;
        width: 10px;
        height: 10px;
      }
      img{
        width: 100%;
      }
      .indexBox .indexBanner .mint-swipe-indicator.is-active{
        margin-top: -1px;
      }
      .mint-swipe-indicators{
        bottom: -0.2rem;
      }
    }


    .indexExpressCont{
      padding: 0.3rem 0.2rem;
      .iconRight{
        width: 100%;
        position: relative;
        padding: 0.3rem 0.4rem;
        display: flex;
        justify-content:flex-start;
        margin-bottom: 0.2rem;
        background-color: #ffffff;
        .imgBox{
          width: 1.3rem;
          max-height: 1rem;
        }
        .FontBox{
          margin-left: 0.5rem;
          .p1{
            font-size: 0.38rem;
            color: #3b3b3b;
            font-weight: 700;
          }
          .p2{
            font-size: 0.28rem;
            color: #939393;
          }
          .xidan{
            padding-top: 0.1rem;
          }
        }
      }

      .iconRight:before{
        font-family: "iconfont" !important;
        font-size: 0.32rem;
        font-style: normal;
        -webkit-font-smoothing: antialiased;
        -moz-osx-font-smoothing: grayscale;
        content: "\E63C";
        position: absolute;
        right: 0.28rem;
        top: 50%;
        margin: -0.16rem 0;
        display: block;
      }
    }
  }

</style>



